// libs
import React from "react";
import moment from "moment";
import { Virtuoso } from "react-virtuoso";

// utils
import { getDocsUrl } from "../../shared/utils";

const getLastMessageDetails = (lastMessages = [], user = {}) => {
    const latestUserData =
        lastMessages.filter((message = {}) => message.id == user?.id)?.[0] || user || {};

    return (
        <>
            {latestUserData?.unread_msg_count ? (
                <small className="unread_msg">{latestUserData?.unread_msg_count}</small>
            ) : null}
            {latestUserData?.last_msg_time ? (
                <span className="msg_time">{getLastSeen(latestUserData?.last_msg_time)}</span>
            ) : latestUserData?.last_seen ? (
                <span className="msg_time">{getLastSeen(latestUserData?.last_seen)}</span>
            ) : null}
            <p>
                {latestUserData?.last_msg_type !== "0" ? "Attachment" : latestUserData?.last_msg?.length > 20
                    ? latestUserData?.last_msg?.slice(0, 23) + "..."
                    : latestUserData?.last_msg}
            </p>
        </>
    );
};

export const getLastSeen = (date) => {
    let current = new Date();
    var msgDate = new Date(date);
    let showDate = "";
    if (current?.getDate() === msgDate?.getDate() && current?.getMonth() === msgDate?.getMonth()) {
      showDate = moment(date).format("h:mm a");
    } else if (current?.getDate() - 1 === msgDate?.getDate() && current?.getMonth() === msgDate?.getMonth()) {
      showDate = `Yesterday`;
    } 
    else {
      showDate = `${moment(msgDate).format("DD MMM YYYY")}`;
    }
    return showDate;
  };

const UsersListWithInfiniteScroll = ({
    styles = {},
    data = {},
    endReached = () => {},
    overscan = 10,
    itemClick = () => {},
    isChatList = false,
    activeUser = {},
    lastMessages = [],
}) => {
    return (
        <React.Fragment>
            <Virtuoso
                style={styles}
                data={data}
                endReached={endReached}
                overscan={overscan}
                listClassName="chat_scroll"
                itemContent={(index, user = {}) => {
                    return (
                        <li
                            key={user.id + index}
                            onClick={() => itemClick(user)}
                            className={activeUser?.id == user?.id ? "active" : ``}
                        >
                            <div className="d-flex chat_items">
                                <figure className="avtar avtar50 mb-0">
                                    <img
                                        src={getDocsUrl(user?.photo)}
                                        alt="Locumbay"
                                        height="50"
                                        width="50"
                                    />
                                </figure>
                                <div className="chat_summary">
                                    <h6 className="h6 mb-0 text-capitalize">{`${user?.first_name} ${user?.last_name}`}</h6>
                                    {isChatList ? 
                                        getLastMessageDetails(lastMessages, user)
                                    : null}
                                </div>
                            </div>
                        </li>
                    );
                }}
                components={() => "Loading"}
            />
        </React.Fragment>
    );
};

export default UsersListWithInfiniteScroll;
