import React from "react";
import "../style.scss";
import { Images } from "shared/images";
import {
  ADMIN_KEYS,
  API_RESPONSE,
  BUTTONS,
  SNACK_VARIANT,
  LOGIN_FORM,
} from "shared/constants";
import TextField from "components/atom/textField/TextField";
import { useDispatch } from "react-redux";
import { login_auth, saveEmailPassword } from "../../../redux/actions/auth";
import { useHistory } from "react-router-dom";
// import Snackbar from "components/atom/snackbar";
import { SNACK_MESSAGES } from "../../../shared/constants";
import useNotification from "components/atom/snackbar";
import { ROUTE_CONSTANTS } from "shared/routeConstants";

function LoginForm({
  email,
  setEmail,
  password,
  setPassword,
  emailErr,
  rememberMe,
  setRememberMe,
  validate,
  passwordErr,
  setIsForgot,
}) {
  const dispatch = useDispatch();
  const history = useHistory();
  const [msg, sendNotification] = useNotification();

  const btnDisable = () => {
    if (emailErr.length || (email.length === 0 && password.length === 0)) {
      return true;
    } else {
      return false;
    }
  };

  const onLogin = (e) => {
    e.preventDefault();
    let data = {
      email: email,
      password: password,
      is_staff: ADMIN_KEYS.ADMIN,
    };
    if (!btnDisable()) {
      dispatch(
        login_auth(
          data,
          (res) => {
            sendNotification({
              msg: SNACK_MESSAGES.LOGIN_SUCCESSFUL,
              variant: SNACK_VARIANT.SUCCESS,
            });
            history.push("./");
          },
          (err) => {
            sendNotification({
              msg: err ? err : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
              variant: SNACK_VARIANT.ERROR,
            });
          }
        )
      );
    }

    if (rememberMe) {
      dispatch(saveEmailPassword(data));
    }
  };

  return (
    <form autoComplete="off">
      <TextField
        image={Images.emailImage}
        label={LOGIN_FORM.FIELDS.EMAIL.LABEL}
        error={emailErr}
        value={email}
        type="text"
        onChange={(e) => {
          setEmail(e.target.value);
          validate(LOGIN_FORM.FIELDS.EMAIL.LABEL, e.target.value);
        }}
      />

      <TextField
        image={Images.ShowPwdIcon}
        label={LOGIN_FORM.FIELDS.PASSWORD.LABEL}
        error={passwordErr}
        value={password}
        type="password"
        onChange={(e) => {
          setPassword(e.target.value);
          validate(LOGIN_FORM.FIELDS.PASSWORD.LABEL, e.target.value);
        }}
      />

      <div className="mb-4">
        <div className="checkbox">
          <input
            type="checkbox"
            onChange={(e) => setRememberMe(e.target.checked)}
          />
          <label>{LOGIN_FORM.FIELDS.REMEMBERME}</label>
        </div>
      </div>

      <div className="mb-4 text-center">
        <button
          className="btn btn-md btn-primary"
          disabled={btnDisable()}
          onClick={(e) => onLogin(e)}
        >
          {BUTTONS.LOGIN}
        </button>
      </div>

      <div className="text-center forgot_txt">
        <span
          className="d-inline-block pointer"
          onClick={() => setIsForgot((prev) => !prev)}
        >
          {LOGIN_FORM.FIELDS.FORGOTPASSWORD}
        </span>
      </div>
    </form>
  );
}

export default LoginForm;
