import io from "socket.io-client";
import { API } from "shared/api";
import { store } from "redux/store";

class SocketManager {
  socket = "";
  callBacks = [];
  data = [];
  isError = false;
  async establishConnection(val = () => {}) {
    this.socket = io(
      `${API.SOCKET_IO}?user_id=${
        store.getState().auth?.adminDetails?.user_id
      }&token=${store.getState().auth?.token}`,
      { transports: ["websocket"] }
    );
    this.socket.on("connect_error", (err) => {
      this.isError = true;
      console.log(err, "connect_error");
    });
    this.socket.on("connectOk", (res) => {
      this.isError = false;
      console.log("HIDU", res);
    });
    this.socket.on("connect", () => {
      this.isError = false;
      console.log("..........connected..........");
    });
    this.socket?.on("disconnect", () => {
      this.isError = true;
      console.log("...........disconnected...........");
    });
  }
  disconnectConnection() {
    this.socket.disconnect();
    this.socket.removeAllListeners();
  }
  emitEvent(eventName, data, val = () => {}) {
    //console.log(this,this.socket," chcking ");
    this.socket.emit(eventName, data, (res) => {
      val(res);
    });
  }
  onJoinEvent(eventName, val = () => {}) {
    this.socket.on(eventName, (res) => {
      val(res);
    });
  }
  onConnect(val = () => {}) {
    this.socket.on("connect", (socket) => {
      val(socket);
    });
  }
  onListenEvent(eventName, val = () => {}) {
    this.socket.on(eventName, (res) => {
      val(res);
    });
  }
  onNewMessage(val = () => {}) {
    this.socket?.on("newMessage", (res) => {
      //console.log(res, 'newMessage')
      val(res);
    });
  }
  onJoinFriend(val = () => {}) {
    this.socket?.on("addedInRoom", (res) => {
      this.socket?.emit("joinInRoom", res, (response) => {
        val(response);
      });
    });
  }
  expiryTime(val = () => {}) {
    this.socket?.on("roomExpired", (res) => {
      val(res);
    });
  }
  otherDeviceLogin(val = () => {}) {
    this.socket?.on("otherDeviceLogin", (res) => {
      val(res);
    });
  }
  disconnectEvent(eventName) {
    this.socket.removeAllListeners(eventName);
  }
  removeListenerEvent() {
    this.socket.removeAllListeners();
  }
  addRegister(callback) {
    this.callBacks.push(callback);
  }
  removeRegister(callback) {
    this.callBacks.slice(this.callBacks.findIndex(callback), 1);
  }
  addData(callback) {
    this.data.push(callback);
  }
  removeData(callback) {
    this.data.slice(this.data.findIndex(callback), 1);
  }

  socketError() {
    return this.isError;
  }
}

const socketInstance = new SocketManager();

//Object.freeze(socketInstance);
export default socketInstance;
