import React from "react";
import { Images } from "shared/images";
import './style.scss';

function Ratingstars({ rating = 3 }) {
  return (
    <>
      {[...Array(5).keys()].map((value) => (
        <em className="ms-2">
          <img
            src={value < rating ? Images.blueStar : Images.starIcon}
            alt="star"
          />
        </em>
      ))}
    </>
  );
}

export default Ratingstars;
