//libs
import React, { useEffect, useRef, useState, useMemo } from "react";
import { Virtuoso } from "react-virtuoso";
import { useSelector } from "react-redux";
import moment from "moment";

// components
import MessageItem from "./MessageItem";

// hooks
import usePrevious from "../../shared/hooks/UsePrevious";

// utils
import { getMessageDay } from "../../shared/utils";

// consts
import { MESSAGES_LIMIT } from "../..";

const MessagesList = ({
    data = [],
    overscan = 30,
    itemClick = () => {},
    styles = {},
    startReached = () => {},
    activeUser = {},
    fetchUserMessages = () => {},
    totalMessages = 20,
    recieverName = "",
    handleMessageClick = () => {},
    virtuosoRef = null,
    filteredMessagesExist = false,
    filteredMessages = {},
    activeSearchIndex = {},
    ...rest
}) => {
    const prevUserId = usePrevious(activeUser?.id);
    // eslint-disable-next-line react-hooks/exhaustive-deps
    const adminDetails = useSelector((state) => state.auth.adminDetails);
    const [atBottom, setAtBottom] = useState(false);
    const showButtonTimeoutRef = useRef(null);
    const [showButton, setShowButton] = useState(false);
    const [firstItemIndex, setFirstItemIndex] = useState(totalMessages - data.length);

    useEffect(() => {
        return () => {
            clearTimeout(showButtonTimeoutRef.current);
        };
    }, []);

    useEffect(() => {
        clearTimeout(showButtonTimeoutRef.current);
        if (!atBottom) {
            showButtonTimeoutRef.current = setTimeout(() => setShowButton(true), 500);
        } else {
            setShowButton(false);
        }
    }, [atBottom, setShowButton]);

    // keep a copy of messages -> set next first item index before setting new messages
    const internalMessages = useMemo(() => {
        const nextFirstItemIndex = totalMessages - data.length;
        setFirstItemIndex(nextFirstItemIndex);
        return data;
    }, [data]);

    const senderUserName = useMemo(
        () => `${adminDetails?.first_name} ${adminDetails?.last_name}`,
        [adminDetails],
    );

    // if switching lists, unmount virtuoso so internal state gets reset
    if (prevUserId !== activeUser?.id) {
        return null;
    };

    // do call api and append prev data w.r.t. to top item in viewport
    const getPrevMessages = (res) => {
        if(totalMessages > MESSAGES_LIMIT && totalMessages !== internalMessages.length){
            fetchUserMessages(internalMessages[0]?.id);
        };
    };

    const isSameDay = (currentMessage = {}, diffMessage = {}) => {
        if (!diffMessage || !diffMessage.created_at) {
            return false;
        }

        const currentCreatedAt = moment(currentMessage.created_at);
        const diffCreatedAt = moment(diffMessage.created_at);

        if (!currentCreatedAt.isValid() || !diffCreatedAt.isValid()) {
            return false;
        }
        return currentCreatedAt.isSame(diffCreatedAt, "day");
    };

    return (
        <>
            <Virtuoso
                ref={virtuosoRef}
                data={internalMessages}
                styles={styles}
                className="chat_messages"
                listClassName="msg_items"
                totalCount={totalMessages}
                overscan={overscan}
                startReached={getPrevMessages}
                initialTopMostItemIndex={data?.length - 1}
                firstItemIndex={Math.max(0, firstItemIndex)}
                atBottomStateChange={(bottom) => {
                    setAtBottom(bottom);
                }}
                itemContent={(index, message = {}) => {
                    const previousMsg = internalMessages[(internalMessages?.findIndex((msg) => msg?.id === message?.id) || 0) - 1];
                    return (
                        <li
                            key={message.id}
                            className={
                                message.sender == activeUser?.id
                                    ? "incoming_msg"
                                    : "outgoing_msg"
                            }
                            id={message.id}
                        >
                            {/* group same day messages */}
                            {!isSameDay(message, previousMsg) && <div className="message_date"><span>{getMessageDay(message?.created_at)}</span></div>}
                             <MessageItem
                                message={message}
                                userId={adminDetails?.user_id}
                                userImage={
                                    message.sender == adminDetails?.user_id
                                        ? adminDetails?.photo
                                        : activeUser?.photo
                                }
                                prevMsg={previousMsg}

                                userName={
                                    message.sender == adminDetails?.user_id
                                        ? senderUserName
                                        : recieverName
                                }
                                itemClick={handleMessageClick}
                                filteredMessages={filteredMessages}
                                activeSearchIndex={activeSearchIndex}
                            />
                        </li>
                    );
                }}
                components={() => "Loading"}
                followOutput={"auto"}
                {...rest}
            />
            {showButton && (
                <button
                    className="btn-transparent btn-secondary down_arrow"
                    onClick={() =>
                        virtuosoRef.current.scrollToIndex({
                            index: data?.length - 1,
                            behavior: "smooth",
                            align: "end",
                        })
                    }
                    style={{ float: "right", transform: "translate(0, -3rem)" }}
                >
                    <svg className="svg-icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"><path d="M512 85.34016q17.67424 0 30.16704 12.4928t12.4928 30.16704l0 665.00608 225.66912-225.9968q12.32896-12.32896 30.33088-12.32896 18.3296 0 30.49472 12.16512t12.16512 30.49472q0 18.00192-12.32896 30.33088l-298.65984 298.65984q-12.32896 12.32896-30.33088 12.32896t-30.33088-12.32896l-298.65984-298.65984q-12.32896-12.32896-12.32896-30.33088 0-18.3296 12.16512-30.49472t30.49472-12.16512q18.00192 0 30.33088 12.32896l225.66912 225.9968 0-665.00608q0-17.67424 12.4928-30.16704t30.16704-12.4928z" fill="#fff" /></svg>
                </button>
            )}
        </>
    );
};

export default MessagesList;
