import { getLastSeen } from "../Inbox/components/UsersListWithInfiniteScroll";

const getUserDetails = (lastMessages = [], user = {}) => {
    const latestUserData =
        lastMessages.filter((message = {}) => message.id == user?.id)?.[0] || user || {};
    return latestUserData;
};

const getUserMeta = (user = {}) => {
    return (
        <>
            <p>{user?.is_online ? "Active now" : user?.last_seen ? `Last Seen ${getLastSeen(user?.last_seen)}` : null}</p>
        </>
    );
};

