import ButtonStatus from "components/atom/button/ButtonStatus";
import { Pagination } from "components/atom/pagination";
import CustomTable from "components/atom/table";
import CheckboxMultiBody from "components/molecules/checkboxMulti/body";
import CheckboxHeader from "components/molecules/checkboxMulti/header";
import SortIcon from "components/molecules/sortIcon";
import { convertToPrice, getLocalizedDateString, switchbadges } from "components/utility";
import React, { useEffect } from "react";
import { useHistory } from "react-router-dom";
import {
  JOB_STATUS,
  NA,
  SORT_KEY,
  SORT_KEY_VACANCY,
  STRINGS,
  VACANCY_TYPE,
} from "shared/constants";
import { Images } from "shared/images";
import { ROUTE_CONSTANTS } from "shared/routeConstants";
import {
  BRANCH_SERVICES,
  VACANCIES_BRANCH_TABLE,
  VACANCIES_DASHBOARD_TABLE,
  VACANCIES_PHARMACY_TABLE,
} from "shared/tableData";

function VacanciesTable({
  setmultiplePayload = () => {},
  multiplepayload,
  navigate = () => {},
  data,
  onDelete = () => {},
  itemsPerPage,
  setItemsPerPage = () => {},
  setPageCount = () => {},
  setSortKey = () => {},
  sortKey,
  multiSelection,
  setMultipSelection = () => {},
  pageCount,
  handleSort = () => {},
  toggle = {
    isActive: false,
    name: "",
  },
  setToggle = () => {},
  setSortType = () => {},
  setId = () => {},
  type = VACANCY_TYPE.DASHBOARD,
}) {
  const history = useHistory();
  // function to switch sort key based on the name of the column
  const sortSwitchKey = (name) => {
    switch (name) {
      case VACANCIES_DASHBOARD_TABLE.PHARMACY:
        return setSortKey(SORT_KEY_VACANCY.PHARMACY_NAME);
      case VACANCIES_DASHBOARD_TABLE.BRANCH:
        return setSortKey(SORT_KEY_VACANCY.BRANCH_NAME);
      case VACANCIES_DASHBOARD_TABLE.NAME:
        return setSortKey(SORT_KEY_VACANCY.JOB_NAME);
      case VACANCIES_DASHBOARD_TABLE.START_DATE:
        return setSortKey(SORT_KEY_VACANCY.START_DATE);
      case VACANCIES_DASHBOARD_TABLE.END_DATE:
        return setSortKey(SORT_KEY_VACANCY.END_DATE);
      case VACANCIES_DASHBOARD_TABLE.RATE:
        return setSortKey(SORT_KEY_VACANCY.RATE);
      case VACANCIES_DASHBOARD_TABLE.STATUS:
        return setSortKey(SORT_KEY_VACANCY.STATUS);
      default:
        return setSortKey(SORT_KEY_VACANCY.DEFAULT);
    }

    setSortType(0);
  };

  const tableheader = () => {
    switch (type) {
      case VACANCY_TYPE.DASHBOARD:
        return VACANCIES_DASHBOARD_TABLE;
      case VACANCY_TYPE.PHARMACY:
        return VACANCIES_PHARMACY_TABLE;
      case VACANCY_TYPE.BRANCHES:
        return VACANCIES_BRANCH_TABLE;
      default:
    }
  };

  // function to return a boolean indicating whether an image should be shown for a given column
  const sortImage = (name) => {
    switch (name) {
      case VACANCIES_DASHBOARD_TABLE.PHARMACY:
        return true;
      case VACANCIES_DASHBOARD_TABLE.BRANCH:
        return true;
      case VACANCIES_DASHBOARD_TABLE.NAME:
        return true;
      case VACANCIES_DASHBOARD_TABLE.START_DATE:
        return true;
      case VACANCIES_DASHBOARD_TABLE.END_DATE:
        return true;
      case VACANCIES_DASHBOARD_TABLE.RATE:
        return true;
      case VACANCIES_DASHBOARD_TABLE.STATUS:
        return true;
      default:
        return false;
    }
  };

  // useEffect hook to set the multiple selection flag to false if there are no payloads
  useEffect(() => {
    if (multiplepayload?.length === 0) setMultipSelection(false);
  }, [multiplepayload]);


  return (
    <div className="tab_content pt-0">
      <CustomTable>
        <thead>
          <tr key={1000 + 1}>
            {/* column for checkboxes */}
            <th width={70}>
              <CheckboxHeader
                multipleSelect={multiSelection}
                setmultiplePayload={setmultiplePayload}
                setMultipleSelect={setMultipSelection}
                data={data}
                setId={setId}
                multiplepayload={multiplepayload}
              />
            </th>

            {/* columns for branch data */}
            {Object.values(tableheader()).map((i, idx) => (
              <th scope="col" id={idx}>
                {/* if the column is branch name, show a sort icon */}
                {sortImage(i) ? (
                  <SortIcon
                    toggleIcon={
                      toggle.name === i && toggle.isActive ? true : false
                    }
                    onClick={() => {
                      if (toggle.name !== i) {
                        setSortType(0);
                      }
                      setToggle({
                        name: i,
                        isActive: !toggle.isActive,
                      });
                      sortSwitchKey(i);
                      setPageCount(1);
                      handleSort();
                    }}
                  />
                ) : null}
                {i}
              </th>
            ))}
          </tr>
        </thead>

        {/* table body */}
        {data?.length ? (
          data.map((i, idx) => {
            return (
              <>
                <tbody>
                  <tr>
                    {/* checkbox for each row */}
                    <td>
                      <CheckboxMultiBody
                        setMultipleSelect={setMultipSelection}
                        setmultiplePayload={setmultiplePayload}
                        value={i}
                        multiplepayload={multiplepayload}
                        data={data}
                        setId={setId}
                      />
                    </td>
                    {/* columns for branch data */}
                    <td className="clickable" onClick={() => navigate(i?.id)}>
                      {i?.id ? i?.id : NA}{" "}
                    </td>
                    {type === VACANCY_TYPE.PHARMACY ||
                    type === VACANCY_TYPE.BRANCHES ? null : (
                      <td
                        className="clickable"
                        onClick={() =>
                          i?.pharmacy_id
                            ? history.push(ROUTE_CONSTANTS.PHARMACY_VIEW, {
                                id: i?.pharmacy_id,
                              })
                            : null
                        }
                      >
                        {i?.pharmacy_name ? i?.pharmacy_name : NA}{" "}
                      </td>
                    )}

                    {type === VACANCY_TYPE.BRANCHES ? null : (
                      <td
                        className="clickable"
                        onClick={() =>
                          i?.branch_id
                            ? history.push(ROUTE_CONSTANTS.BRANCHES_VIEW, {
                                id: i?.branch_id,
                              })
                            : null
                        }
                      >
                        {i?.branch_name ? i?.branch_name : NA}{" "}
                      </td>
                    )}

                    <td>{i?.job_name ? i?.job_name : NA} </td>
                    <td>
                      {i?.start_date
                        ? getLocalizedDateString(i?.start_date)
                        : NA}{" "}
                    </td>
                    <td>
                      {i?.end_date ? getLocalizedDateString(i?.end_date) : NA}{" "}
                    </td>
                    <td>
                      {i?.end_rate
                        ? `${convertToPrice(
                            i?.start_rate
                          )} - ${convertToPrice(i?.end_rate)}`
                        : NA}{" "}
                    </td>
                    <td>{JSON.stringify(i?.offered)?.length  ? JSON.stringify(i?.offered) : NA} </td>
                    <td>
                      <div className="d-flex align-items-center">
                        <span
                          className={`m-1 badge-md badge-${switchbadges(
                            i?.vacancy_status
                          )}`}
                        >
                          {JOB_STATUS[`${i?.vacancy_status}`] ?? NA}
                        </span>
                      </div>
                    </td>
                    <td width={200}>
                      <div className="d-flex">
                        <ButtonStatus
                          image={Images.viewIcon}
                          status={"default"}
                          title={"View Details"}
                          onClick={() => navigate(i?.id)}
                        />
                        <ButtonStatus
                          image={Images.deleteIcon}
                          status={"Unverified"}
                          title={"View Details"}
                          onClick={() => {
                            setId(i?.id);
                            onDelete();
                          }}
                        />
                      </div>
                    </td>
                  </tr>
                </tbody>
              </>
            );
          })
        ) : (
          <tbody>
            <tr>
              <td
                colSpan={Object.values(tableheader()).length + 1}
                className="text-center py-5"
              >
                {STRINGS.NO_DATA_AVAILIABLE}
              </td>
            </tr>
          </tbody>
        )}
      </CustomTable>

      {data?.length ? (
        <Pagination
          items={data?.length ? data?.length : []}
          setPageCount={setPageCount}
          itemsPerPage={itemsPerPage}
          setItemsPerPage={setItemsPerPage}
          pageCount={pageCount}
        />
      ) : null}
    </div>
  );
}

export default VacanciesTable;
