import React, {
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import "./style.scss";
import debounce from "lodash.debounce";
import { Images } from "shared/images";

function SearchBar({
  keyword,
  setKeyword,
  style,
  filter = false,
  searchAPI = () => {},
  toggle,
  setToggle = () => {},
  setPageCount = () => {},
  pageCount,
  reset = () => {},
}) {
  const [isInitial, setiIsInitial] = useState(true);
  const firstRenderDone = useRef(false);

  const searchApi = (key) => {
    setPageCount(1);
    if (pageCount === 1) searchAPI(key);
  };

  const handleSearchChange = (e) => {
    setKeyword(e.target.value);
  };

  useEffect(() => {
    return () => {
      debouncedResults.cancel();
    };
  });

  const debouncedResults = useMemo(() => {
    if (isInitial) setiIsInitial(false);
    return debounce(handleSearchChange, 400);
  }, []);

  useEffect(() => {
    if (!firstRenderDone.current) {
      firstRenderDone.current = true;
      return;
    }
    filter && searchApi(keyword);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [keyword]);

  return (
    <div className="d-flex">
      <div className="d-flex searchbar ms-2">
        <input
          style={style}
          key="search-bar"
          placeholder={"Search here..."}
          onChange={debouncedResults}
          className="form-control"
        />
        {filter ? (
          <button
            className="btn btn-primary ms-2"
            onClick={() => {
              setToggle((prev) => {
                reset();
                return !prev;
              });
            }}
          >
            <img
              src={toggle ? Images.filterClose : Images.filter}
              alt="filter"
            />
          </button>
        ) : null}
      </div>
    </div>
  );
}

export default SearchBar;
