import React, { useCallback, useEffect, useState } from "react";
import { FILTERS_ENUM, FILTER_TYPES, STATUS_OPTIONS } from "shared/constants";
import { docStatusUpdate, statusUpdate } from "../searchBar/filterConsts";
import { useDispatch } from "react-redux";
import { cityListing, countryListing } from "redux/actions/auth";
import ReactSelect from "../ReactSelect";
import "./style.scss";
import DateRangeSelector from "../dateRange";
import RangeSlider from "../rangeSelector";


function Filters({
  setCityFilter = () => {},
  setCountryFilter = () => {},
  setStatus = () => {},
  setStatusDoc = () => {},
  setDateRange = () => {},
  dateRange,
  setPrice = () => {},
  price = 0,
  toggle,
  setPageCount = () => {},
  type = FILTER_TYPES.DASHBOARD,
}) {
  const dispatch = useDispatch();
  const [city, setCity] = useState([]);
  const [country, setCountry] = useState([]);
  const filterButtonsDashboard = [
    {
      title: "Country",
      type: FILTER_TYPES.DROPDOWN,
      data: country,
      id: FILTERS_ENUM.country,
      handleChange: (value) => {
        setPageCount(1);
        setCountryFilter(value?.value ? value?.value : "");
      },
    },
    {
      title: "City",
      type: FILTER_TYPES.DROPDOWN,
      data: city,
      id: FILTERS_ENUM.city,
      handleChange: (value) => {
        setPageCount(1);
        setCityFilter(value?.value ? value?.value : "");
      },
    },
    {
      title: "Activation Status",
      type: FILTER_TYPES.DROPDOWN,
      data: statusUpdate,
      id: FILTERS_ENUM.blocked,
      handleChange: (value) => {
        setPageCount(1);
        setStatus(value?.value);
      },
    },
    {
      title: "Document Status",
      type: FILTER_TYPES.DROPDOWN,
      data: docStatusUpdate,
      id: FILTERS_ENUM.blocked,
      handleChange: (value) => {
        setPageCount(1);
        setStatusDoc(value?.value);
      },
    },
  ];

  const filterButtonsVacancy = [
    {
      title: "Country",
      type: FILTER_TYPES.DROPDOWN,
      data: country,
      id: FILTERS_ENUM.country,
      handleChange: (value) => {
        setPageCount(1);
        setCountryFilter(value?.value ? value?.value : "");
      },
    },
    {
      title: "City",
      type: FILTER_TYPES.DROPDOWN,
      data: city,
      id: FILTERS_ENUM.city,
      handleChange: (value) => {
        setPageCount(1);
        setCityFilter(value?.value ? value?.value : "");
      },
    },
    {
      title: "Date Range",
      type: FILTER_TYPES.DATE_RANGE,
      id: FILTERS_ENUM.blocked,
      handleChange: (value) => {
        setPageCount(1);
        setStatus(value?.value);
      },
    },
    {
      title: "Price Range",
      type: FILTER_TYPES.RANGESELECTOR,
      id: FILTERS_ENUM.blocked,
      handleChange: (value) => {
        setPageCount(1);
        setStatusDoc(value?.value);
      },
    },
  ];

  const buttonSwitch = () => {
    switch (type) {
      case FILTER_TYPES.DASHBOARD:
        return filterButtonsDashboard;
      case FILTER_TYPES.VACANCY:
        return filterButtonsVacancy;
      default:
    }
  };

  const getListing = useCallback(() => {
    dispatch(
      cityListing(
        "",
        (res) => {
          const temp = res?.data?.length
            ? res?.data?.map((i, idx) => ({
                value: i.city,
                label: i.city,
              }))
            : [];
          temp.unshift({
            value: "",
            label: STATUS_OPTIONS.DEFAULT.title,
          });
          setCity(temp);
        },
        (err) => {
          // console.log(err?.data?.message, "ERROR CITY");
        }
      )
    );

    dispatch(
      countryListing(
        "",
        (res) => {
          const temp = res?.data?.length
            ? res?.data?.map((i, idx) => ({
                value: i.country,
                label: i.country,
              }))
            : [];
          temp.unshift({
            value: "",
            label: STATUS_OPTIONS.DEFAULT.title,
          });
          setCountry(temp);
        },
        (err) => {
          // console.log(err.data.message, "ERROR COUNTRY");
        }
      )
    );
  }, [dispatch, toggle]);

  useEffect(() => {
    if (toggle) getListing();
  }, [getListing, toggle]);

  const renderSelector = (item, idx) => {
    switch (item.type) {
      case FILTER_TYPES.DROPDOWN:
        return (
          <ReactSelect
            options={item.data}
            handleChange={item.handleChange}
            selectedOption={item.selected}
            key={idx}
            title={"Select"}
          />
        );
      case FILTER_TYPES.DATE_RANGE:
        return (
          <>
            <DateRangeSelector
              dateRange={dateRange}
              setDateRange={setDateRange}
            />
          </>
        );
      case FILTER_TYPES.RANGESELECTOR:
        return (
          <>
            <RangeSlider  setState={setPrice} state={price} />
          </>
        );
      default:
        break;
    }
  };

  return (
    <>
      {toggle ? (
        <div className={`row filters_col ${!toggle ? "d-none" : ""}`}>
          {buttonSwitch()?.map((i, idx) => {
            return (
              <div className="col-sm-6 col-md-3 mb-1">
                <label className="d-block text-capitalize">{i.title}</label>
                {renderSelector(i, idx)}
              </div>
            );
          })}
        </div>
      ) : null}
    </>
  );
}

export default Filters;
