import React, { useEffect, useRef, useState } from "react";
import "./styles.scss";
import {
  API_RESPONSE,
  BUTTONS,
  SNACK_MESSAGES,
  SNACK_VARIANT,
  SORT_KEY,
  STATUS_OPTIONS,
  TAB_USER_DATA,
  USERS_CONSTS,
  VACANCY_TYPE,
} from "shared/constants";
import NavTab from "components/atom/tabNav";
import SearchBar from "components/atom/searchBar";
import CustomTable from "components/atom/table";
import UserTable from "./Table";
import { Pagination } from "components/atom/pagination";
import {
  changeDocumentStatus,
  deleteUsersApi,
  getUserListing,
  saveLocumData,
  savePharmacyData,
} from "redux/actions/auth";
import { useDispatch, useSelector } from "react-redux";
import { userStatusUpdate } from "redux/actions/common";
import { startLoader, stopLoader } from "redux/actions/commonCRUD";
import StatusChangeDropdown from "components/molecules/statusChangeDropdown";
import ActivateDeactivate from "components/molecules/activateDeactivate";
import StatusChangeModal from "components/molecules/statuschangeModal";
import useNotification from "components/atom/snackbar";
import DocUnverifyModal from "components/molecules/docUnverifyModal";
import { useHistory, useLocation } from "react-router-dom";
import { ROUTE_CONSTANTS } from "shared/routeConstants";
import DocumentViewerModal from "components/molecules/documentViewer";
import DeleteModal from "components/molecules/deleteModal";
import Filters from "components/atom/filter";
import FiltersCustom from "components/atom/chatComponents/Filter";

function Users() {
  const dispatch = useDispatch();
  const location = useLocation();
  const history = useHistory();
  const token = useSelector((state) => state.auth.token);
  const [activeItem, setActiveItem] = useState(0);
  const [pageCount, setPageCount] = useState(1);
  const [keyword, setKeyword] = useState("");
  const [itemsPerPage, setItemsPerPage] = useState(15);
  const [pageItems, setPageItems] = useState(1);
  const locumUser = useSelector((state) => state.persist.locumData);
  const pharmacyUser = useSelector((state) => state.persist.pharmacyData);

  const [viewDoc, setViewDoc] = useState(false);
  const [verifyPayload, setVerifyPayload] = useState([]);
  const [docViewerParams, setDocViewerParams] = useState([]);
  const [showStatusModal, setShowStatusModal] = useState(false);
  const [docType, setDocType] = useState(1);
  const [msg, sendNotification] = useNotification();
  const [showReasonModal, setShowReasonModal] = useState(false);
  const [reasonUnverify, setReasonUnverify] = useState("");
  const [sortKey, setSortKey] = useState(0);
  const [sortType, setSortType] = useState(0);
  const [modalOpacity, setmodalOpacity] = useState(false);
  const [deleteModal, setDeleteModal] = useState(false);
  const [city, setCity] = useState("");
  const [country, setCountry] = useState("");
  const [filterStatus, setFilterStatus] = useState("");
  const [docStatus, setDocStatus] = useState("");
  const firstRenderDone = useRef(true);
  const [toggle, setToggle] = useState(false);

  const tabID = location?.state?.id;

  const [filterData, setFilterData] = useState({
    country: "",
    state: "",
    activation: "",
    document: "",
  });

  useEffect(() => {
    if (tabID) {
      setActiveItem(tabID);
    }
  }, [tabID]);

  //fetch listing
  const fetchUser = (isResetting = false) => {
    let payload = {
      userType: activeItem,
      page: pageCount,
      search: keyword,
      limit: itemsPerPage,
      sorting: sortKey,
      sort_type: sortType,
      ...(filterData.country?.length
        ? { country: isResetting == true ? "" : filterData.country }
        : null),
      ...(filterData.state?.length
        ? { state: isResetting == true ? "" : filterData.state }
        : null),
      ...(filterData.activation?.value?.length
        ? { is_active: isResetting == true ? "" : filterData.activation?.value }
        : null),
      ...(filterData.document?.value?.length
        ? { doc_status: isResetting == true ? "" : filterData.document?.value }
        : null),
    };
    if (token) {
      dispatch(
        getUserListing(
          payload,
          (res, type) => {
            setPageItems(res.count);
            // Set data for tabs separately
            if (type === API_RESPONSE.SUCCESS) {
              switch (activeItem) {
                case USERS_CONSTS.LOCUM_USERS.ID:
                  //setLocumUser(res?.data);
                  dispatch(saveLocumData(res?.data));
                  break;
                case USERS_CONSTS.PHARMACY_USERS.ID:
                  //setPharmacyUser(res?.data);
                  dispatch(savePharmacyData(res?.data));
                  break;
                default:
                  break;
              }
            }
          },
          (err) => {
            sendNotification({
              msg: err?.data?.message
                ? err.data.message
                : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
              variant: SNACK_VARIANT.ERROR,
            });
          }
        )
      );
    }
  };

  useEffect(() => {
    if (!firstRenderDone.current) {
      firstRenderDone.current = true;
      return;
    }
    fetchUser();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    activeItem,
    itemsPerPage,
    pageCount,
    sortKey,
    sortType,
    docStatus,
    country,
    city,
    filterStatus,
  ]);

  // User status change from activate/deactive
  const userStatusChange = (id, is_active) => {
    let arr = [];
    if (verifyPayload.length && !id) {
      arr = verifyPayload.map((i, idx) => {
        return {
          user_id: i,
          is_active: is_active,
        };
      });
    } else {
      arr = [
        {
          user_id: id,
          is_active: is_active,
        },
      ];
    }
    let data = {
      user_status_update: arr,
    };
    dispatch(
      userStatusUpdate(
        data,
        (res) => {
          fetchUser();
          setVerifyPayload([]);
          sendNotification({
            msg: res?.data?.message
              ? res?.data.message
              : SNACK_MESSAGES.SAVED_SUCCESSFULLY,
            variant: SNACK_VARIANT.SUCCESS,
          });
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  // Managing data for different tabs to pass to table
  const dataSwitch = () => {
    switch (activeItem) {
      case USERS_CONSTS.LOCUM_USERS.ID:
        return locumUser;
      case USERS_CONSTS.PHARMACY_USERS.ID:
        return pharmacyUser;
      default:
    }
  };

  //Document Status change Verify/unverify
  const documentButtonClick = (isVerified) => {
    let arr = [];
    if (verifyPayload.length) {
      //Multiple Payload
      arr = verifyPayload.map((i, idx) => {
        return {
          user_id: i,
          document_type: docType,
          ...(isVerified === STATUS_OPTIONS.UNVERIFIED.key && {
            comment: reasonUnverify,
          }),
          verified: isVerified,
        };
      });
    } else {
      //single Payload
      arr = [
        {
          user_id: docViewerParams[0],
          document_type: docViewerParams[1],
          verified: isVerified,
          ...(isVerified === STATUS_OPTIONS.UNVERIFIED.key && {
            comment: reasonUnverify,
          }),
        },
      ];
    }

    const data = {
      doc_status_update: arr,
    };
    documentStatusChange(data);
  };

  const documentStatusChange = (payload) => {
    dispatch(
      changeDocumentStatus(payload, (res) => {
        fetchUser();
        setViewDoc(false);
        setShowStatusModal(false);
        setShowReasonModal(false);
        setVerifyPayload([]);
        sendNotification({
          msg: res?.data?.message
            ? res?.data.message
            : SNACK_MESSAGES.SAVED_SUCCESSFULLY,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        setShowReasonModal(false);
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  //Api for deleting user
  const deleteUsers = () => {
    let arr = [];
    if (verifyPayload.length) {
      arr = verifyPayload.map((i, idx) => {
        return {
          id: i,
        };
      });
      let data = {
        users: arr,
      };
      dispatch(
        deleteUsersApi(data, (res) => {
          fetchUser();
          setVerifyPayload([]);
          setDeleteModal(false);
          sendNotification({
            msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
            variant: SNACK_VARIANT.SUCCESS,
          });
        }),
        (err) => {
          setDeleteModal(false);
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      );
    }
  };

  //Switch case table for changing view on tab clicks
  const userView = (id) => {
    switch (activeItem) {
      case USERS_CONSTS.LOCUM_USERS.ID:
        return history.push(ROUTE_CONSTANTS.LOCUM_VIEW, { id: id });
      case USERS_CONSTS.PHARMACY_USERS.ID:
        return history.push(ROUTE_CONSTANTS.PHARMACY_VIEW, { id: id });
      default:
    }
  };

  useEffect(() => {
    if (viewDoc) {
      dispatch(startLoader());
      setmodalOpacity(true);
      setTimeout(() => {
        dispatch(stopLoader());
      }, 30000);
    }
  }, [viewDoc]);

  //function to handle sort icon toggle
  const handleSortIcon = () => {
    if (sortType === SORT_KEY.ascending) {
      setSortType(SORT_KEY.descending);
    } else {
      setSortType(SORT_KEY.ascending);
    }
  };

  //Not uploaded snack message
  const showNotUploadedSnack = () => {
    sendNotification({
      msg: SNACK_MESSAGES.DOCUMENT_NOT_UPLOADED,
      variant: SNACK_VARIANT.ERROR,
    });
  };

  const handleChangeFilterData = (key, value) => {
    setFilterData((prevFilterData) => ({ ...prevFilterData, [key]: value }));
  };

  const handleFetchDataForFilter = () => {
    setPageCount(1);
    fetchUser();
    setVerifyPayload([]);
  };

  const resetFilter = () => {
    setPageCount(1);
    fetchUser(true);
    setFilterData({
      country: "",
      state: "",
      activation: "",
      document: "",
    });
  };

  return (
    <>
      <div className="d-flex align-items-center flex-wrap justify-content-between tab_full">
        <DocumentViewerModal
          viewDoc={viewDoc}
          setViewDoc={setViewDoc}
          documentButtonClick={documentButtonClick}
          disableKey={docViewerParams[2]}
          setShowReasonModal={setShowReasonModal}
          docURL={docViewerParams[3]}
          doc_type={docViewerParams[1]}
          onLoadError={() => {
            setViewDoc(false);
            dispatch(stopLoader());
            sendNotification({
              msg: SNACK_MESSAGES.DOCUMENT_LOAD_FAILED,
              variant: SNACK_VARIANT.ERROR,
            });
          }}
          onLoad={() => {
            setmodalOpacity(false);
            dispatch(stopLoader());
          }}
          setmodalOpacity={setmodalOpacity}
          modalOpacity={modalOpacity}
        />

        <DocUnverifyModal
          show={showReasonModal}
          setShow={setShowReasonModal}
          onClick={() => documentButtonClick(STATUS_OPTIONS.UNVERIFIED.key)}
          reason={reasonUnverify}
          setReason={setReasonUnverify}
        />

        <StatusChangeModal
          show={showStatusModal}
          setShow={setShowStatusModal}
          documentButtonClick={documentButtonClick}
          setShowReasonModal={setShowReasonModal}
          docType={docType}
        />

        <DeleteModal
          show={deleteModal}
          toggleModal={() => setDeleteModal((prev) => !prev)}
          onClick={() => deleteUsers()}
          text={`you want to delete ${
            verifyPayload?.length > 1
              ? verifyPayload?.length + " users"
              : "user"
          }`}
        />

        <NavTab
          onClick={(id) => {
            setActiveItem(id);
            setSortKey(0);
            setSortType(0);
            setKeyword("");
            resetFilter();
            // history.push(ROUTE_CONSTANTS.USERS, {
            //   id: id,
            // });
          }}
          tabsData={TAB_USER_DATA}
          activeTab={activeItem}
        />

        <div className="d-flex admin_action flex-wrap">
          {verifyPayload?.length ? (
            <>
              <ActivateDeactivate
                title={"Select Action"}
                statusChange={userStatusChange}
              />
              <StatusChangeDropdown
                statusChange={setDocType}
                setDocType={setDocType}
                setShow={setShowStatusModal}
                active={activeItem}
              />
              <button
                className={`btn btn-sm btn-danger ms-2`}
                onClick={() => setDeleteModal(true)}
              >
                {BUTTONS.DELETE}
              </button>
            </>
          ) : null}

          <SearchBar
            searchAPI={fetchUser}
            keyword={keyword}
            setKeyword={setKeyword}
            filter={true}
            setStatus={setFilterStatus}
            toggle={toggle}
            setToggle={setToggle}
            setPageCount={setPageCount}
            pageCount={pageCount}
            reset={resetFilter}
          />
        </div>
      </div>

      <FiltersCustom
        toggle={toggle}
        filterData={filterData}
        reset={resetFilter}
        handleChangeFilterData={handleChangeFilterData}
        handleFetchDataForFilter={handleFetchDataForFilter}
        type={VACANCY_TYPE.USER}
      />

      <CustomTable>
        <UserTable
          data={dataSwitch()}
          activeTab={activeItem}
          userStatusChange={userStatusChange}
          setViewDoc={setViewDoc}
          setDocViewerParams={setDocViewerParams}
          setmultiplePayload={setVerifyPayload}
          multiplepayload={verifyPayload}
          userView={userView}
          sortKey={sortKey}
          setSortKey={setSortKey}
          handleSort={handleSortIcon}
          sortType={sortType}
          setSortType={setSortType}
          showNotUploadedSnack={showNotUploadedSnack}
          setPageCount={setPageCount}
          pageCount={pageCount}
          itemsPerPage={itemsPerPage}
        />
      </CustomTable>

      {dataSwitch()?.length ? (
        <Pagination
          items={pageItems}
          setPageCount={setPageCount}
          itemsPerPage={itemsPerPage}
          setItemsPerPage={setItemsPerPage}
          pageCount={pageCount}
        />
      ) : null}
    </>
  );
}

export default Users;
