import React from "react";
import "./styles.scss";

const CustomTextInput = React.forwardRef(
    ({ id, errorMessage, onIconClick = () => { }, titleText, icon, iconStyle, ...props }, ref) => {
        return (
            <div className="position-relative">
                {titleText && <label className="titleText">{titleText}</label>}
                <div className="position-relative">
                    {!!icon && (
                        <em
                            onClick={() => {
                                onIconClick(id);
                            }}
                            className="show_hide_pwd"
                        >
                            <img width="12" src={icon} alt="" />
                        </em>
                    )}
                    <input
                        ref={ref}
                        className="form-control"
                        id={id}
                        autoComplete="false"

                        {...props}
                    />
                </div>
                {errorMessage && <span className="error">{errorMessage}</span>}
            </div>
        );
    },
);

export default React.memo(CustomTextInput);
