import React, { useEffect, useState } from "react";
import { BUTTONS } from "shared/constants";
import CustomForm from "components/atom/CustomForm";
import { CONTACT_US_DATA } from "./constant";

function ContactEditForm({
  country,
  state,
  saveChanges = () => {},
  defaultValues,
  setDefaultValues,
  data={}
}) {
  const [phone, setPhone] = useState();

  const onSubmit = (registrationData, country, state, phone) => {
    let payload = {
      phone_number: phone,
      email: registrationData?.email,
      country: country,
      state: state,
      postal_code: registrationData?.postal_code,
      address: registrationData?.address,
    };
    saveChanges(payload);
  };

  useEffect(()=>{
    setPhone(data?.phone_number)
  },[data])

  return (
    <>
      <div className="confirmation_modal">
        <h6 className="h6 text-center text-capitalize mb-4">{`${BUTTONS.EDIT} Details`}</h6>
        <CustomForm
          id={"contactus"}
          onSubmit={onSubmit}
          formData={CONTACT_US_DATA}
          phone={phone}
          onPhoneNumberChange={setPhone}
          renderSubmitButton={true}
          country={country}
          region={state}
          setDefaultValues={setDefaultValues}
          defaultValues={{ ...defaultValues }}
        />
      </div>
    </>
  );
}

export default ContactEditForm;
