import React, { useCallback, useEffect } from "react";
import "./styles.scss";
import {
  CONTACT_US,
  NA,
  SNACK_MESSAGES,
  SNACK_VARIANT,
} from "shared/constants";
import { useState } from "react";
import { useDispatch } from "react-redux";
import ButtonStatus from "components/atom/button/ButtonStatus";
import { Images } from "shared/images";
import GoogleMap from "components/molecules/googleMap";
import CustomModal from "components/atom/modal";
import { editContactUs, fetchContactUs } from "redux/actions/auth";
import useNotification from "components/atom/snackbar";
import ContactEditForm from "./ContactForm";
import { ROUTE_TITLES } from "shared/routeConstants";

function ContactUs() {
  // State variables
  const dispatch = useDispatch();
  const [showModal, setShowModal] = useState(false);
  const [msg, sendNotification] = useNotification();
  const [data, setData] = useState([]);
  const [defaultValues, setDefaultValues] = useState();
  const [country, setCountry] = useState("");
  const [state, setState] = useState("");

  const location = {
    address: "1600 Amphitheatre Parkway, Mountain View, california.",
    lat: 37.42216,
    lng: -122.08427,
  };

  //Contact us contants
  const contactData = [
    {
      label: "Phone",
      value: data?.phone_number ? "+" + data?.phone_number : NA,
      icon: Images.phone,
    },
    {
      label: "Email",
      value: data?.email ? data?.email : NA,
      icon: Images.email,
    },
    {
      label: "Address",
      value: data?.address ? data?.address : NA,
      icon: Images.address,
    },
  ];

  // Fetch data function
  const fetchData = useCallback(() => {
    dispatch(
      fetchContactUs(
        "",
        (res, type) => {
          setDefaultValues(res?.data);
          setData(res?.data);
          setCountry(res?.data?.country);
          setState(res?.data?.state);
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  }, [dispatch, sendNotification]);

  // Call fetch data function when component is mounted
  useEffect(() => {
    fetchData();
  }, [fetchData, sendNotification]);

  // Save changes function
  const saveChanges = (payload) => {
    setShowModal(false);
    dispatch(
      editContactUs(
        payload,
        (res, type) => {
          setData({
            phone_number: payload?.phone_number,
            email: payload?.email,
            country: payload?.country,
            state: payload?.state,
            postal_code: payload?.postal_code,
            address: payload?.address,
          });

          setDefaultValues((prev) => ({
            ...prev,
            phone_number: payload?.phone_number,
            email: payload?.email,
            country: payload?.country,
            state: payload?.state,
            postal_code: payload?.postal_code,
            address: payload?.address,
          }));
          //fetchData();
          //setData({ ...data, payload });
          setCountry(payload?.country);
          setState(payload?.state);
          // Show success notification
          sendNotification({
            msg: res?.data?.message
              ? res?.data.message
              : SNACK_MESSAGES.SAVED_SUCCESSFULLY,
            variant: SNACK_VARIANT.SUCCESS,
          });
        },
        (err) => {
          // Show Failure notification
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  const EditModal = () => {
    return (
      <CustomModal
        isOpen={showModal}
        large={true}
        handleToggle={() => setShowModal((prev) => !prev)}
      >
        <ContactEditForm
          country={country}
          state={state}
          saveChanges={saveChanges}
          defaultValues={defaultValues}
          setDefaultValues={setDefaultValues}
          data={data}
        />
      </CustomModal>
    );
  };

  return (
    <>
      <div className="d-flex align-itm flex-wrap align-items-center justify-content-between tab_full">
        <h1 className="h6 mb-0">{ROUTE_TITLES.CONTACT_US}</h1>

        <div className="d-flex admin_action flex-wrap mb-3">
          <ButtonStatus
            image={Images.edit}
            status={"default"}
            title={"Edit Service"}
            onClick={() => {
              setShowModal(true);
            }}
          />
        </div>
      </div>
      <div className="card mt-3">
        <GoogleMap
          address={location.address}
          lat={location.lat}
          lng={location.lng}
        />
      </div>
      <div className="row mt-3">
        {contactData?.map((i, idx) => (
          <div key={idx} class="col-md-4 col-sm-6 mb-3 address_col">
            <h6 class="mb-3">{i.label}</h6>
            <div className="d-flex">
              <em>
                <img src={i.icon} alt="" width="13" />
              </em>
              <span class="d-block">{i.value}</span>
            </div>
          </div>
        ))}
      </div>
      <EditModal />
    </>
  );
}

export default ContactUs;
