import React from "react";
import Modal from "react-modal";
import { Images } from "shared/images";
import "./modal.scss";
function CustomModal(
  {
    children,
    handleToggle = () => {},
    isOpen = false,
    large = false,
    medium = false,
    small = false,
    avarge = false,
    xLarge = false,
    clearHandler = () => {},
    modalOpacity,
  },
  props
) {
  return (
    <>
      <Modal
        isOpen={isOpen}
        onRequestClose={handleToggle}
        className={`Modal react_modal_main`}
        overlayClassName="Overlay hidden"
        ariaHideApp={false}
      >
        <div
          className={
            `modal-dialog modal-dialog-centered ${
              large
                ? `modal-lg`
                : medium
                ? `modal-md`
                : avarge
                ? `modal-mds`
                : small
                ? `small`
                : xLarge
                ? `modal-xl`
                : "modal-md"
            }`
          }
        >
          <div
            className="modal-content"
            hidden={modalOpacity ? modalOpacity : false}
          >
            <div className="modal-body">
              <button
                className="btn-close btn-transparent"
                onClick={() => {
                  handleToggle();
                  clearHandler();
                }}
              >
                <img src={Images.close} alt="Close" width="14" />
              </button>
              {children}
            </div>
          </div>
        </div>
      </Modal>
    </>
  );
}

export default CustomModal;
