// libs
import React, { useState, useEffect, useContext, useCallback, useRef } from "react";
import { useHistory, useLocation } from "react-router-dom";
import { useDispatch } from "react-redux";

// components
import Inbox from "./Inbox";
import LiveChat from "./LiveChat";

// hooks
import useNotification from "./shared/hooks/Notifications";

// contexts
import SocketContext from "shared/SocketContext";

// utils
import { deepCopy, getQueryParamValue } from "./shared/utils";

// actions modify asper need
import {
    getChatUsersList,
    getMessagesByText,
    getUserMessagesList,
    getUsersListByName,
    uploadMedia,
} from "redux/actions/auth";

// consts
import { SNACK_MESSAGES, SNACK_VARIANT } from "./shared/Constants";

export const MESSAGES_LIMIT = 20;
export const GET_USERS_LIMIT = 20;
export const SEARCH_USERS_LIMIT = 20;

export const CHAT_FILTER_OPTIONS = {
    INBOX: {
        ID: 0,
        TITLE: "Inbox",
    },
    DELETED: {
        ID: 1,
        TITLE: "Deleted",
    },
    ARCHIVED: {
        ID: 2,
        TITLE: "Archived",
    },
    BLOCKED: {
        ID: 3,
        TITLE: "Blocked",
    },
};

const Messages = () => {
    const { socket, socketError } = useContext(SocketContext);
    const location = useLocation();
    const history = useHistory();
    const dispatch = useDispatch();
    const [msg, sendNotification] = useNotification();

    // inbox state variables
    const [usersList, setUsersList] = useState({
        data: [],
        currentPage: 1,
        isLoading: false,
        totalCount: 0,
    });
    const [filteredUsersData, setFilteredUsersData] = useState({
        data: [],
        query: null,
        isLoading: false,
        currentPage: 1,
        totalCount: 0,
    });
    const [activeUser, setActiveUser] = useState(null);
    const [activeFilter, setActiveFilter] = useState(CHAT_FILTER_OPTIONS.INBOX.ID);
    const [searchSidebarVisible, setSearchSidebarVisible] = useState(false);
    const [showChatFilter, setShowChatFilter] = useState(false);
    const [roomId, setRoomId] = useState(null);

    // active chat messages state variables
    const virtuosoRef = useRef(null);
    const [newMessage, setNewMessage] = useState(null);
    const [messages, setMessages] = useState({ data: [], totalCount: 0 });
    const [sendTextValue, setSendTextValue] = useState("");
    const [attachmentDropdownVisible, setAttachmentDropdownVisible] = useState(false);
    const [uploadLoading, setUploadLoading] = useState(false);
    const [chatLoading, setChatLoading] = useState(false);
    const [modalStates, setModalStates] = useState({
        clear: false,
        archive: false,
        delete: false,
        block: false
    });
    const [file, setFile] = useState(null);
    const [showChatOptions, setShowChatOptions] = useState(false);
    const [lastMessages, setLastMessages] = useState([]);
    const [largeImage, setLargeImage] = useState("");

    // search messages
    const [filteredMessages, setFilteredMessages] = useState({
        data: [],
        query: null,
        isLoading: false,
        totalCount: 0,
        showSearch: false,
    });

    // for managing the current searched message index
    const [activeSearchIndex, setActiveSearchIndex] = useState({
        id: null,
        pos: null,
        visibleIndex: null,
    });

    // fetch Chat users List
    useEffect(() => {
        fetchChatUsers();
    }, []);

    useEffect(() => {
        if(socketError){
            sendNotification({
                msg: "Connection not established.",
                variant: SNACK_VARIANT.ERROR,
            });
        }else{
            sendNotification({
                msg: "Connection established.",
                variant: SNACK_VARIANT.SUCCESS,
            });
        }
    }, [socketError])

    // socket events
    useEffect(() => {
        let localRoomId = null;
        if (socket) {
            socket.listenEvent("join_room", (msg) => {
                // console.log("room joined", msg);
            });

            socket.listenEvent("room_name", (msg) => {
                localRoomId = msg?.data.room_name;

                // handle unread msgs
                socket.triggerEvent("msg_read", { room_name: localRoomId }, emitUpdatedChatList);
                setRoomId(localRoomId);
            });

            socket.listenEvent("send_msg", (res) => {
                socket.triggerEvent("msg_read", { room_name: localRoomId });

                localRoomId == res?.data?.thread_name && setNewMessage(res?.data);
                // socket.triggerEvent("chat_list", {
                //     page: 1,
                //     limit: usersList?.data?.length || GET_USERS_LIMIT,
                // });
            });

            socket.listenEvent("receive_msg", (res) => {
                socket.triggerEvent("msg_read", { room_name: localRoomId });

                res?.data?.thread_name === localRoomId && setNewMessage(res?.data);
            });

            socket.listenEvent("chat_list", (res) => {
                if (Array.isArray(res?.data) && res?.data?.length == 1) {
                    // append new user if tab selected is inbox
                    activeFilter === CHAT_FILTER_OPTIONS.INBOX.ID && appendUser(res?.data[0]);
                }
                setLastMessages(res?.data || []);
            });
        }

        return () => {
            if (socket) {
                socket.disconnectEvent("join_room");
                socket.disconnectEvent("room_name");
                socket.disconnectEvent("send_msg");
                socket.disconnectEvent("receive_msg");
                socket.disconnectEvent("chat_list");
                socket.disconnectEvent("msg_read");
            }
        };
    }, [socket, activeFilter]);

    // when navigation happens from different page
    useEffect(() => {
        if (socket) {
            if (location.search) {
                const userId = getQueryParamValue(location.search, "id");
                const userData = usersList?.data?.filter((user) => user.id == userId)?.[0] || null;
                if (!userData) return history.push("/admin/messages");
                handleActiveUser(userData);
            }
        }
    }, [location, usersList, socket]);

    /* change users list order if new message is updated in state &
    append sent/received message in messages list */
    useEffect(() => {
        if (newMessage) {
            activeUser?.id && activeFilter === CHAT_FILTER_OPTIONS.INBOX.ID && appendUser();
            // sortChatList();
            appendMessage();
            emitUpdatedChatList();
        }
    }, [newMessage]);

    // fetch active User Chat
    useEffect(() => {
        if (roomId) {
            fetchUserMessages();
        }
    }, [roomId]);

    // upload media to get the asset url
    useEffect(() => {
        if (file) {
            uploadMediaCall(file);
        }
    }, [file]);

    // if search active, scroll to particular message logic
    useEffect(() => {
        let timeOutId;
        if (filteredMessages?.showSearch) {
            // get message index for scroll
            const messageIndex = messages?.data?.length
                ? messages.data.findIndex((el = {}) => el.id === activeSearchIndex?.id)
                : null;

            // msg exist in state, scroll to that element via msg index
            if (messageIndex !== -1) {
                // scroll to particular element if 1st index, when virtuosoRef is busy rendering list
                if (activeSearchIndex?.visibleIndex == 1) {
                    timeOutId = setTimeout(() => {
                        virtuosoRef?.current?.scrollToIndex({
                            index: messageIndex,
                            behavior: "smooth",
                        });
                    }, 500);
                } else {
                    virtuosoRef?.current?.scrollToIndex({
                        index: messageIndex,
                        behavior: "smooth",
                    });
                }
            }
        }

        return () => {
            clearTimeout(timeOutId);
        };
    }, [messages, activeSearchIndex, virtuosoRef]);

    // fetch messages uptil searched indices TODO
    useEffect(() => {
        if (messages?.data?.length && filteredMessages?.data?.length) {
            if (
                filteredMessages?.data?.[0] &&
                messages?.data?.some((msg) => msg.id !== filteredMessages?.data?.[0]) &&
                messages?.totalCount !== messages?.data?.length
            ) {
                // setChatLoading(true);
                fetchUserMessages(messages?.data[0]?.id, messages?.totalCount);
            }
        }
    }, [activeSearchIndex]);

    // const sortChatList = () => {
    //     let chatList = deepCopy(usersList?.data);
    //     let prevUserIndex = chatList.findIndex((user) => user?.id === activeUser?.id);
    //     if (prevUserIndex > 0) {
    //         let user = chatList[prevUserIndex];
    //         chatList.splice(prevUserIndex, 1);
    //         chatList.unshift({...user, last_msg : "", last_msg_time: ""});
    //         setUsersList((prev) => {
    //             return { ...prev, data: chatList };
    //         });
    //     }
    // };

    const removeCurrentUser = () => {
        if (activeUser?.id) {
            setUsersList((prev) => {
                return {
                    ...prev,
                    data: prev?.data?.filter((user = {}) => user?.id !== activeUser?.id) || [],
                    totalCount: prev.totalCount - 1,
                };
            });
        }
    };

    const appendMessage = () => {
        let messagesCopy = deepCopy(messages.data);
        messagesCopy.push(newMessage);
        setMessages((prev) => {
            return { ...prev, data: messagesCopy };
        });
        setNewMessage(null);
    };

    const appendUser = (newUser = {}) => {
        if(!activeUser?.isArchive){
        const userChatExists =
            usersList?.data?.findIndex(
                (user = {}) => user?.id == (newUser?.id || activeUser?.id),
            ) !== -1;

        // return if active user already present inside usersList else unshift user to the list
        if (userChatExists) return;

        let usersCopy = deepCopy(usersList.data);
        usersCopy.unshift(Object.keys(newUser || {})?.length ? newUser : activeUser);
        setUsersList((prev) => {
            return { ...prev, data: usersCopy, totalCount: prev.totalCount + 1 };
        });
        }
    };

    // filter users on the basis of name
    const handleSearchChange = ({ target: { value: wordToMatch = "" } }) => {
        if (wordToMatch.slice(-1) !== "\\") {
            // let filteredData = [];
            // try {
            //     const regex = new RegExp(wordToMatch, "gi");
                // filteredData = usersList?.data?.filter((user) => {
                //     let userName = user.first_name + user.last_name;
                //     return userName.match(regex);
                // });
            // } catch (e) {
            //     filteredData = [];
            // }
    
            setFilteredUsersData((prev) => {
                return {
                    ...prev,
                    // query: wordToMatch?.replace(/[^a-zA-Z0-9@.]/g, ''),
                    query: wordToMatch,
                    ...(!wordToMatch.length) && { data: [] },
                    currentPage: 1,
                };
            });
        }
    };

    // filter users on the basis of name
    const handleMessageSearchChange = ({ target: { value: wordToMatch = "" } }) => {
        if (wordToMatch.slice(-1) !== "\\") {
            setFilteredMessages((prev) => {
                return {
                    ...prev,
                    query: wordToMatch,
                };
            });
        }
    };

    // fetch chat history of user
    const fetchUserMessages = useCallback(
        (messageId = "", limit = 0) => {
            const payload = {
                limit: limit || MESSAGES_LIMIT,
                room_name: roomId,
                id: messageId,
                isDebounceCall : true
            };
            dispatch(
                getUserMessagesList(
                    payload,
                    (res = {}, type) => {
                        setMessages((prev) => {
                            return {
                                data: !messageId ? res?.data || [] : [...res?.data, ...prev.data],
                                totalCount: res?.count || 0,
                            };
                        });
                        setChatLoading(false);
                    },
                    (err) => {
                        setMessages({ data: [], totalCount: 0 });
                        setChatLoading(false);
                        sendNotification({
                            msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
        },
        [roomId, messages],
    );

    // fetch chat users
    const fetchChatUsers = useCallback(
        (disabledLoading = false, initialPagefetch = 0, isArchive = 0, isBlock = 0) => {
            const payload = {
                page: initialPagefetch || usersList?.currentPage || 1,
                limit: GET_USERS_LIMIT,
                archive: isArchive,
                blocked: isBlock,
                isDebounceCall: disabledLoading,
            };
            // don't do api call if current page is <== total page length
            const totalPages = Math.ceil(usersList?.totalCount / GET_USERS_LIMIT || 1);
            if (payload?.page <= totalPages) {
                dispatch(
                    getChatUsersList(
                        payload,
                        (res = {}, type) => {
                            // append latest data with prev one and increment currentPage
                            setUsersList((prev) => {
                                const updatedData =
                                    initialPagefetch == 1
                                        ? res?.data
                                            : res?.data || []
                                            ? [...prev.data, ...res.data]
                                        : [...prev?.data];
                                return {
                                    ...prev,
                                    data: updatedData,
                                    currentPage: prev.currentPage + 1,
                                    totalCount: res?.count,
                                };
                            });
                        },
                        (err) => {
                            setUsersList((prev) => {
                                return {
                                    ...prev,
                                    currentPage: 1,
                                    data: [],
                                    isLoading: false,
                                    totalCount: 0,
                                };
                            });
                            sendNotification({
                                msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                                variant: SNACK_VARIANT.ERROR,
                            });
                        },
                    ),
                );
            }
        },
        [usersList],
    );

    // search users on server
    const fetchUsersByName = useCallback(
        (searchCall = false) => {
            const payload = {
                search: filteredUsersData?.query,
                page: searchCall ? 1 : filteredUsersData?.currentPage || 1,
                limit: SEARCH_USERS_LIMIT,
                isDebounceCall: true,
            };
            if (filteredUsersData?.query !== null) {
                // show loading when search call happens
                if(!filteredUsersData?.query?.trim()?.length) return;
                if (searchCall) {
                    setFilteredUsersData((prev) => {
                        return {
                            ...prev,
                            isLoading: true,
                        };
                    });
                }
                dispatch(
                    getUsersListByName(
                        payload,
                        (res = {}, type) => {
                            // apend searched data with new one and increment current page
                            if (searchCall) {
                                setFilteredUsersData((prev) => {
                                    return {
                                        ...prev,
                                        isLoading: false,
                                        data: res?.data ? res.data : [],
                                        currentPage: prev.currentPage + 1,
                                        totalCount: res?.count,
                                    };
                                });
                            } else {
                                setFilteredUsersData((prev) => {
                                    return {
                                        ...prev,
                                        isLoading: false,
                                        data: res?.data
                                            ? [...prev.data, ...res.data]
                                            : [...prev?.data],
                                        currentPage: prev.currentPage + 1,
                                    };
                                });
                            }
                        },
                        (err) => {
                            setFilteredUsersData((prev) => {
                                return {
                                    ...prev,
                                    isLoading: false,
                                    data: [],
                                    currentPage: 1,
                                    totalCount: 0,
                                };
                            });
                            sendNotification({
                                msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                                variant: SNACK_VARIANT.ERROR,
                            });
                        },
                    ),
                );
            }
        },
        [filteredUsersData],
    );

    // search messages on server
    const fetchMessagesByText = useCallback(
        () => {
            const payload = {
                search: filteredMessages?.query,
                room_name: roomId,
                isDebounceCall: true,
            };
            if (filteredMessages?.query !== null) {
                // show loading when search call happens
                if (!filteredMessages?.query?.trim()?.length) {
                    setFilteredMessages((prev) => {
                        return {
                            ...prev,
                            isLoading: false,
                            data: [],
                            totalCount: 0
                        };
                    });
                    setActiveSearchIndex({id: null, pos: 0, visibleIndex: 0});
                    setChatLoading(false);
                    return;
                };
                // loading true
                setFilteredMessages((prev) => {
                    return {
                        ...prev,
                        isLoading: true,
                    };
                });

                // show chat loading only inital messages fetch when search messages
                messages?.data?.length !== messages?.totalCount && setChatLoading(true);
                dispatch(
                    getMessagesByText(
                        payload,
                        (res = {}, type) => {
                            // set searched messages res id's
                            setFilteredMessages((prev) => {
                                return {
                                    ...prev,
                                    isLoading: false,
                                    data: res?.data ? res.data : [],
                                    totalCount: res?.count,
                                };
                            });

                            // handle current state of active search element
                            setChatLoading(false);
                            setActiveSearchIndex({id: res?.data[0] || null, pos: 0, visibleIndex: res?.data?.length ? 1 : 0});
                        },
                        (err) => {
                            setFilteredMessages((prev) => {
                                return {
                                    ...prev,
                                    isLoading: false,
                                    data: [],
                                    totalCount: 0,
                                };
                            });
                            setChatLoading(false);
                            setActiveSearchIndex({id: null, pos: null, visibleIndex: null});
                            sendNotification({
                                msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                                variant: SNACK_VARIANT.ERROR,
                            });
                        },
                    ),
                );
            }
        },
        [filteredMessages, roomId],
    );

    // upload Media
    const uploadMediaCall = useCallback(() => {
        let type = "";
        if (file?.type?.indexOf("video") !== -1) {
            type = "2";
        } else if (file?.type?.indexOf("audio" || "mpeg") !== -1) {
            type = "3";
        } else if (file?.type === "text/csv" || file?.type?.indexOf("application") !== -1) {
            type = "4";
        } else {
            type = "1";
        }

        const payload = {
            type,
            media: file,
            room_name: roomId,
            isDebounceCall: true
        };
        setUploadLoading(true);
        dispatch(
            uploadMedia(
                payload,
                (res = {}) => {
                    if (res.data) {
                        sendMessage(type, res.data);
                    }
                    setUploadLoading(false);
                },
                (err) => {
                    setFile(null);
                    setUploadLoading(false);
                    sendNotification({
                        msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    }, [roomId, file]);

    const handleActiveUser = (user) => {
        if (activeUser?.id !== user?.id) {
            // triggerEvent join event to create roomId, if !socket call api
            if (socket) {
                socket?.triggerEvent("join", { to_user: user?.id });
            } else if (user?.room_name) {
                setRoomId(user?.room_name);
            }

            setActiveUser(user);
            setChatLoading(true);

            // clear send message input active user gets changed
            setSendTextValue("");
            setMessages({ data: [], count: 0 });

            resetCurrentSearchMessage();
        }
    };

    const handleActiveFilter = (filter) => {
        // archive
        setUsersList((prev) => {
            return {
                ...prev,
                currentPage: 1,
                data: [],
            };
        });
        if (filter == CHAT_FILTER_OPTIONS.ARCHIVED.ID) {
            // send archive == 1
            fetchChatUsers(false, 1, 1);
            resetCurrentUserState();
        } else if (filter == CHAT_FILTER_OPTIONS.BLOCKED.ID) {
            // send blocked == 1
            fetchChatUsers(false, 1, 0, 1);
            resetCurrentUserState();
        } else {
            // send archive == 0
            fetchChatUsers(false, 1, 0);
            resetCurrentUserState();
        }
        setActiveFilter(filter);
    };

    const handleSearchSidebar = (state) => {
        setSearchSidebarVisible(state);
        setFilteredUsersData((prev) => {
            return {
                ...prev,
                data: [],
                query: null,
                currentPage: 1,
                totalCount: 0,
            };
        });
    };

    const handleSendMessage = (e) => {
        e.preventDefault();
        sendTextValue?.length && sendMessage("0");
    };

    const handleKeypress = (e) => {
        if (e.key == "Enter" && !e.shiftKey) {
            e.preventDefault();
            handleSendMessage(e);
        }
    };

    const handleSendTextChange = (e) => {
        if (e.charCode === 13 && e.key === "shift") {
            sendMessage("0");
        } else if (e.charCode === 13 && !e.shiftKey) {
            e.preventDefault();
            setSendTextValue(e.target.value, "\r", "\n");
        } else {
            setSendTextValue(e.target.value);
        }
    };

    const sendMessage = (type = "0", value = "") => {
        if (sendTextValue.trim().length && type == "0") {
            socket?.triggerEvent("send_msg", {
                room_name: roomId,
                message: value || sendTextValue.trim(),
                timestamp: new Date(),
                type,
            });
            setSendTextValue("");
        } else {
            value.trim()?.length  && socket?.triggerEvent("send_msg", {
                room_name: roomId,
                type,
                url: value,
            });
            setFile(null);
        }
    };

    const handleUploadMedia = (e, type) => {
        if (e.target?.files[0]?.type) {
            if (e.target?.files[0]?.type === "text/csv" || e.target?.files[0]?.type.includes(type)) {
                if (e.target?.files[0]?.size) {
                    setFile(e.target.files[0]);
                }
                e.target.value = null;
                setAttachmentDropdownVisible(false);
            } else {
                sendNotification({
                    msg: `Please Select ${type} file`,
                    variant: SNACK_VARIANT.ERROR,
                });
            }
        }
    };

    const handleToggleModal = (type, value) => {
        setModalStates((prev) => {
            return {
                ...prev,
                [type]: value,
            };
        });
    };

    const handleClearChat = () => {
        socket?.triggerEvent(
            "clear_chat",
            {
                room_name: roomId,
            },
            () => {
                setMessages({ data: [], totalCount: 0 });
                handleToggleModal("clear", false);
                emitUpdatedChatList();

                sendNotification({
                    msg: "Chat cleared successfully.",
                    variant: SNACK_VARIANT.SUCCESS,
                });
            },
        );
    };

    const emitUpdatedChatList = () => {
        // trigger chat list to fetch inbox/archived latest chat data
        socket?.triggerEvent("chat_list", {
            page: usersList?.data?.currentPage ||  1,
            limit: usersList?.data?.length || GET_USERS_LIMIT,
            archive: activeFilter == CHAT_FILTER_OPTIONS.ARCHIVED.ID ? 1 : 0,
            blocked: activeFilter == CHAT_FILTER_OPTIONS.BLOCKED.ID ? 1 : 0,
        });
    };

    const handleArchiveChat = (type = "") => {
        if(type == "unarchive"){
            socket?.triggerEvent(
                "unarchive_chat",
                {
                    room_name: roomId,
                },
                () => {
                    removeCurrentUser();
                    resetCurrentUserState();
                    handleToggleModal("archive", false);
                    sendNotification({
                        msg: "Chat un-archived successfully.",
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
            );
        };

        if(activeFilter !== CHAT_FILTER_OPTIONS.ARCHIVED.ID){
            socket?.triggerEvent(
                "archive_chat",
                {
                    room_name: roomId,
                },
                () => {
                    removeCurrentUser();
                    resetCurrentUserState();
                    handleToggleModal("archive", false);
                    sendNotification({
                        msg: "Chat archived successfully.",
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
            );
        }
    };

    const handleDeleteChat = () => {
        socket?.triggerEvent(
            "delete_chat",
            {
                room_name: roomId,
            },
            () => {
                removeCurrentUser();

                resetCurrentUserState();
                handleToggleModal("delete", false);
                setRoomId(null);
                sendNotification({
                    msg: "Chat deleted successfully.",
                    variant: SNACK_VARIANT.SUCCESS,
                });
            },
        );
    };


    const handleBlockChat = (type = "") => {
        if (type == "unblock") {
            socket?.triggerEvent(
                "unblock_user",
                {
                    room_name: roomId,
                },
                () => {
                    handleToggleModal("block", false);
                    emitUpdatedChatList();
                    if(activeFilter == CHAT_FILTER_OPTIONS.BLOCKED.ID){
                        removeCurrentUser();
                        resetCurrentUserState();
                    }else{
                        setActiveUser((user) => {
                            return{
                                ...user,
                                is_blocked : false
                            }
                        });
                    }
                    sendNotification({
                        msg: "User un-blocked successfully.",
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
            );
        }

        if (type == "block") {
            socket?.triggerEvent(
                "block_user",
                {
                    room_name: roomId,
                },
                () => {
                    handleToggleModal("block", false);
                    emitUpdatedChatList();
                    setActiveUser((user) => {
                        return{
                            ...user,
                            is_blocked : true
                        }
                    });
                    sendNotification({
                        msg: "User blocked successfully.",
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
            );
        }
    };

    // clear current active user state
    const resetCurrentUserState = () => {
        setActiveUser({});
        setMessages({ data: [], totalCount: 0 });
        setRoomId(null);
    };

    const handleMessageClick = (type, data) => {
        if(type == "image"){
            setLargeImage(data);
        };
    };

    // to show/hide search message input
    const handleMessagesSearchbar = (val) => {
        setFilteredMessages((prev) => {
            return {
                ...prev,
                showSearch: val,
            };
        });

        if (!val) {
            resetCurrentSearchMessage();
        }
    };

    const resetCurrentSearchMessage = () => {
        // reset active search message state 
        setActiveSearchIndex({
            pos: null,
            visibleIndex: null,
            id: null,
        });

        // reset searched msg data state 
        setFilteredMessages((prev) => {
            return {
                data: [],
                query: null,
                isLoading: false,
                totalCount: 0,
            };
        });
    };

    const handleSearchTextNavigation = (type, currEl) => {
        if(type == "up"){
            // manage current active message position, id, visible index as fetched from server
            setActiveSearchIndex({
                pos : currEl?.pos + 1,
                id: filteredMessages?.data[currEl?.pos + 1],
                visibleIndex: currEl?.visibleIndex + 1
            });

        }else{
            setActiveSearchIndex({
                pos : currEl?.pos - 1,
                id: filteredMessages?.data[currEl?.pos - 1],
                visibleIndex: currEl?.visibleIndex - 1
            });

        }
    };

    return (
        <div className="form_white chat_panel d-flex">
            <aside className="chat_leftBar">
                <Inbox
                    activeUser={activeUser}
                    handleActiveUser={handleActiveUser}
                    setMessages={setMessages}
                    usersList={usersList?.data || []}
                    totalUsers={usersList?.totalCount}
                    history={history}
                    handleActiveFilter={handleActiveFilter}
                    activeFilter={activeFilter}
                    handleFilterUsersByName={handleSearchChange}
                    searchKeyword={filteredUsersData?.query}
                    isLoading={filteredUsersData?.isLoading}
                    searchedData={filteredUsersData?.data || []}
                    setSearchSidebarVisible={handleSearchSidebar}
                    searchSidebarVisible={searchSidebarVisible}
                    showChatFilter={showChatFilter}
                    setShowChatFilter={setShowChatFilter}
                    fetchUsersByName={fetchUsersByName}
                    fetchChatUsers={fetchChatUsers}
                    totalSearchUsers={filteredUsersData?.totalCount}
                    lastMessages={lastMessages}
                />
            </aside>
            <aside className="chat_rightBar">
                <LiveChat
                    activeUser={activeUser}
                    chatLoading={chatLoading}
                    uploadLoading={uploadLoading}
                    handleSendMessage={handleSendMessage}
                    handleKeypress={handleKeypress}
                    messages={messages}
                    setAttachmentDropdownVisible={setAttachmentDropdownVisible}
                    attachmentDropdownVisible={attachmentDropdownVisible}
                    onChangeFile={handleUploadMedia}
                    sendTextValue={sendTextValue}
                    handleSendTextChange={handleSendTextChange}
                    handleToggleModal={handleToggleModal}
                    modalStates={modalStates}
                    handleClearChat={handleClearChat}
                    handleArchiveChat={handleArchiveChat}
                    handleDeleteChat={handleDeleteChat}
                    fetchUserMessages={fetchUserMessages}
                    setShowChatOptions={setShowChatOptions}
                    showChatOptions={showChatOptions}
                    activeFilter={activeFilter}
                    lastMessages={lastMessages}
                    handleBlockChat={handleBlockChat}
                    handleMessageClick={handleMessageClick}
                    largeImage={largeImage}
                    handleMessageSearchChange={handleMessageSearchChange}
                    fetchMessagesByText={fetchMessagesByText}
                    searchMessageText={filteredMessages?.query}
                    handleMessagesSearchbar={handleMessagesSearchbar}
                    filteredMessages={filteredMessages}
                    virtuosoRef={virtuosoRef}
                    activeSearchIndex={activeSearchIndex}
                    handleSearchTextNavigation={handleSearchTextNavigation}
                />
            </aside>
        </div>
    );
};

export default Messages;
