import TextField from "components/atom/textField/TextField";
import TextEditor from "components/molecules/textEditor";
import { htmlToText } from "components/utility";
import React, { useState } from "react";
import { URL_HEAD } from "shared/api";
import { BUTTONS } from "shared/constants";
import { Images } from "shared/images";

function PagesField({
  editAPI = () => {},
  data = {},
  onImageChange = () => {},
}) {
  //state variables
  const [pageContent, setPageContent] = useState(
    data?.section_content ? data?.section_content : ""
  );
  const [title, setTitle] = useState(
    data?.section_name ? htmlToText(data?.section_name) : ""
  );
  const [images, setImages] = useState(data?.files ? data?.files : []);

  //if page content or page title is null, disable button
  const disabled = () => {
    if (!htmlToText(pageContent)?.length || !htmlToText(title)?.length) {
      return true;
    } else {
      return false;
    }
  };

  return (
    <>
      <form autoComplete="off">
        <div className="row">
          <div className="col-md-12">
            <TextField
              label={"Title"}
              value={title}
              type="text"
              onChange={(e) => {
                setTitle(e.target.value);
              }}
            />
          </div>
          {images?.length ? (
            <div className="col-md-12 mb-3">
              <label>Upload image</label>
              <div className="d-flex flex-wrap">
                {images?.map((i, idx) => (
                  <div className="me-3 mt-2 position-relative file_upload_field">
                    <div className="profile_pic d-inline-block">
                      <div className="avtar avtar100">
                        <img
                          src={
                            i?.file ? URL_HEAD + i?.file : Images.defaultUserPic
                          }
                          alt="Locumbay"
                          width={85}
                        />
                      </div>
                      <span className="upload_icon">
                        <input
                          type="file"
                          accept="image/*"
                          onChange={(e) => {
                            onImageChange(e?.target?.files[0], i?.id, i?.order);
                          }}
                        />
                        <img src={Images.edit} alt="Locumbay" width={35} />
                      </span>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          ) : null}
        </div>
        <TextEditor setValue={setPageContent} value={pageContent} />
        <div className="text-center mt-3">
          <button
            className={`btn btn-md btn-primary ${disabled() ? "disabled" : ""}`}
            onClick={(e) => editAPI(e, title, pageContent, data?.id)}
          >
            {BUTTONS.UPDATE}
          </button>
        </div>
      </form>
    </>
  );
}

export default PagesField;
