import TextEditor from "components/molecules/textEditor";
import { htmlToText } from "components/utility";
import React, { useEffect, useState } from "react";
import { BUTTONS, STRINGS } from "shared/constants";

function EmailField({
  data = {},
  activeTab,
  editAPI = () => {},
  setIsDataChanged = () => {},
}) {
  //State variables
  const [content, setContent] = useState("");

  //When component mounts save content data from Data passed as prop
  useEffect(() => {
    setContent(data?.content ? data?.content : "");
  }, [data, activeTab]);

  //when data from prop or content  changes check if data is same or different
  useEffect(() => {
    if (htmlToText(data?.content) !== htmlToText(content)) {
      setIsDataChanged(true);
    } else {
      setIsDataChanged(false);
    }
  }, [content, data]);

  //Function for disab
  const disabled = () => {
    if (content === data?.content) {
      return true;
    } else if (!htmlToText(content)?.trim()?.length) {
      setIsDataChanged(false);
      return true;
    } else {
      return false;
    }
  };

  return (
    <div className="mt-3">
      <TextEditor value={content} setValue={setContent} />

      <div className="px-3 pb-3">
        <h6 className="h6 mb-3">{STRINGS.EMAIL_NOTES.heading}</h6>
        <ul className="ms-3 mb-0">
          {STRINGS.EMAIL_NOTES.VARIABLES.map((value) => (
            <li>{value}</li>
          ))}
        </ul>
      </div>

      <div className="text-end py-3">
        <button
          className={`btn btn-md btn-primary ${disabled() ? "disabled" : ""}`}
          onClick={() => editAPI(content)}
        >
          {BUTTONS.SAVE}
        </button>
      </div>
    </div>
  );
}

export default EmailField;
