import React, { useState } from "react";
import { BUTTONS, LOGIN_FORM, REGEX } from "shared/constants";
import { Images } from "shared/images";
import TextField from "components/atom/textField/TextField";
import ButtonGreen from "../../../components/atom/button/ButtonGreen";

function ResetPass({ onUpdate = () => {} }) {
  const [checkPass, setCheckPass] = useState("");
  const [newPass, setNewPass] = useState("");
  const [oldPass, setOldPass] = useState("");
  const [passwordNewErr, setPasswordNewErr] = useState("");
  const [passwordErr, setPasswordErr] = useState("");

  const formData = [
    {
      key: 1,
      label: LOGIN_FORM.PASSWORDCHANGEFORM.FIELDS.OLDPASSWORD.LABEL,
      image: Images.ShowPwdIcon,
      value: oldPass,
      onChange: (e) => setOldPass(e.target.value),
    },
    {
      key: 2,
      label: LOGIN_FORM.PASSWORDCHANGEFORM.FIELDS.NEWPASSWORD.LABEL,
      image: Images.ShowPwdIcon,
      value: newPass,
      error: passwordNewErr,
      onChange: (e) => {
        setNewPass(e.target.value)
        if (e.target.value?.length === 0) {
          setNewPass(e.target.value);
          setPasswordNewErr("");
        } else if (
          e.target.value === "-" ||
          e.target.value?.includes("-") ||
          !new RegExp(REGEX.PASSWORD).test(e.target.value)
        ) {
          setPasswordNewErr(LOGIN_FORM.FIELDS.PASSWORD.ERROR);
        } else {
          setNewPass(e.target.value);
          setPasswordNewErr("");
        }
      },
    },
    {
      key: 3,
      label: LOGIN_FORM.PASSWORDCHANGEFORM.FIELDS.CONFIRMPASSWORD.LABEL,
      image: Images.ShowPwdIcon,
      value: checkPass,
      error: passwordErr,
      disabled: !newPass.length,
      onChange: (e) => {
        setCheckPass(e.target.value);
        if (!e.target.value.length) {
          setPasswordErr("");
        } else if (newPass !== e.target.value) {
          setPasswordErr(
            LOGIN_FORM.PASSWORDCHANGEFORM.FIELDS.CONFIRMPASSWORD.ERROR
          );
        } else {
          setPasswordErr("");
        }
      },
    },
  ];

  const isDisable = () => {
    if (oldPass.length === 0 || checkPass.length === 0) {
      return true;
    } else if (passwordErr.length || passwordNewErr.length) {
      return true;
    } else {
      return false;
    }
  };

  return (
    <>
      <form autoComplete="off">
        {formData.map((i, idx) => (
          <TextField
            id={i.key}
            label={i?.label}
            image={i?.image}
            value={i?.value}
            type="password"
            onChange={i?.onChange}
            error={i?.error}
            disabled={i?.disabled}
          />
        ))}

        <div className="text-center">
          <ButtonGreen
            disabled={isDisable()}
            title={BUTTONS.UPDATE}
            onClick={(e) => onUpdate(e, oldPass, newPass, passwordErr)}
          />
        </div>
      </form>
    </>
  );
}

export default ResetPass;
