import React, { useEffect, useRef, useState } from "react";
import { DateRange } from "react-date-range"; // import DateRange component from react-date-range library
import "react-date-range/dist/styles.css"; // main CSS file for DateRange component
import "react-date-range/dist/theme/default.css"; // theme CSS file for DateRange component
import './style.scss'
import moment from "moment";

function DateRangeSelector({ dateRange={}, setDateRange = () => {}, daysError, titleText="", icon, onIconClick=()=>{} }) {
  const [isOpen, setIsOpen] = useState(false); // state hook to track whether calendar is open or not
  const datePickerRef = useRef(); // ref hook to reference parent element of dropdown
  const toggleCalendar = () => {
    // function to toggle opening and closing of calendar
    setIsOpen(!isOpen);
  };

  // useEffect hook with event listener that listens for clicks outside the dropdown
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (
        datePickerRef.current &&
        !datePickerRef.current.contains(event.target)
      ) {
        setIsOpen(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    return () => {
      document.removeEventListener("mousedown", handleClickOutside); // remove event listener on cleanup
    };
  }, []);

  const handleCalendarMouseDown = (event) => {
    // function to stop event propagation when clicking inside calendar
    event.stopPropagation();
  };

  return (
    <>
      <div className="common_calender position-relative" ref={datePickerRef}>
        {/*  parent element of dropdown */}
        <div className="d-flex flex-column" onClick={toggleCalendar}>
          {/* div containing currently selected date range */}
          {titleText && <label className="titleText">{titleText}</label>}
          <div className="d-flex form-control bg-grey position-relative ">
            {dateRange.startDate && dateRange.endDate ? <span>{`${moment(dateRange.startDate).format("DD MMM YYYY")} - ${moment(dateRange.endDate).format("DD MMM YYYY")}`}</span> : null}{" "}
             {/* display currently selected date range  */}
             {!!icon && (
                <em
                    onClick={() => {
                        onIconClick();
                    }}
                    className="show_hide_pwd"
                >
                    <img width="12" src={icon} alt="" />
                </em>
              )}
          </div>
          {daysError ? <span className="text-danger">{daysError}</span> : ""}
          {/* Show error */}
        </div>
        {/* render calendar if it's open */}
        {isOpen && (
          <div
            className="calendar-wrapper"
            onMouseDown={handleCalendarMouseDown} // prevent event propagation when clicking inside calendar
          >
            <DateRange
              wrapperClassName="date-picker-wrapper"
              ranges={[dateRange]} // pass in currently selected date range to DateRange component
              onChange={(item) => {
                setDateRange(item.selection); // update 'dateRange' prop with newly selected dates
              }}
             //// minDate={new Date()} // set minimum selectable date to today's date
            />
          </div>
        )}
      </div>
    </>
  );
}

export default DateRangeSelector;
