import CustomModal from "components/atom/modal";
import React, { useEffect, useState } from "react";
import {
  BUTTONS,
  EDIT_PROFILE,
  SNACK_MESSAGES,
  SNACK_VARIANT,
} from "shared/constants";
import TextField from "components/atom/textField/TextField";
import { Images } from "shared/images";
import ButtonGreen from "components/atom/button/ButtonGreen";
import { useDispatch, useSelector } from "react-redux";
import { editAdminProfile } from "redux/actions/auth";
import useNotification from "components/atom/snackbar";
import { generateDownload } from "components/atom/CropImage/cropImage";
import Cropper from "react-easy-crop";
import { URL_HEAD } from "shared/api";

function ProfileEdit({ showProfileEdit, setShowProfileEdit }) {
  const dispatch = useDispatch();
  const adminDetails = useSelector((state) => state.auth.adminDetails);
  const [msg, sendNotification] = useNotification();
  const [firstName, setFirstName] = useState();
  const [lastName, setLastName] = useState();
  const [email, setEmail] = useState();
  const [imageUrl, setimageUrl] = useState(null);
  const [image, setImage] = useState("");
  const [croppedArea, setCroppedArea] = React.useState(null);
  const [crop, setCrop] = React.useState({ x: 0, y: 0 });
  const [zoom, setZoom] = React.useState(1);
  const [imageFile, setImageFile] = useState(null);

  useEffect(() => {
    if (adminDetails) {
      setFirstName(adminDetails.first_name);
      setLastName(adminDetails.last_name);
      setimageUrl(URL_HEAD + adminDetails.photo);
      setImageFile(adminDetails.photo);
      if (adminDetails.email) setEmail(adminDetails.email);
    }
  }, [adminDetails, showProfileEdit]);

  const saveData = () => {
    let data = {
      ...(adminDetails.first_name !== firstName && { first_name: firstName }),
      ...(adminDetails.last_name !== lastName && { last_name: lastName }),
      ...(imageFile && { photo: imageFile }),
    };

    dispatch(
      editAdminProfile(
        data,
        (res) => {
          setShowProfileEdit((prev) => !prev);
          sendNotification({
            msg: res?.data?.message
              ? res?.data?.message
              : SNACK_MESSAGES.SAVED_SUCCESSFULLY,
            variant: SNACK_VARIANT.SUCCESS,
          });
        },
        (err) => {
          setShowProfileEdit((prev) => !prev);
          sendNotification({
            msg: err?.data?.message
              ? err?.data?.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  const formData = [
    {
      label: EDIT_PROFILE.FIRSTNAME,
      value: firstName,
      onChange: (e) => {setFirstName((e.target.value)?.trim())},
    },
    {
      label: EDIT_PROFILE.LASTNAME,
      value: lastName,
      onChange: (e) => setLastName(e.target.value),
    },
    {
      label: EDIT_PROFILE.EMAIL,
      value: email,
      onChange: (e) => setEmail(e.target.value),
      disabled: true,
    },
  ];

  const disableProfile = () => {
    if (!firstName?.length && !lastName?.length) {
      return true;
    } else if (
      firstName === adminDetails?.first_name &&
      lastName === adminDetails?.last_name &&
      imageFile === adminDetails?.photo
    ) {
      return true;
    } else {
      return false;
    }
  };

  const onCropComplete = (croppedAreaPercentage, croppedAreaPixels) => {
    setCroppedArea(croppedAreaPixels);
  };

  function b64toBlob(b64Data, contentType, sliceSize) {
    contentType = contentType || "";
    sliceSize = sliceSize || 512;

    var byteCharacters = window.atob(b64Data);
    var byteArrays = [];

    for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      var slice = byteCharacters.slice(offset, offset + sliceSize);

      var byteNumbers = new Array(slice.length);
      for (var i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }

      var byteArray = new Uint8Array(byteNumbers);

      byteArrays.push(byteArray);
    }

    var blob = new Blob(byteArrays, { type: contentType });
    return blob;
  }

  const onDownload = async () => {
    const resultss = await generateDownload(image, croppedArea);
    const contentType = "image/jpeg";
    const b64Data = resultss.slice(23);

    var blob = b64toBlob(b64Data, contentType);
    var blobUrl = URL.createObjectURL(blob);
    var file = new File([blob], "jpeg");
    // dispatch(
    //   getImageFile(file, (callback) => {
    //     setimageUrl(callback);
    //   })
    // );
    setImageFile(file);
    setimageUrl(image);
    setImage(null); // this is to make crop area disappare after completion of croping
  };

  const closeCropper = () => {
    setImage("");
  };

  const reader = (e) => {
    const reader = new FileReader();
    reader.readAsDataURL(e.target.files[0]);
    reader.addEventListener("load", () => {
      setImage(reader.result);
    });
  };

  return (
    <>
      <CustomModal
        isOpen={showProfileEdit}
        className="customModal"
        handleToggle={() => setShowProfileEdit((prev) => !prev)}
        size="modal-md"
      >
        <div className="modal-content">
          <div className="p-4">
            <div className="w-100 text-center mb-3">
              <div className="profile_pic d-inline-block">
                <div className="avtar avtar130">
                  <img
                    src={imageUrl ? imageUrl : Images.defaultUserPic}
                    alt="Locumbay"
                    width={85}
                  />
                </div>
                <span className="upload_icon">
                  <input
                    type="file"
                    accept="image/*"
                    onChange={(e) => {
                      reader(e);
                    }}
                  />
                  <img src={Images.UploadIcon} alt="Locumbay" width={35} />
                </span>
              </div>
            </div>
            {formData.map((i, idx) => (
              <TextField
                key={idx}
                label={i.label}
                image={i.image}
                value={i.value}
                type="text"
                onChange={i.onChange}
                disabled={i.disabled}
              />
            ))}

            <div className="text-center">
              <ButtonGreen
                title={BUTTONS.UPDATE}
                disabled={disableProfile()}
                onClick={saveData}
              />
            </div>
          </div>
        </div>

        {image ? (
          <div className="img-crop">
            <button className="close_crop" onClick={() => closeCropper()}>
              <img src={Images.CloseIcon} alt="" width={20} />
            </button>
            <Cropper
              image={image}
              crop={crop}
              zoom={zoom}
              aspect={1}
              onCropChange={setCrop}
              onZoomChange={setZoom}
              onCropComplete={onCropComplete}
            />
            <div className="text-center position-absolute save_btn">
              <button className="btn btn-md btn-secondary" onClick={onDownload}>
                {BUTTONS.SAVE}
              </button>
            </div>
          </div>
        ) : null}
      </CustomModal>
    </>
  );
}

export default ProfileEdit;
