import ButtonStatus from "components/atom/button/ButtonStatus";
import { Pagination } from "components/atom/pagination";
import CustomTable from "components/atom/table";
import SortIcon from "components/molecules/sortIcon";
import { getLocalizedDateString } from "components/utility";
import React from "react";
import { SORT_KEY, STRINGS } from "shared/constants";
import { Images } from "shared/images";
import { INVOICE_SERVICES } from "shared/tableData";

function InvoiceTable({
  navigateBranches = () => {},
  data,
  itemsPerPage,
  setItemsPerPage = () => {},
  pageCount,
  setPageCount = () => {},
  setSortKey = () => {},
  setSortType = () => {},
  handleSort = () => {},
  sortKey,
  pageItems,
  setPageItems = () => {},
  toggle = {
    isActive: false,
    name: "",
  },
  setToggle = () => {},
}) {
  // function to switch the sort key based on the column name
  const sortSwitchKey = (name) => {
    switch (name) {
      case INVOICE_SERVICES.CREATED_DATE:
        return setSortKey(SORT_KEY.createdAt);
      case INVOICE_SERVICES.FROM:
        return setSortKey(SORT_KEY.from);
      case INVOICE_SERVICES.TO:
        return setSortKey(SORT_KEY.to);
      default:
        return setSortKey(SORT_KEY.default);
    }
  };

  // function to determine whether to display the sort icon for a column
  const sortImage = (name) => {
    switch (name) {
      case INVOICE_SERVICES.CREATED_DATE:
        return true;
      case INVOICE_SERVICES.FROM:
        return true;
      case INVOICE_SERVICES.TO:
        return true;
      default:
        return false;
    }
  };

  return (
    <div className="tab_content">
      <CustomTable>
        {/* render the column headers */}
        <thead>
          <tr key={1000 + 1}>
            {Object.values(INVOICE_SERVICES).map((i, idx) => (
              <th scope="col" id={idx}>
                {i}
                {sortImage(i) ? (
                  <SortIcon
                    toggleIcon={
                      toggle.name === i && toggle.isActive ? true : false
                    }
                    onClick={() => {
                      if (toggle.name !== i) {
                        setSortType(0);
                      }
                      setToggle({
                        name: i,
                        isActive: !toggle.isActive,
                      });
                      sortSwitchKey(i);
                      setPageCount(1);
                      handleSort();
                    }}
                  />
                ) : null}
              </th>
            ))}
          </tr>
        </thead>

        {/* render the body */}
        {data?.length ? (
          data.map((i, idx) => {
            return (
              <>
                <tbody>
                  <tr>
                    <td>{i?.from_invoice} </td>
                    <td>{i?.to_invoice} </td>
                    <td>{getLocalizedDateString(i?.created_at)} </td>
                    <td>
                      <ButtonStatus
                        image={Images.viewIcon}
                        status={"default"}
                        title={"View Details"}
                        onClick={() => navigateBranches(i?.id)}
                      />
                    </td>
                  </tr>
                </tbody>
              </>
            );
          })
        ) : (
          <tbody>
            <tr>
              <td
                colSpan={Object.values(INVOICE_SERVICES).length + 1}
                className="text-center py-5"
              >
                {STRINGS.NO_DATA_AVAILIABLE}
              </td>
            </tr>
          </tbody>
        )}
      </CustomTable>

      {data?.length ? (
        <Pagination
          items={data?.length ? data?.length : []}
          setPageCount={setPageCount}
          itemsPerPage={itemsPerPage}
          setItemsPerPage={setItemsPerPage}
          pageCount={pageCount}
        />
      ) : null}
    </div>
  );
}

export default InvoiceTable;
