import React from "react";
import { Images } from "shared/images";
import VerifiedLogo from "../verifiedIcon";
import ProgressBar from "../ProgressBar/progress";
import Ratingstars from "../rating";
import "./style.scss";

// It accepts data as Array of Objects where object is in {title: "", value: ""} format
// If there is associated icon with any of the data send in as {image: "URL", title: "", value: ""}
// Send rating in number of stars

const ProfileDetails = ({ data, imageURL, rating, profileCompleteness }) => {
  const progData = [
    { bgcolor: "#20606D", completed: 60 },
    // { bgcolor: "#00695c", completed: 30 },
    // { bgcolor: "#ef6c00", completed: 53 },
  ];
  return (
    <div className="row">
      <div className="col-12 col-sm-5 col-md-3 col-xl-2 mb-4 position-relative">
        <div className="user_profile">
          <img src={imageURL} alt="profile" width={300} className="w-100" />
        </div>
        {progData.map((item, idx) => (
          <ProgressBar
            key={idx}
            bgcolor={item.bgcolor}
            completed={profileCompleteness}
          />
        ))}
      </div>
      <div className="col-md-9 col-xl-10 mb-4">
        <div className="card h-100">
          <div className="card-body">
            <div className="row">
              {data?.map((item, idx) => (
                <div
                  key={idx}
                  className={`col-md-4 col-sm-6 mb-3 ${
                    item?.navigate ? "clickable" : ""
                  }`}
                  onClick={() => item?.navigate()}
                >
                  <label className="text-small">{item.title}</label>
                  <span className="d-block">
                    {item.value}

                    {Object(item).hasOwnProperty("isVerified") ? (
                      <VerifiedLogo isVerified={item?.isVerified} />
                    ) : null}
                  </span>
                </div>
              ))}
              {rating ? (
                <div className="col-md-4 col-sm-6 mb-3">
                  <label className="text-small">Overall Rating</label>
                  <div className="rating">
                    <Ratingstars rating={rating} />
                  </div>
                </div>
              ) : null}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProfileDetails;
