import React, { useState } from "react";
import { BUTTONS, LOGIN_FORM, REGEX, SNACK_VARIANT } from "shared/constants";
import { Images } from "shared/images";
import TextField from "components/atom/textField/TextField";
import { useDispatch } from "react-redux";
import { confirmPassword, resetPassword } from "redux/actions/auth";
import ButtonGreen from "../../../components/atom/button/ButtonGreen";
import useNotification from "components/atom/snackbar";
import { SNACK_MESSAGES } from "../../../shared/constants";
import { useParams } from "react-router-dom/cjs/react-router-dom.min";
import { ROUTE_CONSTANTS } from "shared/routeConstants";
import { useHistory } from "react-router-dom";

function ForgotPass({ handleToggle }) {
  const dispatch = useDispatch();
  const history = useHistory();
  const params = useParams();
  const [checkPass, setCheckPass] = useState("");
  const [newPass, setNewPass] = useState("");
  const [passwordErr, setPasswordErr] = useState("");
  const [passwordNewErr, setPasswordNewErr] = useState("");
  const [msg, sendNotification] = useNotification();

  const onUpdate = (e) => {
    e.preventDefault();
    let data = {
      uid: params.uid,
      token: params.token,
      password: newPass,
    };
    if (newPass?.length === 0 && checkPass?.length === 0) {
      sendNotification({
        msg: SNACK_MESSAGES.ENTER_PASSWORD,
        variant: SNACK_VARIANT.ERROR,
      });
    } else if (passwordErr?.length !== 0) {
      sendNotification({
        msg: SNACK_MESSAGES.MATCH_PASSWORD,
        variant: SNACK_VARIANT.ERROR,
      });
    } else {
      dispatch(
        confirmPassword(
          data,
          (res) => {
            history.push(ROUTE_CONSTANTS.LOGIN);
            sendNotification({
              msg: SNACK_MESSAGES.ENTER_PASSWORD,
              variant: SNACK_VARIANT.ERROR,
            });
          },
          (err) => {
            sendNotification({
              msg: err?.data?.message
                ? err?.data?.message
                : SNACK_MESSAGES.ENTER_PASSWORD,
              variant: SNACK_VARIANT.ERROR,
            });
          }
        )
      );
    }
  };

  const formData = [
    {
      label: LOGIN_FORM.PASSWORDCHANGEFORM.FIELDS.NEWPASSWORD.LABEL,
      image: Images.ShowPwdIcon,
      error: passwordNewErr,
      value: newPass,
      onChange: (e) => {
        validate(e.target.value);
        setNewPass(e.target.value);
      },
    },
    {
      label: LOGIN_FORM.PASSWORDCHANGEFORM.FIELDS.CONFIRMPASSWORD.LABEL,
      image: Images.ShowPwdIcon,
      value: checkPass,
      error: passwordErr,
      onChange: (e) => {
        setCheckPass(e.target.value);
        if (e.target.value?.length === 0) {
          setCheckPass(e.target.value);
          setPasswordErr("");
        } else if (newPass !== e.target.value) {
          setPasswordErr(
            LOGIN_FORM.PASSWORDCHANGEFORM.FIELDS.CONFIRMPASSWORD.ERROR
          );
        } else {
          setPasswordErr("");
        }
      },
    },
  ];

  const validate = (value) => {
    if (value?.length === 0) {
      setNewPass(value);
      setPasswordNewErr("");
    } else if (
      value === "-" ||
      value?.includes("-") ||
      !new RegExp(REGEX.PASSWORD).test(value)
    ) {
      setPasswordNewErr(LOGIN_FORM.FIELDS.PASSWORD.ERROR);
    } else {
      setNewPass(value);
      setPasswordNewErr("");
    }
  };

  const switchDisable = () => {
    if (passwordNewErr?.length || passwordErr?.length) {
      return true;
    } else if (!checkPass?.length || !newPass?.length) {
      return true;
    } else {
      return false;
    }
  };

  return (
    <>
      <form autoComplete="off">
        {formData.map((i, idx) => (
          <TextField
            key={idx}
            label={i.label}
            image={i.image}
            value={i.value}
            type="password"
            error={i.error}
            onChange={i.onChange}
          />
        ))}

        <div className="text-center">
          <ButtonGreen
            title={BUTTONS.UPDATE}
            onClick={(e) => onUpdate(e)}
            disabled={switchDisable()}
          />
        </div>
      </form>
    </>
  );
}

export default ForgotPass;
