import { profileCompleted } from "components/utility";
import React from "react";
import { URL_HEAD } from "shared/api";
import { NA, PHARMACY_USER_VIEW } from "shared/constants";
import { Images } from "shared/images";
import { JOB_TIMING } from "shared/tableData";
import "./style.scss";
import ProfileDetails from "components/atom/ProfileDetails";
import ReadMore from "components/atom/ReadMore";

function BasicView({ data, apiData }) {
  // This component takes in two props: data and apiData
  // It returns a JSX structure that displays information about the user
  console.log(apiData, "DATA for Branch");
  return (
    <div className="tab_content">
      <ProfileDetails
        data={data}
        imageURL={
          apiData?.profile_pic
            ? URL_HEAD + apiData?.profile_pic
            : Images.defaultUserPic
        }
        rating={3}
        profileCompleteness={profileCompleted(
          apiData?.profile_completeness,
          apiData?.verified_document
        )}
      />

      {apiData.description ? (
        <div className="card mb-4">
          <div className="card-body">
            <h6 className="h6">{PHARMACY_USER_VIEW.DESCRIPTIONS}</h6>

            <p>
              {apiData.description ? (
                <ReadMore numberOfCharacters={580}>
                  {apiData.description}
                </ReadMore>
              ) : null}
            </p>
          </div>
        </div>
      ) : null}

      <div className="card">
        <div className="card-body">
          <div className="opening_time">
            <h6 className="h6">{PHARMACY_USER_VIEW.JOB_TIMINGS}</h6>
            <div className="table-responsive">
              <table className="w-100 time_table">
                <tr key={1000 + 1}>
                  <th width="80"></th>
                  <th width="80"></th>
                  {Object.values(JOB_TIMING.HEADER).map((i, idx) => {
                    return (
                      <>
                        <th key={idx}>{i}</th>
                      </>
                    );
                  })}
                </tr>
                {apiData?.job_timings?.length
                  ? apiData?.job_timings?.map((i, idx) => (
                      <tr key={idx}>
                        <th className="text-start">
                          {JOB_TIMING.WEEKDAYS[i?.day]}
                        </th>
                        <td> - </td>
                        <td> {i?.opening_time ? i?.opening_time : NA} </td>
                        <td> {i?.closing_time ? i?.closing_time : NA} </td>
                        <td> {i?.lunch_start ? i?.lunch_start : NA} </td>
                        <td> {i?.lunch_end ? i?.lunch_end : NA} </td>
                      </tr>
                    ))
                  : JOB_TIMING.WEEKDAYS?.map((i, idx) => (
                      <tr key={idx}>
                        <th className="text-start">{i}</th>
                        <td> - </td>
                        <td> {NA} </td>
                        <td> {NA} </td>
                        <td> {NA} </td>
                        <td> {NA} </td>
                      </tr>
                    ))}
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default BasicView;
