import React, { useState } from "react";
import "./styles.scss";
import NavTab from "components/atom/tabNav";
import { BUTTONS, SNACK_MESSAGES, SNACK_VARIANT } from "shared/constants";
import useNotification from "components/atom/snackbar";
import ActivateDeactivate from "components/molecules/activateDeactivate";
import CustomTable from "components/atom/table";
import Table from "./Table";
import CustomModal from "components/atom/modal";
import { useDispatch } from "react-redux";
import { useCallback } from "react";
import {
  editFilePagesManagement,
  editPagesManagement,
  fetchPagesManagement,
  statusUpdatePagesManagement,
} from "redux/actions/auth";
import { useEffect } from "react";
import PagesField from "./PageField";
import { ROUTE_TITLES } from "shared/routeConstants";

function PagesManagement() {
  //state variables
  const dispatch = useDispatch();
  const [activeTab, setActiveTab] = useState(1);
  const [msg, sendNotification] = useNotification();
  const [data, setData] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [toggle, setToggle] = useState(false);
  const [pagesData, setPagesData] = useState({});
  const [multiplePayload, setMultiplePayload] = useState([]);
  const [id, setId] = useState(null);
  const [tabData, setTabData] = useState([]);
  const [images, setImages] = useState("");

  //function to fetch Page Management data
  const fetchData = useCallback(() => {
    dispatch(
      fetchPagesManagement(
        "",
        (res, type) => {
          setData(res?.data);
          let temp = res?.data?.length
            ? res?.data?.map((i, idx) => ({
                title: i?.name,
                id: i?.id,
              }))
            : [];
          setTabData(temp);
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  }, [dispatch, sendNotification]);

  //calls Fetchdata function
  useEffect(() => {
    fetchData();
  }, [fetchData, sendNotification]);

  //When data is fetched succesfully change currently selected page data
  useEffect(() => {
    if (data?.length) {
      setPagesData(
        data[data?.findIndex((item) => item?.id === activeTab)]?.sections[
          data[
            data?.findIndex((item) => item?.id === activeTab)
          ]?.sections?.findIndex((item) => item.id === id)
        ]
      );
    }
  }, [data]);

  //edit page data api
  const editAPI = (e, title, pageContent, id) => {
    e.preventDefault();
    let payload = {
      id: id,
      data: {
        section_name: title,
        section_content: pageContent,
      },
    };

    //function for API sucess
    const onSuccess = (res) => {
      setShowModal(false);
      fetchData();
      sendNotification({
        msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
        variant: SNACK_VARIANT.SUCCESS,
      });
    };

    //function for API Failure
    const onError = (err) => {
      setShowModal(false);
      sendNotification({
        msg: err?.data?.message
          ? err.data.message
          : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
        variant: SNACK_VARIANT.ERROR,
      });
    };

    dispatch(
      editPagesManagement(payload, (res) => {
        onSuccess(res);
      }),
      (err) => {
        onError(err);
      }
    );
  };

  //Edit page image API
  const editImageAPI = (imageFile, fileId, imageOrder) => {
    let payload = {
      id: fileId,
      data: {
        file: imageFile,
        order: imageOrder,
        file_name: imageFile?.name ? imageFile?.name : "",
        file_content: imageFile?.type ? imageFile?.type : "",
      },
    };

    const onSuccess = (res) => {
      fetchData();
      //setShowModal(false);
      setToggle((prev) => !prev);
      sendNotification({
        msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
        variant: SNACK_VARIANT.SUCCESS,
      });
    };

    const onError = (err) => {
      //setShowModal(false);
      setToggle((prev) => !prev);

      sendNotification({
        msg: err?.data?.message
          ? err.data.message
          : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
        variant: SNACK_VARIANT.ERROR,
      });
    };

    dispatch(
      editFilePagesManagement(payload, (res) => {
        onSuccess(res);
      }),
      (err) => {
        onError(err);
      }
    );
  };

  // ON Activate/Deactivate status change
  const statusChange = (id, is_active) => {
    let data = {
      id: multiplePayload.length && !id ? multiplePayload : [id],
      status: is_active ? 1 : 0,
    };
    dispatch(
      statusUpdatePagesManagement(
        data,
        (res) => {
          fetchData();
          setMultiplePayload([]);
          sendNotification({
            msg: res?.data?.message
              ? res?.data.message
              : SNACK_MESSAGES.SAVED_SUCCESSFULLY,
            variant: SNACK_VARIANT.SUCCESS,
          });
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  //Modal for edit page data
  const EditPage = () => {
    return (
      <CustomModal
        isOpen={showModal}
        handleToggle={() => setShowModal((prev) => !prev)}
        xLarge={true}
      >
        <div className="confirmation_modal">
          <h6 className="h6 text-center text-capitalize mb-4">{`${BUTTONS.EDIT} Page`}</h6>
          <PagesField
            editAPI={editAPI}
            data={pagesData}
            onImageChange={editImageAPI}
            toggle={toggle}
            images={images}
            setImages={setImages}
          />
        </div>
      </CustomModal>
    );
  };


  const onTabChange = useCallback(() => {
    setMultiplePayload([]);
  }, [activeTab]);

  useEffect(() => {
    onTabChange();
  }, [activeTab, onTabChange]);

  return (
    <>
      <div className="d-flex align-items-center flex-wrap justify-content-between tab_full">
      <h1 className="h6 mb-3">{ROUTE_TITLES.PAGES_MANAGEMENT}</h1>

        <NavTab
          onClick={(id) => setActiveTab(id)}
          tabsData={tabData}
          activeTab={activeTab}
        />
        {multiplePayload.length ? (
          <>
            <ActivateDeactivate
              statusChange={statusChange}
              title={"Select Action"}
            />
          </>
        ) : null}
      </div>
      <div className="tab_content">
        <CustomTable>
          <Table
            data={data}
            statusChange={statusChange}
            setShowModal={setShowModal}
            setmultiplePayload={setMultiplePayload}
            multiplepayload={multiplePayload}
            setId={setId}
            activeTab={activeTab}
            setPagesData={setPagesData}
          />
        </CustomTable>
      </div>
      <EditPage />
    </>
  );
}

export default PagesManagement;
