import { useContext, useEffect, useState } from "react";
import "../styles.scss";
import Header from "components/hoc/header";
import NavBarHOC from "components/hoc/navBar";
import CustomModal from "components/atom/modal";
import { Images } from "shared/images";
import ButtonGreen from "components/atom/button/ButtonGreen";
import { useDispatch, useSelector } from "react-redux";
import ResetPass from "views/admin/login/ResetPass";
import { logout, resetPassword } from "redux/actions/auth";
import { SNACK_VARIANT, STRINGS } from "shared/constants";
import { SNACK_MESSAGES } from "../../shared/constants";
import useNotification from "components/atom/snackbar";
import ProfileEdit from "views/authenticated/dashboard/profileEdit";
import { setNotificationCount } from "redux/actions/common";
import SocketContext from "shared/SocketContext";
//import Snackbar from "components/atom/snackbar";

const PrivateLayout = ({ children }) => {
  const [showLogout, setShowLogout] = useState(false);
  const [showPasswordReset, setShowPasswordReset] = useState(false);
  const [showProfileEdit, setShowProfileEdit] = useState(false);
  const dispatch = useDispatch();
  const [msg, sendNotification] = useNotification();
  const [sidebarToggle, SetSidebarToggle] = useState(true);
  //const socketManager = useSelector((state) => state.socket.socketInstance);
  const { socket, socketError } = useContext(SocketContext);
  const [notifyToggle, setnotifyToggle] = useState(true);
  const notificationCount = useSelector(
    (state) => state.persist.notificationCount
  );

  const onLogout = () => {
    dispatch(
      logout(
        "",
        (res) => {
          setShowLogout(false);
          sendNotification({
            msg: res?.data?.message
              ? res?.data?.message
              : SNACK_MESSAGES.SAVED_SUCCESSFULLY,
            variant: SNACK_VARIANT.SUCCESS,
          });
        },
        (err) => {
          setShowPasswordReset(false);
          sendNotification({
            msg: err.data?.message
              ? err.data?.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  const profileData = [
    {
      title: "Edit Profile",
      image: Images.editProfile,
      onClick: () => setShowProfileEdit(true),
    },
    {
      title: "Change Password",
      image: Images.changePassword,
      onClick: () => setShowPasswordReset(true),
    },
    {
      title: "Logout",
      image: Images.logout,
      onClick: () => setShowLogout(true),
    },
  ];

  const LogoutModal = () => {
    return (
      <CustomModal
        isOpen={showLogout}
        className="as"
        avarge={true}
        handleToggle={() => setShowLogout((prev) => !prev)}
      >
        <div className="confirmation_modal text-center p-4">
          <em className="mb-3 modal_icon bg-grey">
            <img src={Images.logout} alt="" width="30" />
          </em>
          <h2 className="h5 mb-2">{STRINGS.ARE_YOU_SURE}</h2>
          <p className="mb-4">You want to logout this account.</p>
          <ButtonGreen title={"Logout"} onClick={onLogout} />
        </div>
      </CustomModal>
    );
  };

  const onUpdate = (e, oldPass, newPass, passwordErr) => {
    e.preventDefault();
    let data = {
      old_password: oldPass,
      new_password: newPass,
    };

    if (passwordErr.length === 0) {
      dispatch(
        resetPassword(
          data,
          (res) => {
            setShowPasswordReset((prev) => !prev);
            sendNotification({
              msg: res?.data?.message
                ? res?.data?.message
                : SNACK_MESSAGES.SAVED_SUCCESSFULLY,
              variant: SNACK_VARIANT.SUCCESS,
            });
          },
          (err) => {
            setShowPasswordReset((prev) => !prev);
            sendNotification({
              msg: err.data?.message
                ? err.data?.message
                : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
              variant: SNACK_VARIANT.ERROR,
            });
          }
        )
      );
    }
  };

  function ChangePasswordModal() {
    return (
      <CustomModal
        isOpen={showPasswordReset}
        className="customModal"
        handleToggle={() => setShowPasswordReset((prev) => !prev)}
      >
        <div className="text-center mb-2 p-md-2">
          <h2 className="h5 mb-0 text-capitalize">Change password</h2>
        </div>
        <div className="p-md-2">
          <ResetPass
            handleToggle={() => setShowPasswordReset((prev) => !prev)}
            onUpdate={onUpdate}
          />
        </div>
      </CustomModal>
    );
  }

  // useEffect(() => {
  //   if (socketManager?.socket?.connected) {
  //     socketManager.socket.on("notify", (res) => {
  //       dispatch(setNotificationCount(res?.data.countNotification));
  //       setnotifyToggle(true);
  //     });

  //     return socketManager?.socket?.removeAllListeners();
  //   }
  // });

  useEffect(() => {
    if (socket) {
      socket.listenEvent("notify", (res) => {
        dispatch(setNotificationCount(res?.data.countNotification));
        setnotifyToggle(true);
      });
    }

    return () => {
      if (socket) {
        socket.disconnectEvent("notify");
      }
    };
  }, [socket]);

  return (
    <main className="main">
      <LogoutModal />
      <ChangePasswordModal />
      <ProfileEdit
        setShowProfileEdit={setShowProfileEdit}
        showProfileEdit={showProfileEdit}
      />
      <NavBarHOC
        sidebarToggle={sidebarToggle}
        SetSidebarToggle={SetSidebarToggle}
      />
      <div className="content">
        <Header
          setShowLogout={setShowLogout}
          showLogout={showLogout}
          profileData={profileData}
          sidebarToggle={sidebarToggle}
          SetSidebarToggle={SetSidebarToggle}
          notifyToggle={notifyToggle}
          setnotifyToggle={setnotifyToggle}
          notificationCount={notificationCount}
        />
        <div className="container-fluid px-3 px-md-4">{children}</div>
      </div>
    </main>
  );
};

export default PrivateLayout;
