import React from "react";
import { useHistory } from "react-router-dom";
import {
  JOB_STATUS,
  NA,
  SORT_KEY,
  STRINGS,
} from "shared/constants";
import CustomTable from "components/atom/table";
import {
  getLocalizedDateString,
  switchbadges,
} from "components/utility";
import { Images } from "shared/images";
import ButtonStatus from "components/atom/button/ButtonStatus";
import Ratingstars from "components/atom/rating";
import { ROUTE_CONSTANTS } from "shared/routeConstants";
import SortIcon from "components/molecules/sortIcon";
import { Pagination } from "components/atom/pagination";

function JobTable({
  navigateBranches = () => {},
  data,
  itemsPerPage,
  setItemsPerPage = () => {},
  pageCount,
  setPageCount = () => {},
  setSortKey = () => {},
  setSortType = () => {},
  handleSort = () => {},
  sortKey,
  pageItems,
  setPageItems = () => {},
  toggle = {
    isActive: false,
    name: "",
  },
  setToggle = () => {},
}) {
  const history = useHistory();

  const headerData = {
    // ID: "#",
    JOB_ID: "Job ID",
    PHARMACY: "Pharmacy",
    JOB_TITLE: "Job Name",
    JOINED_DATE: "Joined Date",
    RATING: "Rating",
    RATE: "Rate",
    STATUS: "Status",
    ACTION: "Action",
  };

  const sortSwitchKey = (name) => {
    switch (name) {
      case headerData.PHARMACY:
        return setSortKey(SORT_KEY.company_name);
      case headerData.JOB_TITLE:
        return setSortKey(SORT_KEY.jobName);
      case headerData.JOINED_DATE:
        return setSortKey(SORT_KEY.startDate);
      case headerData.RATE:
        return setSortKey(SORT_KEY.rate);
      default:
        return setSortKey(SORT_KEY.default);
    }
  };

  // function to determine whether to display the sort icon for a column
  const sortImage = (name) => {
    switch (name) {
      case headerData.PHARMACY:
        return true;
      case headerData.JOB_TITLE:
        return true;
      case headerData.JOINED_DATE:
        return true;
      case headerData.RATE:
        return true;
      default:
        return false;
    }
  };

  return (
    <div className="tab_content">
      {data?.length ? (
        <CustomTable>
          <thead>
            <tr key={1000 + 1}>
              {Object.values(headerData).map((i, idx) => (
                <th scope="col" id={idx}>
                  {i}
                  {sortImage(i) ? (
                    <SortIcon
                      toggleIcon={
                        toggle.name === i && toggle.isActive ? true : false
                      }
                      onClick={() => {
                        if (toggle.name !== i) {
                          setSortType(0);
                        }
                        setToggle({
                          name: i,
                          isActive: !toggle.isActive,
                        });
                        sortSwitchKey(i);
                        setPageCount(1);
                        handleSort();
                      }}
                    />
                  ) : null}
                </th>
              ))}
            </tr>
          </thead>
          {data?.map((i, idx) => {
            return (
              <tbody>
                <tr>
                  <td
                    className="clickable"
                    onClick={() => navigateBranches(i?.id)}
                  >
                    {i?.id}
                  </td>
                  <td
                    className="clickable"
                    onClick={() =>
                      i?.pharmacy_id
                        ? history.push(ROUTE_CONSTANTS.PHARMACY_VIEW, {
                            id: i?.pharmacy_id,
                          })
                        : null
                    }
                  >
                    {i?.company_name}
                  </td>
                  <td>{i?.job_title}</td>
                  <td>{getLocalizedDateString(i?.start_date)}</td>
                  <td>
                    <div className="rating">
                      <Ratingstars rating={3} />
                    </div>
                  </td>
                  <td>{`${
                    JSON.stringify(i?.rate)?.length ? "" + i?.rate : NA
                  }`}</td>
                  <td>
                    <div className="d-flex align-items-center">
                      <span
                        className={`m-1 badge-md badge-${switchbadges(
                          i?.vacancy_status
                        )}`}
                      >
                        {JOB_STATUS[`${i?.vacancy_status}`] ?? NA}
                      </span>
                    </div>
                  </td>
                  <td>
                    <ButtonStatus
                      image={Images.viewIcon}
                      status={"default"}
                      title={"View Details"}
                      onClick={() => navigateBranches(i?.id)}
                    />
                  </td>
                </tr>
              </tbody>
            );
          })}
        </CustomTable>
      ) : (
        <div className="text-center no_result py-5">
          {" "}
          {STRINGS.NO_DATA_AVAILIABLE}
        </div>
      )}

      {data?.length ? (
        <Pagination
          items={data?.length ? data?.length : []}
          setPageCount={setPageCount}
          itemsPerPage={itemsPerPage}
          setItemsPerPage={setItemsPerPage}
          pageCount={pageCount}
        />
      ) : null}
    </div>
  );
}

export default JobTable;
