import {
  LOCUM_USER_DATA,
  NOTIFICATION_COUNT,
  NOTIFICATION_DATA,
  PHARMACY_USER_DATA,
} from "../actions/actionType";

const initialState = {
  notificationData: [],
  notificationCount: 0,
  locumData: [],
  pharmacyData: [],
};

function persistReducer(state = initialState, action) {
  switch (action.type) {
    case NOTIFICATION_DATA:
      return {
        ...state,
        notificationData: action.payload,
      };
      case NOTIFICATION_COUNT:
        return {
          ...state,
          notificationCount: action.payload,
        };

    case PHARMACY_USER_DATA:
      return {
        ...state,
        pharmacyData: action.payload,
      };

    case LOCUM_USER_DATA:
      return {
        ...state,
        locumData: action.payload,
      };
    default:
      return state;
  }
}

export default persistReducer;
