// libs
import React from "react";

// components
import CustomModal from "../CustomModal";
import CustomButton from "../CustomButton";

// consts
import { STRINGS } from "../../Constants";

// assets
import { Images } from "../../Images";

function ConfirmModal({ onClick, toggleModal, show, medium = false, text = "", buttonText = "", icon = "" }) {
  return (
    <>
      <CustomModal
        isOpen={show}
        className="Modal"
        avarge={medium ? false : true}
        medium={medium}
        handleToggle={toggleModal}
      >
        <div className="confirmation_modal text-center">
          <em className="mb-3 modal_icon danger_icon">
            <img src={icon || Images.trashIcon} alt="" width="30" />
          </em>
          <h2 className="h5 mb-2">{STRINGS.ARE_YOU_SURE}</h2>
          <p className="mb-4">{text || STRINGS.WANT_TO_DELETE}</p>
          <CustomButton
              className="btn btn-md btn-primary"
              title={buttonText || STRINGS.DELETE}
              onClick={onClick}
          />
        </div>
      </CustomModal>
    </>
  );
}

export default ConfirmModal;
