import React, { useState, useEffect } from "react";
import "../style.scss";
import LoginForm from "./LoginForm";
import { Images } from "shared/images";
import { LOGIN_FORM, REGEX } from "shared/constants";
import { useSelector } from "react-redux";
import SendEmail from "./sendEmail";

function Login() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [emailErr, setEmailErr] = useState("");
  const [passwordErr, setPasswordErr] = useState("");
  const [rememberMe, setRememberMe] = useState(false);
  const [isForgot, setIsForgot] = useState(false);
  const savedEmail = useSelector((state) => state.auth.email);
  const savedPassword = useSelector((state) => state.auth.password);

  useEffect(() => {
    if (savedEmail?.length) setEmail(savedEmail);
    if (savedPassword?.length) setPassword(savedPassword);
  }, [savedEmail, savedPassword]);

  const validate = (name, value) => {
    switch (name) {
      case LOGIN_FORM.FIELDS.EMAIL.LABEL:
        if (value?.length === 0) {
          setEmail(value);
          setEmailErr("");
        } else if (
          value === "-" ||
          value?.includes("-") ||
          !new RegExp(REGEX.EMAIL).test(value)
        ) {
          setEmailErr(LOGIN_FORM.FIELDS.EMAIL.ERROR);
        } else {
          setEmailErr("");
          setEmail(value);
        }
        break;

      case LOGIN_FORM.FIELDS.PASSWORD.LABEL:
        if (value?.length === 0) {
          setPassword(value);
          setPasswordErr("");
        } else if (
          value === "-" ||
          value?.includes("-") ||
          !new RegExp(REGEX.PASSWORD).test(value)
        ) {
          setPasswordErr(LOGIN_FORM.FIELDS.PASSWORD.ERROR);
        } else {
          setPassword(value);
          setPasswordErr("");
        }
        break;

      default:
        break;
    }
  };

  return (
    <div className="container-fluid overflow-hidden front_form min-vh-100">
      <svg
        width="1402"
        height="602"
        viewBox="0 0 1402 602"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
        className="vector_shape"
      >
        <path
          opacity="0.2"
          d="M16.8781 639H1369.62C1369.62 639 1541.71 168.52 1092.45 281.601C761.422 364.926 653.325 -36.9366 308.495 2.76848C22.4953 35.6956 140.032 299.278 47.5041 289.838C-167.805 267.868 -74.5626 608.116 16.8781 639Z"
          fill="#417C88"
        />
      </svg>
      <div className="w-100">
        <div className="row align-items-center">
          <div className="col-md-6 text-center d-none d-md-flex justify-content-center align-items-center">
            <div className="ilustrator">
              <img src={Images.Illustrator} alt="Locumbay" width="200" />
            </div>
          </div>
          <div className="col-md-6">
            <div className="ls_form">
              <div className="text-center">
                <div className="logo_front">
                  <img src={Images.locumLogo} alt={"Locumbay"} />
                </div>
                <h1 className="h6 text-capitalize">
                  {isForgot ? LOGIN_FORM.FIELDS.FORGOTPASSWORD : LOGIN_FORM.TITLE}
                </h1>
              </div>
              {isForgot ? (
                <SendEmail setIsForgot={setIsForgot} />
              ) : (
                <LoginForm
                  email={email}
                  setEmail={setEmail}
                  password={password}
                  setPassword={setPassword}
                  emailErr={emailErr}
                  rememberMe={rememberMe}
                  setRememberMe={setRememberMe}
                  validate={validate}
                  //passwordErr={passwordErr}
                  setIsForgot={setIsForgot}
                  isForgot={isForgot}
                />
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Login;
