import React, { useCallback, useEffect, useState } from "react";
import "./styles.scss";
import SearchBar from "components/atom/searchBar";
import CustomTable from "components/atom/table";
import { Pagination } from "components/atom/pagination";
import ServicesTable from "./Table";
import { ROUTE_TITLES } from "shared/routeConstants";
import { useDispatch } from "react-redux";
import {
  pharmacyServiceAdd,
  pharmacyServiceDelete,
  pharmacyServiceEdit,
  pharmacyServiceListing,
  pharmacyServiceStatusChange,
} from "redux/actions/auth";
import {
  BUTTONS,
  PHARMACY_SERVICES,
  SNACK_MESSAGES,
  SNACK_VARIANT,
  SORT_KEY,
} from "shared/constants";
import useNotification from "components/atom/snackbar";
import CustomModal from "components/atom/modal";
import ServiceField from "./ServiceField";
import ActivateDeactivate from "components/molecules/activateDeactivate";
import DeleteModal from "components/molecules/deleteModal";

function PharmacyServices() {
  const dispatch = useDispatch();
  const [keyword, setKeyword] = useState("");
  const [msg, sendNotification] = useNotification();
  const [data, setData] = useState([]);
  const [itemsPerPage, setItemsPerPage] = useState(15);
  const [pageCount, setPageCount] = useState(1);
  const [pageItems, setPageItems] = useState(1);
  const [showModal, setShowModal] = useState(false);
  const [modalText, setModalText] = useState(BUTTONS.ADD);
  const [serviceData, setServiceData] = useState({});
  const [multiplePayload, setMultiplePayload] = useState([]);
  const [deleteModal, setDeleteModal] = useState(false);
  const [id, setId] = useState(null);
  const [sortKey, setSortKey] = useState(0);
  const [sortType, setSortType] = useState(0);
  const [toggle, setToggle] = useState({
    isActive: false,
    name: "",
  });

  const fetchData = useCallback(() => {
    let payload = {
      page: pageCount,
      search: keyword,
      limit: itemsPerPage,
      sorting: sortKey,
      sort_type: sortType,
    };

    dispatch(
      pharmacyServiceListing(
        payload,
        (res, type) => {
          setData(res?.data);
          setPageItems(res.count);
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  }, [
    dispatch,
    itemsPerPage,
    keyword,
    pageCount,
    sortKey,
    sortType,
    sendNotification,
  ]);

  useEffect(() => {
    fetchData();
  }, [fetchData, itemsPerPage, pageCount, sortKey, sortType, sendNotification]);

  const addEditServiceAPI = (e, service, id) => {
    e.preventDefault();
    let payload = {
      service_name: service,
    };

    const onSuccess = (res) => {
      fetchData();
      setShowModal(false);
      sendNotification({
        msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
        variant: SNACK_VARIANT.SUCCESS,
      });
    };

    const onError = (err) => {
      setShowModal(false);
      sendNotification({
        msg: err?.data?.message
          ? err.data.message
          : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
        variant: SNACK_VARIANT.ERROR,
      });
    };

    if (modalText === BUTTONS.ADD) {
      dispatch(
        pharmacyServiceAdd(payload, (res) => {
          onSuccess(res);
        }),
        (err) => {
          onError(err);
        }
      );
    } else if (modalText === BUTTONS.UPDATE) {
      const payloadwithId = {
        data: payload,
        id: serviceData?.id,
      };
      dispatch(
        pharmacyServiceEdit(payloadwithId, (res) => {
          onSuccess(res);
        }),
        (err) => {
          onError(err);
        }
      );
    }
  };

  const statusChange = (id, is_active) => {
    let arr = [];
    if (multiplePayload.length && !id) {
      arr = multiplePayload.map((i, idx) => {
        return {
          id: i,
          status: is_active,
        };
      });
    } else {
      arr = [
        {
          id: id,
          status: is_active,
        },
      ];
    }
    let data = {
      services_status_update: arr,
    };
    dispatch(
      pharmacyServiceStatusChange(
        data,
        (res) => {
          fetchData();
          setMultiplePayload([]);
          sendNotification({
            msg: res?.data?.message
              ? res?.data.message
              : SNACK_MESSAGES.SAVED_SUCCESSFULLY,
            variant: SNACK_VARIANT.SUCCESS,
          });
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  const deletePharmacyService = () => {
    let arr = [];
    if (multiplePayload.length && !id) {
      arr = multiplePayload.map((i, idx) => {
        return {
          id: i,
        };
      });
    } else {
      arr = [
        {
          id: id,
        },
      ];
    }

    let data = {
      services: arr,
    };
    dispatch(
      pharmacyServiceDelete(data, (res) => {
        fetchData();
        setMultiplePayload([]);
        setDeleteModal(false);
        setId(null);
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        setDeleteModal(false);
        setId(null);
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  const ServiceModal = () => {
    return (
      <CustomModal
        isOpen={showModal}
        avarge={true}
        handleToggle={() => setShowModal((prev) => !prev)}
      >
        <div className="confirmation_modal">
          <h6 className="h6 text-center text-capitalize mb-4">{`${
            modalText === BUTTONS.UPDATE ? BUTTONS.EDIT : modalText
          } Service`}</h6>
          <ServiceField
            serviceData={serviceData}
            addEditServiceAPI={addEditServiceAPI}
            buttonText={modalText}
          />
        </div>
      </CustomModal>
    );
  };

  const handleSortIcon = () => {
    if (sortType === SORT_KEY.ascending) {
      setSortType(SORT_KEY.descending);
    } else {
      setSortType(SORT_KEY.ascending);
    }
  };

  const showSnack = (message) => {
    sendNotification({
      msg: message ? message : SNACK_MESSAGES.DEFAULT_VALUES,
      variant: SNACK_VARIANT.INFO,
    });
  };

  return (
    <>
      <div className="d-flex  flex-wrap align-items-center justify-content-between">
      <h1 className="h6 mb-3">{ROUTE_TITLES.PHARMACY_SERVICES}</h1>
        <div className="d-flex admin_action flex-wrap">
          <SearchBar
            searchAPI={fetchData}
            keyword={keyword}
            setKeyword={setKeyword}
            setPageCount={setPageCount}
            pageCount={pageCount}
          />
          {multiplePayload.length ? (
            <>
              <ActivateDeactivate
                statusChange={statusChange}
                title={"Select Action"}
              />
              {/* <button
                className="btn btn-sm btn-danger ms-2"
                onClick={() => setDeleteModal(true)}
              >
                {BUTTONS.DELETE}
              </button> */}
            </>
          ) : null}
          {/* <button
            className="btn btn-sm btn-primary ms-2"
            onClick={() => {
              setModalText(BUTTONS.ADD);
              setShowModal(true);
            }}
          >
            {PHARMACY_SERVICES.ADD_SERVICE}
          </button> */}
        </div>
      </div>

      <hr className="divider" />

      <CustomTable>
        <ServicesTable
          data={data}
          statusChange={statusChange}
          setModalText={setModalText}
          setShowModal={setShowModal}
          setServiceData={setServiceData}
          setmultiplePayload={setMultiplePayload}
          multiplepayload={multiplePayload}
          deleteService={() => setDeleteModal(true)}
          setId={setId}
          setSortKey={setSortKey}
          handleSort={handleSortIcon}
          sortType={sortType}
          setSortType={setSortType}
          setToggle={setToggle}
          toggle={toggle}
          setPageCount={setPageCount}
          showSnack={showSnack}
          pageCount={pageCount}
          itemsPerPage={itemsPerPage}
        />
      </CustomTable>

      {data?.length ? (
        <Pagination
          items={pageItems}
          setPageCount={setPageCount}
          itemsPerPage={itemsPerPage}
          setItemsPerPage={setItemsPerPage}
          pageCount={pageCount}
        />
      ) : null}
      <ServiceModal />
      <DeleteModal
        show={deleteModal}
        toggleModal={()=> setDeleteModal(prev => !prev)}
        onClick={deletePharmacyService}
      />
    </>
  );
}

export default PharmacyServices;
