import React, { useEffect } from "react";
import { useState } from "react";
import { Pagination } from "../../../components/atom/pagination/index";
import "./style.scss";
import { useDispatch, useSelector } from "react-redux";
import { clearNotification, getNotificationListing } from "redux/actions/auth";
import useNotification from "components/atom/snackbar";
import { SNACK_MESSAGES, SNACK_VARIANT, NA } from "shared/constants";
import { firstCharacter, timeAgo } from "components/utility";
import { setNotificationCount, setNotificationData } from "redux/actions/common";

function NotificationList() {
  //state variables
  const dispatch = useDispatch();
  const [msg, sendNotification] = useNotification();
  const notificationData = useSelector(
    (state) => state.persist.notificationData
  );
  //const [notificationData, setNotificationData] = useState([]);
  const [itemsPerPage, setItemsPerPage] = useState(15);
  const [pageCount, setPageCount] = useState(1);
  const [pageItems, setPageItems] = useState(1);

  //fetch  notification list from api response
  const getNotification = () => {
    let payload = {
      page: pageCount,
      limit: itemsPerPage,
    };

    dispatch(
      getNotificationListing(
        payload,
        (res) => {
          setPageItems(res.count);
          // setNotificationData(res?.data);
          dispatch(setNotificationData(res?.data));
        },
        (err) => {
          //onError Message for send notifications
          sendNotification({
            msg: err.data?.message
              ? err.data?.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  useEffect(() => {
    dispatch(
      clearNotification(
        "",
        (res) => {
          dispatch(setNotificationCount(0));
        },
        (err) => {
          sendNotification({
            msg: err.data?.message
              ? err.data?.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  }, [notificationData]);

  //when page changes or items per page changes , fetch data with updated values
  useEffect(() => {
    getNotification();
  }, [pageCount, itemsPerPage]);

  return (
    <div className="notification_sec">
      <div className="d-flex">
        <h6 className="h6 mb-0">Notifications</h6>
      </div>
      <hr className="divider mb-4" />
      {notificationData?.length ? (
        notificationData.map((i, idx) => {
          return (
            <div key={idx} className="notification_list">
              <span className="name text-capitalize">
                {i?.sender?.first_name
                  ? firstCharacter(i?.sender?.first_name, 1)
                  : i?.sender?.last_name
                  ? firstCharacter(i?.sender?.last_name, 1)
                  : "U"}
              </span>
              <h5 className="text-bold">{i?.notif_for} </h5>
              <p>{i?.notif_text}</p>
              <div className="d-flex align-items-center user_time">
                <small className="user_time">
                  <em className="me-1">
                    <svg
                      width="12"
                      height="12"
                      viewBox="0 0 12 12"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <g opacity="0.5">
                        <path
                          d="M6.00021 0C2.68638 0 0 2.68574 0 6C0 9.3134 2.6866 12 6.00021 12C9.31362 12 11.9998 9.3134 11.9998 6C11.9997 2.68574 9.31362 0 6.00021 0ZM6.00021 10.639C3.43748 10.639 1.35969 8.56188 1.35969 6C1.35969 3.43727 3.43759 1.36012 6.00021 1.36012C8.56252 1.36012 10.6401 3.43727 10.6401 6C10.6401 8.56188 8.56263 10.639 6.00021 10.639Z"
                          fill="#104651"
                        />
                        <path
                          d="M6.67978 2.72705H5.32043V6.67991L8.67327 8.09859L9.20506 6.84615L6.67961 5.7746L6.67978 2.72705Z"
                          fill="#104651"
                        />
                      </g>
                    </svg>
                  </em>
                  <span>{i?.created_at ? timeAgo(i?.created_at) : NA}</span>
                </small>
                <small className="user_time">
                  <em className="me-1">
                    <svg
                      width="10"
                      height="10"
                      viewBox="0 0 10 10"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <g opacity="0.5">
                        <path
                          d="M5 0C3.61718 0 2.5 1.00782 2.5 2.73429C2.5 4.46075 3.42968 6.25 5 6.25C6.71093 6.25 7.50001 4.4609 7.50001 2.73429C7.50001 1.00768 6.38283 0 5 0ZM2.5 6.25C1.11718 6.25 0 7.36718 0 8.75001V9.88283C0 9.94533 0.0546875 10 0.117188 10H9.88291C9.94541 10 10.0001 9.94533 10.0001 9.88283V8.75001C10.0001 7.36718 8.88292 6.25 7.5001 6.25H6.86777C6.80527 6.25 6.71006 6.28174 6.65536 6.32081C6.17879 6.68018 5.61734 6.875 5.00002 6.875C4.41409 6.875 3.8672 6.68018 3.39845 6.32081C3.34376 6.28174 3.25873 6.25 3.18841 6.25H2.49986H2.5Z"
                          fill="#104651"
                        />
                      </g>
                    </svg>
                  </em>{" "}
                  <span>
                    {i?.sender?.first_name || i?.sender?.last_name
                      ? i?.sender?.first_name + " " + i?.sender?.last_name
                      : "Unknown"}
                  </span>
                </small>
              </div>
            </div>
          );
        })
      ) : (
          <div className="d-flex justify-content-center">
            <span> {"No new notifications"}</span>
          </div>
      )}

      {notificationData?.length ? (
        <Pagination
          items={pageItems}
          setPageCount={setPageCount}
          itemsPerPage={itemsPerPage}
          setItemsPerPage={setItemsPerPage}
          pageCount={pageCount}
        />
      ) : null}
    </div>
  );
}

export default NotificationList;
