// libs
import React from "react";
import Modal from "react-modal";

// assets
import { Images } from "../../Images";

function CustomModal(
    {
        children,
        className = "",
        handleToggle = () => {},
        isOpen = false,
        large = false,
        medium = false,
        small = false,
        avarge = false,
        xLarge = false,
        style = {},
        clearHandler = () => {},
        modalOpacity = false,
        bodyClassname = "modal-body",
        shouldCloseOnOverlayClick = false,
        overlayClassName = "Overlay",
    },
    props,
) {
    return (
        <>
            <Modal
                isOpen={isOpen}
                onRequestClose={handleToggle}
                className={className}
                overlayClassName={overlayClassName}
                ariaHideApp={false}
                shouldCloseOnOverlayClick={shouldCloseOnOverlayClick}
            >
                <div
                    className={`modal-dialog modal-dialog-centered ${
                        large
                            ? `modal-lg`
                            : medium
                            ? `modal-md`
                            : avarge
                            ? `modal-mds`
                            : small
                            ? `small`
                            : xLarge
                            ? `modal-xl`
                            : "modal-md"
                    }`}
                >
                    <div className="modal-content" hidden={modalOpacity ? modalOpacity : false}>
                            <button
                                className="btn-close btn-transparent"
                                onClick={() => {
                                    handleToggle();
                                    clearHandler();
                                }}
                            >
                                <img src={Images.closeIcon} alt="Close" width="24" />
                            </button>
                        <div className={`${bodyClassname}`}>{children}</div>
                    </div>
                </div>
            </Modal>
        </>
    );
}

export default CustomModal;
