import React, { useCallback, useEffect, useState } from "react";
import { Images } from "shared/images";
import CustomDropdown from "components/atom/dropdown";
import { useHistory } from "react-router-dom";
import { ROUTE_CONSTANTS } from "shared/routeConstants";
import "./style.scss";
import {
  TruncateString,
  convertDateTimeToTime,
  toggleClasses,
} from "components/utility";
import CheckClickOutside from "components/atom/checkOutsideClick/checkOutsideClick";
import { useDispatch, useSelector } from "react-redux";
import { URL_HEAD } from "shared/api";
import { SNACK_MESSAGES, SNACK_VARIANT, VERSION } from "shared/constants";
import {
  clearAllNotifications,
  clearNotification,
  getNotificationListing,
  getUnreadNotificationListing,
} from "redux/actions/auth";
import useNotification from "components/atom/snackbar";
import {
  setNotificationCount,
  setNotificationData,
} from "redux/actions/common";

function Header({
  profileData,
  sidebarToggle,
  SetSidebarToggle,
  socketListener,
  notifyToggle,
  setnotifyToggle,
}) {
  const history = useHistory();
  const dispatch = useDispatch();
  const [msg, sendNotification] = useNotification();
  const adminDetails = useSelector((state) => state.auth.adminDetails);
  const [showProfileDrop, setShowProfileDrop] = useState(false);
  const [showNotificationDrop, setShowNotificationDrop] = useState(false);
  const [image, setImage] = useState("");
  const [date, setDate] = useState(Date.now());
  const [unreadNotificationData, setUnreadNotificationData] = useState([]);
  const notificationCount = useSelector(
    (state) => state.persist.notificationCount
  );

  const toggleSidebar = useCallback(() => {
    toggleClasses("main", "slide-active", sidebarToggle ? "add" : "remove");
  }, [sidebarToggle]);

  useEffect(() => {
    toggleSidebar();
  }, [toggleSidebar]);

  useEffect(() => {
    setImage(adminDetails?.photo);
  }, [adminDetails]);

  useEffect(() => {
    toggleClasses("profile_info", "active", showProfileDrop ? "add" : "remove");
  }, [showProfileDrop]);

  useEffect(() => {
    toggleClasses(
      "noficaition_btn",
      "active",
      showNotificationDrop ? "add" : "remove"
    );
  }, [showNotificationDrop]);

  const getNotification = () => {
    let payload = {
      page: 1,
      limit: 15,
    };

    dispatch(
      getNotificationListing(
        payload,
        (res) => {
          setUnreadNotificationData(res?.data);
          dispatch(setNotificationData(res?.data));
        },
        (err) => {
          //onError Message for send notifications
          sendNotification({
            msg: err.data?.message
              ? err.data?.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  const getNotificationCount = () => {
    dispatch(
      getUnreadNotificationListing(
        "",
        (res) => {
          getNotification();
          setnotifyToggle(false);
        },
        (err) => {
          sendNotification({
            msg: err.data?.message
              ? err.data?.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  const onClearNotification = () => {
    dispatch(
      clearNotification(
        "",
        (res) => {
          getNotificationCount();
        },
        (err) => {
          sendNotification({
            msg: err.data?.message
              ? err.data?.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  const onClearAllNotifications = () => {
    dispatch(
      clearAllNotifications(
        "",
        (res) => {
          dispatch(setNotificationCount(0));
          getNotification();
        },
        (err) => {
          sendNotification({
            msg: err.data?.message
              ? err.data?.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  useEffect(() => {
    if (notifyToggle) {
      getNotificationCount();
    }
  }, [notifyToggle]);

  return (
    <>
      <header className="header">
        <div className="container-fluid px-3 px-md-4">
          <div className="d-flex flex-wrap align-items-center justify-content-between">
            <div className="d-flex align-items-center col_lft">
              <div
                className="logo_icon me-3"
                onClick={() => history.push(ROUTE_CONSTANTS.DASHBOARD)}
              >
                <img
                  src={Images.locumLogoIcon}
                  alt="Locumbay"
                  width={40}
                  className="w-100"
                />
              </div>
              <button
                className="hamburger me-2 me-lg-4"
                onClick={() => {
                  SetSidebarToggle((prev) => !prev);
                }}
              >
                <img src={Images.hamburger} alt="hamburger" width="20" />
              </button>
              <label> {VERSION ? "v" + VERSION : ""}</label>
            </div>
            <div className="d-flex align-items-center col_rht">
              <CheckClickOutside onClick={() => setShowNotificationDrop(false)}>
                <div className="position-relative notification_drop">
                  <button
                    className="noficaition_btn"
                    onClick={() => {
                      setShowNotificationDrop((prev) => !prev);
                    }}
                  >
                    <img src={Images.notification} alt="" />
                    <span className="badge">
                      {notificationCount
                        ? notificationCount > 99
                          ? "99+"
                          : notificationCount
                        : 0}
                    </span>
                  </button>
                  <CustomDropdown
                    setShow={setShowNotificationDrop}
                    show={showNotificationDrop}
                  >
                    <div className="d-flex  flex-wrap align-items-center justify-content-center dropdown-header">
                      <h6 className="h6 mb-0">Notifications</h6>
                      {unreadNotificationData.length ? (
                        <button
                          className="btn"
                          onClick={() => onClearAllNotifications()}
                        >
                          Clear all
                        </button>
                      ) : null}
                    </div>

                    <ul className="list-unstyled dropdown-body">
                      {unreadNotificationData?.length ? (
                        unreadNotificationData.map((i, idx) => {
                          return (
                            <li key={idx}>
                              <h6 className="h6">{i.notif_for}</h6>
                              <span>{i.notif_text} </span>
                              <span>
                                {convertDateTimeToTime(i?.updated_at)}
                              </span>
                            </li>
                          );
                        })
                      ) : (
                        <div className="d-flex justify-content-center">
                          <span> {"No new notifications"}</span>
                        </div>
                      )}
                    </ul>

                    <div className="dropdown-footer text-center">
                      <button
                        className="btn btn-sm btn-primary"
                        onClick={() => {
                          history.push(ROUTE_CONSTANTS.NOTIFICATION_LIST);
                          setShowNotificationDrop(false);
                          onClearNotification();
                        }}
                      >
                        View all
                      </button>
                    </div>
                  </CustomDropdown>
                </div>
              </CheckClickOutside>
              <div className="position-relative d-flex align-items-center ms-3 profile_info">
                <div className="avtar avtar50">
                  <img
                    src={image ? URL_HEAD + image : Images.defaultUserPic}
                    alt="Locumbay"
                    width={55}
                  />
                </div>

                <div className="author_info ms-2 d-none d-lg-block">
                  <h6 className="h6 text-capitalize">
                    {TruncateString(
                      adminDetails?.first_name + " " + adminDetails?.last_name,
                      12,
                      12
                    )}
                  </h6>
                  <small>Admin</small>
                </div>

                <CheckClickOutside onClick={() => setShowProfileDrop(false)}>
                  <button
                    className="p-0 btn-transparent arrow_down"
                    onClick={() => {
                      setShowProfileDrop((prev) => !prev);
                    }}
                  >
                    <img src={Images.arrowDown} alt="" width={20} />
                  </button>
                  <CustomDropdown
                    setShow={setShowProfileDrop}
                    show={showProfileDrop}
                  >
                    <div className="author_info ms-2 pt-3 px-3 d-lg-none text-center">
                      <h6 className="h6 text-capitalize">
                        {adminDetails?.first_name +
                          " " +
                          adminDetails?.last_name}
                      </h6>
                      <small>Admin</small>
                      <hr className="mb-0 d-lg-none" />
                    </div>
                    <div className="user-body">
                      {profileData && profileData?.length > 0
                        ? profileData.map((i, idx) => {
                            return (
                              <div
                                key={idx}
                                className="dropDown-list"
                                onClick={() => {
                                  i.onClick();
                                  setShowProfileDrop((prev) => !prev);
                                }}
                              >
                                <em>
                                  <img
                                    src={i?.image}
                                    className="img-fluid"
                                    alt="icon"
                                    width={20}
                                  />
                                </em>
                                <span>{i.title} </span>
                              </div>
                            );
                          })
                        : null}
                    </div>
                  </CustomDropdown>
                </CheckClickOutside>
              </div>
            </div>
          </div>
        </div>
      </header>
    </>
  );
}

export default Header;
