import CustomTable from "components/atom/table";
import VerifiedLogo from "components/atom/verifiedIcon";
import { getLocalizedDateString } from "components/utility";
import React from "react";
import { NA, PHARMACY_USER_VIEW, STRINGS } from "shared/constants";
import { Images } from "shared/images";
import { INVOICE_JOB_TABLE } from "shared/tableData";

function BasicInvoiceView({ data, apiData }) {
  const invoice_Extras = [
    {
      title: "Lunch",
      value: JSON.stringify(apiData?.extras?.lunch)?.length
        ? JSON.stringify(apiData?.extras?.lunch)
        : NA,
    },
    {
      title: "Mileage to be paid for travel",
      value: JSON.stringify(apiData?.extras?.mileage_to_be_paid)?.length
        ? JSON.stringify(apiData?.extras?.mileage_to_be_paid)
        : NA,
    },
    {
      title: "Total (Before tax)",
      value: JSON.stringify(apiData?.extras?.total_before_taxes)?.length
        ? JSON.stringify(apiData?.extras?.total_before_taxes)
        : NA,
    },
    {
      title: "Taxes",
      value: JSON.stringify(apiData?.extras?.taxes)?.length
        ? JSON.stringify(apiData?.extras?.taxes)
        : NA,
    },
    {
      title: "Total",
      value: JSON.stringify(apiData?.extras?.total)?.length
        ? JSON.stringify(apiData?.extras?.total)
        : NA,
    },
  ];

  return (
    <div className="tab_content">
      <div className="card p-3">
        <div className="grey-bg rounded px-4 py-3 mb-4">
          <div className="row invoice_send_info">
            {data?.length
              ? data?.map((i, idx) => {
                  return (
                    <div className="col-md-6 my-2">
                      <h6 className="h6">{i?.title ? i?.title : NA}</h6>
                      <span className="d-block">
                        {i?.value ? i?.value : NA}
                        {Object(i).hasOwnProperty("isVerified") ? (
                          <VerifiedLogo isVerified={i?.isVerified} />
                        ) : null}
                      </span>
                    </div>
                  );
                })
              : null}
          </div>
        </div>

        <div className="grey-bg rounded p-3 mb-4">
          <h6 class="h6">{PHARMACY_USER_VIEW.COMPLETED_JOBS}</h6>
          <CustomTable>
            <thead>
              <tr key={1000 + 1}>
                {Object.values(INVOICE_JOB_TABLE).map((i, idx) => (
                  <th scope="col" id={idx}>
                    {i}
                  </th>
                ))}
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{1}</td>
                <td>
                  {apiData.jobDetails?.job_id ? apiData.jobDetails?.job_id : NA}
                </td>
                <td>
                  {apiData?.jobDetails?.invoice_id
                    ? apiData?.jobDetails?.invoice_id
                    : NA}
                </td>
                <td>
                  {apiData?.jobDetails?.job_date
                    ? getLocalizedDateString(apiData?.jobDetails?.job_date)
                    : NA}
                </td>
                <td>
                  {apiData?.jobDetails?.hourly_fee
                    ? apiData?.jobDetails?.hourly_fee
                    : NA}
                </td>
                <td>
                  {apiData?.jobDetails?.no_of_hours
                    ? apiData?.jobDetails?.no_of_hours
                    : NA}
                </td>
                <td>
                  {apiData?.jobDetails?.amount
                    ? apiData?.jobDetails?.amount
                    : NA}
                </td>
              </tr>
            </tbody>
          </CustomTable>
        </div>

        <div className="grey-bg rounded py-4 px-3 mb-4">
          <h6 className="h6">{PHARMACY_USER_VIEW.EXTRAS}</h6>
          <div className="row justify-content-center">
            <div className="col-md-8 col-lg-7">
              <div className="bg-white rounded py-3 px-lg-5">
                <div class="table-responsive">
                  <table class="table-white">
                    <tbody>
                      {invoice_Extras ? (
                        invoice_Extras?.map((i, idx) => {
                          return (
                            <>
                              <tr id={idx}>
                                <td>{i?.title ? i?.title : NA} :</td>
                                <th className="text-end">
                                  {JSON.stringify(i?.value)?.length ? i?.value : NA}
                                </th>
                              </tr>
                            </>
                          );
                        })
                      ) : (
                        <tr>
                          <td
                            colSpan={
                              Object.values(INVOICE_JOB_TABLE).length + 1
                            }
                            className="no-result text-center py-5"
                          >
                            {STRINGS.NO_DATA_AVAILIABLE}
                          </td>
                        </tr>
                      )}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="px-3 pb-3">
          <h6 className="h6 mb-3">
            {PHARMACY_USER_VIEW.LOCUM_TERMS_CONDITIONS}
          </h6>
          <ul className="ms-3 mb-0">
            <li>
              The above independent contractor is a registered pharmacist in the
              UK and has performed their duties as a pharmacist in accordance to
              the specified pharmacies Standard Operating Procedures. Any taxes
              or National Insurance payments required for the above income will
              be paid independently by the above named person.
            </li>
            <li>
              Invoices must be authorised by branch manager before being paid by
              accounts. This usually takes 14-28 days, however, it can be
              delayed in some circumstances.
            </li>
            <li>
              Claims made after six months from date worked will incur an admin
              fee. Any claims made after 12 months will not be processed. No
              cash payment, locums will be paid direct into their account.
            </li>
          </ul>
        </div>
      </div>

      {/* <div class="d-flex btn_group mt-3">
        <button
          className="btn btn-md btn-primary"
          onClick={() => window.print()}
        >
          Download
        </button>
        <button
          className="btn btn-md btn-primary"
          onClick={() => window.print()}
        >
          Print
        </button>
      </div> */}
    </div>
  );
}

export default BasicInvoiceView;
