import React, { useCallback, useEffect, useState } from "react";
import { ROUTE_CONSTANTS } from "../../../../shared/routeConstants";
import { useHistory, useLocation } from "react-router-dom";
import {
  BRANCHES_VIEW,
  FILTER_TYPES,
  NA,
  PHARMACY_DETAILS,
  PHARMACY_USER_VIEW,
  SNACK_MESSAGES,
  SNACK_VARIANT,
  SORT_KEY,
  TAB_BRANCHES_VIEW,
  TAB_PHARMACY_USER_DATA,
  USERS_CONSTS,
  VACANCY_TYPE,
} from "shared/constants";
import NavTab from "components/atom/tabNav";
import BasicView from "./BasicView";
import { useDispatch } from "react-redux";
import useNotification from "components/atom/snackbar";
import {
  branchViewApi,
  deleteBranchsApi,
  deleteVacancy,
  pharmacyVacancy,
} from "redux/actions/auth";
import BreadCrumb from "components/atom/breadcrum";
import VacanciesTable from "components/molecules/vacancyTable";
import Filters from "components/atom/filter";
import SearchBar from "components/atom/searchBar";
import FiltersCustom from "components/atom/chatComponents/Filter";
import moment from "moment";
import DeleteModal from "components/molecules/deleteModal";

function BranchesView({}) {
  // Importing required hooks and functions from React.
  const history = useHistory();
  const dispatch = useDispatch();
  const [deleteModal, setDeleteModal] = useState(false);
  // Retrieving the id of the branch from the location object passed through the route.
  const idParam = useLocation();
  const [multiplePayload, setMultiplePayload] = useState([]);
  const [fetchedVacancyData, setFetchedVacancyData] = useState({});
  const [itemsPerPage, setItemsPerPage] = useState(15);
  const [pageCount, setPageCount] = useState(1);
  const [pageItems, setPageItems] = useState(1);
  const [sortKey, setSortKey] = useState(0);
  const [sortType, setSortType] = useState(0);
  const [multiSelection, setMultipSelection] = useState(false);
  const [toggle, setToggle] = useState({
    isActive: false,
    name: "",
  });

  const [filterData, setFilterData] = useState({
    country: "",
    state: "",
    selectedDate: {
      from: null,
      to: new Date(""),
    },
    rateRange: [0, 0],
  });

  const [viewDoc, setViewDoc] = useState(false);
  const [showReasonModal, setShowReasonModal] = useState(false);
  const [docViewerParams, setDocViewerParams] = useState([]);
  const [modalOpacity, setmodalOpacity] = useState(false);
  const [reasonUnverify, setReasonUnverify] = useState("");
  const [jobID, setJobID] = useState();
  const [city, setCity] = useState("");
  const [country, setCountry] = useState("");
  const [priceRange, setPriceRange] = useState([0, 10]);
  const [toggleFilter, setToggleFilter] = useState(false);
  const [dateRange, setDateRange] = useState({
    startDate: new Date(),
    endDate: new Date(),
    key: "selection",
  });

  useEffect(() => {
    if (!toggleFilter) {
      setCity("");
      setCountry("");
      setDateRange({
        startDate: new Date(),
        endDate: new Date(),
        key: "selection",
      });
      setPriceRange([0, 10]);
    }
  }, [toggleFilter]);

  // State variables for the branch details and notification messages.
  const [activeItem, setActiveItem] = useState(1);
  const id = idParam?.state?.id;
  const [viewData, setViewData] = useState({});
  const [keyword, setKeyword] = useState("");
  const [msg, sendNotification] = useNotification();
  const BASIC_CONST = [
    {
      title: "Pharmacy Name",
      value: viewData?.pharmacy_name ? viewData?.pharmacy_name : NA,
      isVerified:
        parseInt(viewData?.verified_document) > 100
          ? true
          : parseInt(viewData?.verified_document),
      navigate: () => history.push(ROUTE_CONSTANTS.PHARMACY_VIEW, { id: viewData?.manager_info?.pharmacy }),
    },
    {
      title: "Branch Name",
      value: viewData?.branch_name ? viewData?.branch_name : NA,
    },
    {
      title: "Address",
      value: viewData?.address ? viewData?.address : NA,
    },
    {
      title: "Phone",
      value: viewData?.phone_number ? "+" + viewData?.phone_number : NA,
    },
    {
      title: "Branch Email",
      value: viewData?.branch_email ? viewData?.branch_email : NA,
      isVerified: viewData?.verified,
    },
    {
      title: "Pharmacy Email",
      value: viewData?.pharmacy_email ? viewData?.pharmacy_email : NA,
      isVerified: viewData?.pharmacy_verified,
    },
    {
      title: "Manager Name",
      value: viewData?.manager_info?.manager_name
        ? viewData?.manager_info?.manager_name
        : NA,
    },
    {
      title: "Manager Phone",
      value: viewData?.manager_info?.manager_phone
        ? viewData?.manager_info?.manager_phone
        : NA,
    },
    {
      title: "Manager Email",
      value: viewData?.manager_info?.manager_email
        ? viewData?.manager_info?.manager_email
        : NA,
    },
    {
      title: "Number of Staff",
      value: viewData?.number_of_staff ? viewData?.number_of_staff : NA,
    },
  ];

  const deleteBranch = (id) => {
    // Creating an object with the id of the current branch to be deleted.
    let data = {
      branches: [
        {
          id: id,
        },
      ],
    };
    // Dispatching an API call to delete the branch from the database.
    dispatch(
      deleteBranchsApi(data, (res) => {
        // If the deletion is successful, displaying a success notification message.
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        // If the deletion is unsuccessful, displaying an error notification message.
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  const onDelete = () => {
    let arr = [];
    if (multiplePayload.length && !jobID) {
      arr = multiplePayload.map((i, idx) => {
        return i;
      });
    } else {
      arr = [jobID];
    }

    let data = {
      vacancy_list: arr,
    };
    dispatch(
      deleteVacancy(data, (res) => {
        fetchVacancyList();
        setDeleteModal(false);
        setMultiplePayload([]);
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        setDeleteModal(false);
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };
  const handleSortIcon = () => {
    if (sortType === SORT_KEY.ascending) {
      setSortType(SORT_KEY.descending);
    } else {
      setSortType(SORT_KEY.ascending);
    }
  };

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const fetchVacancyList = (isResetting = false) => {
    let payload = {
      page: pageCount,
      search: keyword,
      limit: itemsPerPage,
      sorting: sortKey,
      sort_type: sortType,
      branch_id: id,
      country: isResetting == true ? "" : filterData.country,
      state: isResetting == true ? "" : filterData.state,
      ...(filterData.selectedDate.from && {
        start_date:
          isResetting == true
            ? ""
            : filterData.selectedDate.from
            ? moment(filterData.selectedDate.from).format("YYYY-MM-DD")
            : "",
      }),
      ...(filterData.selectedDate.from && {
        end_date:
          isResetting == true
            ? ""
            : filterData.selectedDate.to
            ? moment(filterData.selectedDate.to).format("YYYY-MM-DD")
            : "",
      }),
      start_rate: isResetting == true ? "" : filterData.rateRange[0] || "",
      ...(filterData.rateRange[1] > 0 && {
        end_rate: isResetting == true ? "" : filterData.rateRange[1] || "",
      }),
    };
    dispatch(
      pharmacyVacancy(
        payload,
        (res) => {
          setFetchedVacancyData(res?.data);
          setPageItems(res.count);
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  useEffect(() => {
    if (BRANCHES_VIEW.VACANCIES.ID === activeItem) fetchVacancyList(true);
  }, [sortKey, sortType, activeItem, itemsPerPage, pageCount, keyword]);

  const dataSwitch = () => {
    switch (activeItem) {
      case BRANCHES_VIEW.BASIC_BRANCH_INFO.ID:
        return <BasicView data={BASIC_CONST} apiData={viewData} />;
      case BRANCHES_VIEW.VACANCIES.ID:
        return (
          <VacanciesTable
            multiplepayload={multiplePayload}
            setmultiplePayload={setMultiplePayload}
            navigate={(id) =>
              history.push(ROUTE_CONSTANTS.VACANCIES_VIEW, {
                id: id,
                type: VACANCY_TYPE.BRANCHES,
              })
            }
            data={fetchedVacancyData}
            onDelete={onDelete}
            setSortType={setSortType}
            itemsPerPage={itemsPerPage}
            setItemsPerPage={setItemsPerPage}
            pageCount={pageCount}
            setPageCount={setPageCount}
            sortKey={sortKey}
            setSortKey={setSortKey}
            handleSort={handleSortIcon}
            sortType={sortType}
            pageItems={pageItems}
            setPageItems={setPageItems}
            multiSelection={multiSelection}
            setMultipSelection={setMultipSelection}
            setToggle={setToggle}
            toggle={toggle}
            type={VACANCY_TYPE.BRANCHES}
            setId={setJobID}
          />
        );
      default:
    }
  };

  // Function to retrieve the details of the current branch from the database.
  const viewUser = useCallback(() => {
    // Creating an object with the id of the current branches to be retrieved.
    let data = { id: id };
    // Dispatching an API call to retrieve the branches details from the database.
    dispatch(
      branchViewApi(data, (res) => {
        // If the retrieval is successful, updating the state variable with the branches details.
        setViewData(res?.data);
      }),
      (err) => {
        // If the retrieval is unsuccessful, displaying an error notification message and navigating back to the previous page.
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
        history.goBack();
      }
    );
  }, [id, dispatch, sendNotification, history]);

  // useEffect hook to retrieve the branches details on page load
  useEffect(() => {
    viewUser();
  }, [viewUser]);

  const handleFetchDataForFilter = () => {
    setPageCount(1);
    fetchVacancyList();
  };

  const resetFilter = () => {
    setPageCount(1);
    fetchVacancyList(true);
    setFilterData({
      country: "",
      state: "",
      selectedDate: {
        from: null,
        to: new Date(""),
      },
      rateRange: [0, 0],
    });
  };

  const handleChangeFilterData = (key, value) => {
    setFilterData((prevFilterData) => ({ ...prevFilterData, [key]: value }));
  };

  useEffect(() => {
    if (!toggleFilter) resetFilter();
  }, [toggleFilter]);

  return (
    <>
      <div className="d-flex flex-wrap align-items-center justify-content-between">
        <BreadCrumb
          onClick={() =>
            history.push(ROUTE_CONSTANTS.USERS, {
              id: USERS_CONSTS.PHARMACY_USERS.ID,
            })
          }
          previousLink={PHARMACY_USER_VIEW.PHARMACY_USERS}
          currentLink={PHARMACY_USER_VIEW.DETAILS}
        />
      </div>
      <div className="d-flex align-items-center flex-wrap justify-content-between tab_full">
        <NavTab
          onClick={(id) => {
            setActiveItem(id);
            resetFilter();
            setKeyword("");
            setToggleFilter(false);
          }}
          tabsData={TAB_BRANCHES_VIEW}
          activeTab={activeItem}
        />

        {BRANCHES_VIEW.VACANCIES.ID === activeItem ? (
          <div className="d-flex admin_action flex-wrap">
            {multiplePayload.length ? (
              <button className={`btn btn-danger`} onClick={onDelete}>
                Delete
              </button>
            ) : null}

            <SearchBar
              searchAPI={() => fetchVacancyList()}
              keyword={keyword}
              setKeyword={setKeyword}
              setPageCount={setPageCount}
              filter={true}
              pageCount={pageCount}
              toggle={toggleFilter}
              setToggle={setToggleFilter}
              reset={resetFilter}
            />
          </div>
        ) : null}
      </div>
      {BRANCHES_VIEW.VACANCIES.ID === activeItem ? (
        <>
          <FiltersCustom
            toggle={toggleFilter}
            filterData={filterData}
            reset={resetFilter}
            handleChangeFilterData={handleChangeFilterData}
            handleFetchDataForFilter={handleFetchDataForFilter}
            type={VACANCY_TYPE.BRANCHES}
          />
          <hr className="divider" />
        </>
      ) : null}
      {dataSwitch()}
      <DeleteModal
        show={deleteModal}
        toggleModal={()=> setDeleteModal(prev => !prev)}
        onClick={onDelete}
      />
    </>
  );
}

export default BranchesView;
