import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";

import { updateAuthToken } from "shared/axios";
import AppLayout from "components/core/AppLayout";
import { AUTH_ROUTES } from "./AuthRoutes";
import { PUBLIC_ROUTES } from "./PublicRoutes";
import { PRIVATE_ROUTES } from "./PrivateRoutes";
import DocumentTitle from "./DocumentTitle";
import PublicLayout from "components/core/PublicLayout";
import PrivateLayout from "components/core/PrivateLayout";
import RenderRoutes from "./RenderRoutes";
import HomeLoader from "components/atom/loader";
import socketInstance from "services/socketManagerIO";
import { setSocketInstance } from "redux/actions/socket";
import useSocket from "views/authenticated/Messages/hooks/useSocket";
import { API } from "shared/api";
import SocketContext from "shared/SocketContext";
const DEFAULT_AUTHENTICATED_ROUTE = "/dashboard";
const DEFAULT_GUEST_ROUTE = "/login";

const GuestRoutes = () => {
  return (
    <Switch>
      <Route exact path={AUTH_ROUTES.map((route) => route.path)}>
        <RenderRoutes routes={AUTH_ROUTES} />
      </Route>
      <Route exact path={PUBLIC_ROUTES.map((route) => route.path)}>
        <PublicLayout>
          <RenderRoutes routes={PUBLIC_ROUTES} />
        </PublicLayout>
      </Route>
      <Redirect from="*" to={DEFAULT_GUEST_ROUTE} />
    </Switch>
  );
};

const AuthenticatedRoutes = () => {
  const routes = PUBLIC_ROUTES.concat(PRIVATE_ROUTES);
  return (
    <PrivateLayout>
      <Switch>
        <Route path={routes.map((route) => route.path)}>
          <RenderRoutes routes={routes} />
        </Route>
        <Redirect from="*" to={DEFAULT_AUTHENTICATED_ROUTE} />
      </Switch>
    </PrivateLayout>
  );
};

const RootRouter = () => {
  const dispatch = useDispatch();
  const token = useSelector((state) => state.auth.token);
  const loading = useSelector((state) => state.loading.loading);
  const socketManager = useSelector((state) => state.socket.socketInstance);
  const adminDetails = useSelector((state) => state.auth.adminDetails);
  const { socket, socketError } = useSocket(
    API.SOCKET_IO,
    adminDetails?.user_id,
    token
  );
  updateAuthToken(token);
  const baseName = process.env.REACT_APP_BASE_NAME;
  const isAuthenticated = !!token;

  // useEffect(() => {
  //   if (token) {
  //     (async()=>await webSocket.establishConnection())()
  //     console.log(webSocket,"CHECK 1");
  //     console.log(webSocket,"CHECK 2");
  //     console.log(socketManager,"CHECK 4")
  //   }
  // }, [token, dispatch]);

  // useEffect(() => {
  //   if (token) {
  //     console.log("establishing Connection");
  //     socketInstance.establishConnection();
  //     dispatch(setSocketInstance(socketInstance));
  //   }
  //   return () => {
  //     token && socketInstance.disconnectConnection();
  //   };
  // }, [token]);

  return (
    <SocketContext.Provider value={{ socket, socketError }}>
      <BrowserRouter basename={baseName}>
        <HomeLoader visible={loading}>
          <DocumentTitle isAuthenticated={isAuthenticated} />
          <AppLayout isAuthenticated={isAuthenticated}>
            {token ? (
              <>
                <AuthenticatedRoutes />
              </>
            ) : (
              <GuestRoutes />
            )}
          </AppLayout>
        </HomeLoader>
      </BrowserRouter>
    </SocketContext.Provider>
  );
};

export default RootRouter;
