import React, { useEffect, useState } from "react";
import "./styles.scss";
import NavTab from "components/atom/tabNav";
import {
  BUTTONS,
  SNACK_MESSAGES,
  SNACK_VARIANT,
  STRINGS,
  TAB_EMAIL_TEMP_DATA,
} from "shared/constants";
import useNotification from "components/atom/snackbar";
import { useDispatch } from "react-redux";
import { useCallback } from "react";
import { editEmailTemplate, fetchEmailTemplate } from "redux/actions/auth";
import EmailField from "./EmailField";
import CustomModal from "components/atom/modal";
import { Images } from "shared/images";
import ButtonGreen from "components/atom/button/ButtonGreen";

function EmailTemplate() {
  const dispatch = useDispatch();
  const [activeTab, setActiveTab] = useState(TAB_EMAIL_TEMP_DATA[0].id);
  const [activeId, setActiveId] = useState(1);
  const [msg, sendNotification] = useNotification();
  const [data, setData] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [isDataChanged, setIsDataChanged] = useState(false);

  const fetchData = useCallback(() => {
    let payload = {
      template_type: activeTab,
    };
    dispatch(
      fetchEmailTemplate(
        payload,
        (res, type) => {
          setIsDataChanged(false);
          setData(res?.data);
          //  setActiveTab(res?.data?.template_type);
        },
        (err) => {
          setIsDataChanged(false);
          setData({ content: "" });
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  }, [activeTab, dispatch, sendNotification]);

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  const editAPI = (content) => {
    let payload = {
      content: content,
      template_type: activeTab,
    };

    const onSuccess = (res) => {
      fetchData();
      setIsDataChanged(false);
      sendNotification({
        msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
        variant: SNACK_VARIANT.SUCCESS,
      });
    };

    const onError = (err) => {
      sendNotification({
        msg: err?.data?.message
          ? err.data.message
          : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
        variant: SNACK_VARIANT.ERROR,
      });
    };

    dispatch(
      editEmailTemplate(payload, (res) => {
        onSuccess(res);
      }),
      (err) => {
        onError(err);
      }
    );
  };

  const onTabChange = (id) => {

    setActiveId(id);
    if (isDataChanged) {
      setShowModal(true);
    } else {
      setActiveTab(id);
    }
  };

  const ChangeTabModal = () => {
    return (
      <CustomModal
        isOpen={showModal}
        avarge={true}
        handleToggle={() => {
          setShowModal((prev) => !prev);
        }}
      >
        <div className="confirmation_modal text-center">
          <em className="mb-3 modal_icon danger_icon">
            <img src={Images.emailTemp} alt="" width="30" />
          </em>
          <h2 className="h5 mb-2">{STRINGS.ARE_YOU_SURE}</h2>
          <p className="mb-4">{STRINGS.DISCARD_ON_TAB_CHANGE}</p>
          <ButtonGreen
            title={BUTTONS.CONFIRM}
            onClick={() => {
              setActiveTab(activeId);
              setShowModal(false);
            }}
          />
        </div>
      </CustomModal>
    );
  };

  return (
    <>
      <NavTab
        onClick={(id) => onTabChange(id)}
        tabsData={TAB_EMAIL_TEMP_DATA}
        activeTab={activeTab}
      />
      <EmailField
        data={data}
        editAPI={editAPI}
        activeTab={activeTab}
        setIsDataChanged={setIsDataChanged}
        isDataChanged={isDataChanged}
      />
      <ChangeTabModal />
    </>
  );
}

export default EmailTemplate;
