import { NOTIFICATION_COUNT, NOTIFICATION_DATA, USER_STATUS_UPDATE } from "./actionType";

export const setNotificationData = (payload) => {
  return {
    type: NOTIFICATION_DATA,
    payload,
  };
};

export const setNotificationCount = (payload) => {
  return {
    type: NOTIFICATION_COUNT,
    payload,
  };
};

export const userStatusUpdate = (
  payload,
  onSuccess = () => {},
  onError = () => {}
) => {
  return {
    type: USER_STATUS_UPDATE,
    payload,
    onSuccess,
    onError,
  };
};
