// libs
import moment from "moment";

export const urlConfig = process.env.REACT_APP_IMAGE_BASE_URL || "";

export const getDocsUrl = (url) => {
    if (url) {
        return `${urlConfig}${url}`;
    } else {
        return "";
    }
};

export const getQueryParamValue = (url = "", queryName = "") => {
    if (!url.length) return "";
    const searchParams = new URLSearchParams(url);
    return searchParams.get(queryName);
};

export const deepCopy = (data) => {
    if (!data) return {};
    return JSON.parse(JSON.stringify(data));
};

const getImageType = (type) => {
    switch (type) {
        case "image/png":
            return "png";
        case "image/jpg":
            return "jpg";
        case "image/jpeg":
            return "jpeg";
        case "image/svg+xml":
            return "svg";
        default:
            return "jpeg";
    }
};

const getDocFileType = (type) => {
    switch (type) {
        case "text/csv":
            return "csv";
        case "application/vnd.ms-excel":
            return "xls";
        default:
            return "pdf";
    }
};

export const downloadFile = (file) => {
    fetch(file, {
        method: "GET",
        headers: {
            "Content-Type": "application",
        },
    })
        .then((response) => response.blob())
        .then((blob) => {
            // Create blob link to download
            const url = window.URL.createObjectURL(new Blob([blob]));
            let fileName = `file.${
                blob?.type?.indexOf("image") !== -1
                    ? getImageType(blob?.type)
                    : blob?.type == "application/msword"
                    ? "doc"
                    : blob?.type.indexOf("mpeg") !== -1
                    ? "mpeg"
                    : getDocFileType(blob?.type)
            }`;
            let link = document.createElement("a");
            link.href = url;
            link.setAttribute("download", fileName);
            // Append to html link element page
            document.body.appendChild(link);

            // Start download
            link.click();

            // Clean up and remove the link
            link.parentNode.removeChild(link);
        });
};

export const getMessageDay = (date) => {
    let current = new Date();
    var msgDate = new Date(date);
    let showDate = "";
    if (current?.getDate() === msgDate?.getDate() && current?.getMonth() === msgDate?.getMonth()) {
        showDate = `TODAY`;
    } else if (
        current?.getDate() - 1 === msgDate?.getDate() &&
        current?.getMonth() === msgDate?.getMonth()
    ) {
        showDate = `YESTERDAY`;
    } else {
        showDate = `${moment(msgDate).format("DD MMM YYYY")}`;
    }
    return showDate;
};
