import React, { useEffect, useState } from "react";
import { Images } from "shared/images";
import { BUTTONS, SORT_KEY, STRINGS } from "shared/constants";
import ButtonStatus from "components/atom/button/ButtonStatus";
import { CS_IN_USE_TABLE } from "shared/tableData";
import ActivateDeactivate from "components/molecules/activateDeactivate";
import CheckboxMultiBody from "components/molecules/checkboxMulti/body";
import CheckboxHeader from "components/molecules/checkboxMulti/header";
import SortIcon from "components/molecules/sortIcon";
import { getPaginatedIndex } from "components/utility";

function ServicesTable({
  data,
  statusChange,
  setmultiplePayload = () => {},
  multiplepayload,
  setModalText = () => {},
  setShowModal = () => {},
  setServiceData = () => {},
  deleteService,
  setId = () => {},
  setPageCount = () => {},
  handleSort = () => {},
  setSortKey = () => {},
  setSortType = () => {},
  toggle = {
    isActive: false,
    name: "",
  },
  setToggle = () => {},
  showSnack = () => {},
  itemsPerPage = 15,
  pageCount = 1,
}) {
  const [multipleSelect, setMultipleSelect] = useState(false);

  useEffect(() => {
    if (multiplepayload?.length === 0) setMultipleSelect(false);
  }, [multiplepayload]);

  const sortSwitchKey = (name) => {
    switch (name) {
      case CS_IN_USE_TABLE.CS_IN_USER_NAME:
        return setSortKey(SORT_KEY.cs_in_use);
      default:
        return setSortKey(SORT_KEY.default);
    }
  };

  const sortImage = (name) => {
    switch (name) {
      case CS_IN_USE_TABLE.CS_IN_USER_NAME:
        return true;
      default:
        return false;
    }
  };

  return (
    <>
      <thead>
        <tr key={1000 + 1}>
          <th>
            <CheckboxHeader
              setId={setId}
              multipleSelect={multipleSelect}
              setmultiplePayload={setmultiplePayload}
              setMultipleSelect={setMultipleSelect}
              data={data}
              multiplepayload={multiplepayload}
            />
          </th>
          {Object.values(CS_IN_USE_TABLE).map((i, idx) => (
            <th scope="col" id={idx}>
              {i}
              {sortImage(i) ? (
                <SortIcon
                  toggleIcon={
                    toggle.name === i && toggle.isActive ? true : false
                  }
                  onClick={() => {
                    if (toggle.name !== i) {
                      setSortType(0);
                    }
                    setToggle({
                      name: i,
                      isActive: !toggle.isActive,
                    });
                    sortSwitchKey(i);
                    setPageCount(1);
                    handleSort();
                  }}
                />
              ) : null}
            </th>
          ))}
        </tr>
      </thead>

      {data?.length ? (
        data.map((value, index) => (
          <tbody key={index}>
            <tr
              onClick={() =>
                value?.is_default
                  ? showSnack("Default Values can not be changed")
                  : ""
              }
            >
              <td width={80}>
                <CheckboxMultiBody
                  setMultipleSelect={setMultipleSelect}
                  setmultiplePayload={setmultiplePayload}
                  value={value}
                  multiplepayload={multiplepayload}
                  setId={setId}
                  data={data}
                />
              </td>
              {/* <td>{getPaginatedIndex(itemsPerPage, pageCount, index)}</td> */}
              <td>{value.cs_name}</td>
              <td>
                <ActivateDeactivate statusChange={statusChange} value={value} />
              </td>

              <td width={200}>
                <div
                  className={`d-flex btn_group ${
                    value?.is_default ? "disabled " : ""
                  }`}
                >
                  <ButtonStatus
                    image={Images.edit}
                    status={"default"}
                    title={"Edit Service"}
                    onClick={() => {
                      setModalText(BUTTONS.UPDATE);
                      setServiceData(value);
                      setShowModal(true);
                    }}
                  />
                  <ButtonStatus
                    image={Images.deleteIcon}
                    status={"Unverified"}
                    title={"Delete Service"}
                    onClick={() => {
                      setId(value?.id);
                      deleteService();
                    }}
                  />
                </div>
              </td>
            </tr>
          </tbody>
        ))
      ) : (
        <tbody>
          <tr>
            <td
              colSpan={Object.values(CS_IN_USE_TABLE).length + 1}
              className="text-center py-5"
            >
              {STRINGS.NO_DATA_AVAILIABLE}
            </td>
          </tr>
        </tbody>
      )}
    </>
  );
}

export default ServicesTable;
