import TextField from "components/atom/textField/TextField";
import React, { useEffect, useState } from "react";
import { BUTTONS, CS_IN_USE } from "shared/constants";

function CsField({ addEditAPI, buttonText, data }) {
  const [name, setName] = useState("");

  useEffect(() => {
    if (buttonText === BUTTONS.UPDATE) {
      setName(data.cs_name ? data.cs_name : "");
    } else {
      setName("");
    }
  }, [buttonText, data.cs_name]);

  const disabled = () => {
    if (name === data.cs_name) {
      return true;
    } else if (!name.length) {
      return true;
    } else {
      return false;
    }
  };

  return (
    <>
      <form autoComplete="off">
        <TextField
          label={CS_IN_USE.NAME}
          value={name}
          type="text"
          onChange={(e) => setName(e.target.value)}
        />
        <div className="text-center">
          <button
            className={`btn btn-md btn-primary ${disabled() ? "disabled" : ""}`}
            onClick={(e) => {
              addEditAPI(e, name);
            }}
          >
            {buttonText}
          </button>
        </div>
      </form>
    </>
  );
}

export default CsField;
