export const getInititalValueRegistration = (
  data = {},
  country = "",
  state = "",
  phone = ""
) => {
  const { address, city, email, postal_code } =
    data;
  return {
    address,
    city,
    country,
    postal_code,
    state: state,
    email,
    phone_number: phone,
  };
};
