import "../../styles.scss";
import SideItem from "components/atom/sideItem/SideItem";
import { Images } from "shared/images";
import { navbarData_Admin } from "./navbarData";
import { useHistory, useLocation ,matchPath} from "react-router-dom";
import { ROUTE_CONSTANTS } from "shared/routeConstants";
import { mobileDevice, toggleClasses } from "components/utility";
import { useCallback, useEffect, useState } from "react";
import { DEVICE_TYPE } from "shared/constants";

function NavBarHOC({ sidebarToggle, SetSidebarToggle }) {
  const history = useHistory();
  const location = useLocation();
  const [active, setActive] = useState(0);

  useEffect(() => {
    const currentTab = navbarData_Admin?.find(
      (route) => route?.path === location?.pathname||
      route?.nestedRoutes?.find(
          (nestedRoute) =>
              matchPath(location?.pathname, {
                  path: nestedRoute,
              })?.url?.length,
      ),
    );
    setActive(currentTab?.id);
  }, [location.pathname]);

  const handleItemClick = (data) => {
    setActive(data?.id);
    history.push(data?.path);
    if (mobileDevice() === DEVICE_TYPE.MOBILE) SetSidebarToggle(false);
  };

  const toggleSidebar = useCallback(() => {
    toggleClasses("main", "slide-active", "remove");
  }, [sidebarToggle]);

  useEffect(() => {
    toggleSidebar();
  }, [toggleSidebar]);

  return (
    <>
      <aside className="sideBar">
        <button
          className="d-xl-none close_btn"
          onClick={() => {
            SetSidebarToggle(false);
          }}
        >
          <img src={Images.CloseIcon} alt="Close" width="30" />
        </button>
        <nav className="navbar text-left">
          <div
            className="navbar-brand"
            onClick={() => history.push(ROUTE_CONSTANTS.DASHBOARD)}
          >
            <img
              src={Images.locumLogo}
              alt="locumlogo"
              width="194"
              className="img-fluid"
            />
          </div>
          <ul className="navbar-nav w-100">
            {navbarData_Admin.map((item, idx) => {
              return (
                <SideItem
                  key={item.id}
                  data={item}
                  handleClick={handleItemClick}
                  active={active}
                />
              );
            })}
          </ul>
        </nav>
      </aside>
    </>
  );
}

export default NavBarHOC;
