import React from "react";
import "./progressbar.scss";
import RangeSlider from "../chatComponents/RangeSlider";

const ProgressBar = ({ bgcolor, completed }) => {
  const tooltipPercentage = 14;

  const containerStyles = {
    backgroundColor: "#e0e0de",
  };

  const fillerStyles = {
    height: "100%",
    width: `${completed}%`,
    backgroundColor: "#5A9B48",
    borderRadius: "inherit",
    textAlign: "right",
  };

  const labelStyles = {
    right:
      completed == 0
        ? `${tooltipPercentage + 4}px`
        : completed <= 10
        ? `${tooltipPercentage / 2}px`
        : `-${tooltipPercentage}px`,
  };

  return (
    <div style={containerStyles} className="progressBar">
      {/* <div style={fillerStyles}>
        <span
          style={labelStyles}
          className="progresstext"
        >{`${completed}%`}</span>
      </div> */}
      <div
        class="progress-bar text-white"
        role="progressbar"
        style={fillerStyles}
        aria-valuenow="25"
        aria-valuemin="0"
        aria-valuemax="100"
      >
        <span>{`${completed}%`}</span>
      </div>
    </div>
  );
};

export default ProgressBar;
