import React from "react";

function CheckboxHeader({
  multipleSelect,
  setmultiplePayload = () => {},
  setMultipleSelect = () => {},
  data,
  multiplepayload,
  setId = () => {},
}) {
  return (
    <div className="checkbox">
      <input
        type="checkbox"
        //disabled={!multiplepayload.length}
        checked={multipleSelect}
        onChange={(e) => {
          setId(null);
          if (e.target.checked) {
            setmultiplePayload(
              data
                .map((value, index) =>  value?.id)
                ?.filter(Boolean)
            );
            setMultipleSelect(true);
          } else if (
            data?.filter((i) => (i))?.length >
            multiplepayload.length
          ) {
            setMultipleSelect(false);
          } else {
            setmultiplePayload([]);
            setMultipleSelect(false);
          }
        }}
      />
      <label></label>
    </div>
  );
}

export default CheckboxHeader;
