// libs
import React from "react";

// components
import CustomDropdown from "../CustomDropDown";
import CheckClickOutside from "../OutsideClick";

const CustomDropdownList = ({
    isVisible,
    handleIsVisible = () => { },
    activeIndex,
    dropdownOptions = [],
    activeClassName = "",
    className = "",
    heading = "",
    headingComponent = () => <></>
}) => {
    const HeadingComponent = headingComponent;
    return (<>
        <CheckClickOutside
            onClick={() => {
                handleIsVisible(false);
            }}
        >
            <div
                className="d-flex align-items-center userProfile position-relative"
                onClick={() => {
                    handleIsVisible((prevState) => !prevState);
                }}
            >
                {heading ? heading : <HeadingComponent/>}
                <CustomDropdown show={isVisible}>
                    <div className="user-body">
                        {dropdownOptions?.length > 0 ?
                            dropdownOptions.map((i, idx) => {
                                return (
                                    <div
                                        key={idx}
                                        className={`dropDown-list ${activeIndex == i.id ? activeClassName : ""}`}
                                        onClick={() => activeIndex !== i.id ? i.onClick() : undefined
                                        }
                                    >
                                        <span>{i.title}</span>
                                    </div>
                                );
                            }) :
                            null}
                    </div>
                </CustomDropdown>
            </div>
        </CheckClickOutside>
    </>)
};

export default CustomDropdownList;