import React, { useState } from "react";
import {
  BUTTONS,
  LOGIN_FORM,
  REGEX,
  SNACK_VARIANT,
} from "shared/constants";
import { Images } from "shared/images";
import TextField from "components/atom/textField/TextField";
import { useDispatch } from "react-redux";
import { forgotPassword } from "redux/actions/auth";
import "../style.scss";
import { SNACK_MESSAGES } from "../../../shared/constants";
import useNotification from "components/atom/snackbar";

function SendEmail({ setIsForgot }) {
  const dispatch = useDispatch();
  const [email, setEmail] = useState("");
  const [emailErr, setEmailErr] = useState("");
  const [isSuccess, setIsSuccess] = useState();
  const [msg, sendNotification] = useNotification();

  const validate = (value) => {
    if (value?.length === 0) {
      setEmail(value);
      setEmailErr("");
    } else if (
      value === "-" ||
      value?.includes("-") ||
      !new RegExp(REGEX.EMAIL).test(value)
    ) {
      setEmailErr(LOGIN_FORM.FIELDS.EMAIL.ERROR);
    } else {
      setEmailErr("");
      setEmail(value);
    }
  };

  const onSave = (e) => {
    e.preventDefault();
    const payload = {
      email: email,
    };

    dispatch(
      forgotPassword(
        payload,
        (res) => {
          setIsSuccess(true);
          sendNotification({
            msg: res?.data?.message
              ? res?.data?.message
              : SNACK_MESSAGES.SAVED_SUCCESSFULLY,
            variant: SNACK_VARIANT.SUCCESS,
          });
        },
        (err) => {
          setIsSuccess(false);
          sendNotification({
            msg: err.data?.message
              ? err.data?.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  return (
    <>
      {isSuccess ? (
        <p className="text-center"> {LOGIN_FORM.PASSWORD_LINK_SENT}</p>
      ) : (
        <>
          <p className="text-center"> {LOGIN_FORM.FIELDS.FORGOT_TITLE}</p>
          <form autoComplete="off">
            <TextField
              label={LOGIN_FORM.FIELDS.EMAIL.LABEL}
              image={Images.emailImage}
              error={emailErr}
              value={email}
              type="text"
              onChange={(e) => {
                setEmail(e.target.value);
                validate(e.target.value);
              }}
            />

            <div className="text-center mb-4">
              <button
                className={`btn btn-md btn-primary ${
                  email?.length && !emailErr.length ? "" : "disabled"
                }`}
                onClick={(e) => onSave(e)}
              >
                {BUTTONS.CONFIRM}
              </button>
            </div>
          </form>
        </>
      )}
      <div className="text-center forgot_txt">
        <span
          className="d-inline-block pointer"
          onClick={() => setIsForgot((prev) => !prev)}
        >
          {LOGIN_FORM.FIELDS.BACK_TO_LOGIN}
        </span>
      </div>
    </>
  );
}

export default SendEmail;
