// libs
import { useEffect, useRef, useState } from 'react';
import { io } from 'socket.io-client';

// hook to create socket connection
const useSocket = (url, id, token) => {
    const socketRef = useRef();
    const [socket, setSocket] = useState();
    const [socketError, setSocketError] = useState(true);
    useEffect(() => {
        if(url && id && token ){
            
            const socketConnection = io(`${url}?user_id=${id}&token=${token}`,{
                transports: ["websocket"],
            });

            socketRef.current = socketConnection;
    
            socketRef.current.triggerEvent = (eventName, payload, cb = () => {}) => {
                socketRef.current.emit(eventName, payload, cb);
            };

            socketRef.current.listenEvent = (eventName, callBack) => {
                socketRef.current.on(eventName, callBack);
            };
    
            socketRef.current.disconnectEvent = (eventName) => {
                socketRef.current.removeAllListeners(eventName);
            }
    
            socketRef.current.removeListenerEvent = () => {
                socketRef.current.removeAllListeners();
            }
    
            socketRef.current.on('connect_error', (err) => {
                setSocketError(err.message);
            });

            socketRef.current.on('connect', () => {
                socketRef.current.connected = true;
                setSocket(socketRef.current);
                setSocketError(false);
            });
        }

        // Clean up the socket connection when the component unmounts
        return () => {
            if(socketRef?.current){
                socketRef.current.disconnect();
                socketRef.current.connected = false;
            }
        };
    }, [url, id, token]);

    // if connection is not established, set error state
    useEffect(() => {
        if (!socketRef?.current?.connected) {
            setSocketError(true);
        } else {
            setSocketError(false);
        }
    }, [socketRef?.current]);
    
    return {
        socket,
        socketError,
    };
};

export default useSocket;
