// libs
import React from "react";

// components
import ChatList from "./components/ChatList";
import SearchSidebar from "./components/SearchSidebar";
import CheckClickOutside from "../shared/components/OutsideClick";

// utils
import { deepCopy } from "../shared/utils";

// styles
import '../chatpanel.scss';

const Inbox = ({
    activeUser = null,
    handleActiveUser = () => { },
    usersList = [],
    lastMessages = [],
    history,
    handleActiveFilter = () => { },
    activeFilter,
    handleFilterUsersByName = () => { },
    searchKeyword = null,
    isLoading = false,
    setSearchSidebarVisible = () => { },
    searchSidebarVisible = false,
    searchedData = [],
    showChatFilter = false,
    setShowChatFilter = () => {},
    fetchUsersByName = () => {},
    hasMoreUsers = true,
    totalUsers = 0,
    fetchChatUsers = () => {},
    totalSearchUsers = 0
}) => {

    // chat list recent order
    const order = lastMessages?.reduce((acc, val, idx) => {
        acc[val?.id] = idx;
        return acc;
    }, {});

    const sortedUsersList = deepCopy(usersList)?.sort((a = {}, b = {}) => {
        return order[a.id] - order[b.id];
    });

    return (
        <>
            {searchSidebarVisible ? (
            <CheckClickOutside className="d-inline-block w-100 search_result" onClick={() => setSearchSidebarVisible(false)}>
                <SearchSidebar
                    handleFilterUsersByName={handleFilterUsersByName}
                    setSearchSidebarVisible={setSearchSidebarVisible}
                    searchKeyword={searchKeyword}
                    searchedData={searchedData}
                    isLoading={isLoading}
                    isSearchBarActive={searchSidebarVisible}
                    handleActiveUser={handleActiveUser}
                    activeUser={activeUser}
                    fetchUsersByName={fetchUsersByName}
                    hasMoreUsers={hasMoreUsers}
                    totalUsers={totalSearchUsers}
                />
            </CheckClickOutside>
            ) : (
                <ChatList
                    setSearchSidebarVisible={setSearchSidebarVisible}
                    handleActiveFilter={handleActiveFilter}
                    usersList={sortedUsersList}
                    showChatFilter={showChatFilter}
                    setShowChatFilter={setShowChatFilter}
                    activeFilter={activeFilter}
                    handleActiveUser={handleActiveUser}
                    activeUser={activeUser}
                    lastMessages={lastMessages}
                    totalUsers={totalUsers}
                    fetchChatUsers={fetchChatUsers}
                />
            )}
        </>
    );
};

export default Inbox;
