import React, { useCallback, useEffect, useState } from "react";
import { ROUTE_CONSTANTS } from "../../../../shared/routeConstants";
import { useHistory, useLocation } from "react-router-dom";
import {
  PHARMACY_USER_VIEW,
  SNACK_MESSAGES,
  SNACK_VARIANT,
  USERS_CONSTS,
} from "shared/constants";
import BasicInvoiceView from "./BasicInvoiceView";
import { useDispatch } from "react-redux";
import useNotification from "components/atom/snackbar";
import { deleteBranchsApi, invoiceViewApi } from "redux/actions/auth";
import BreadCrumb from "components/atom/breadcrum";

function InvoiceView({}) {
  // Importing required hooks and functions from React.
  const history = useHistory();
  const dispatch = useDispatch();
  const idParam = useLocation();

  // Retrieving the id of the invoice from the location object passed through the route.
  const id = idParam?.state?.id;

  // State variables for the invoice details and notification messages.
  const [viewData, setViewData] = useState({});
  const [msg, sendNotification] = useNotification();

  // An array of basic information to be displayed about the invoice.
  const BASIC_CONST = [
    { title: "From", value: viewData?.from_invoice, isVerified: true },
    { title: "To", value: viewData?.to_invoice },
    { title: "Address", value: viewData?.from_address },
    { title: "Address", value: viewData?.to_address },
  ];

  // Function to delete the current invoice from the database.
  const deleteBranch = (id) => {
    // Creating an object with the id of the current invoice to be deleted.
    let data = {
      branches: [
        {
          id: id,
        },
      ],
    };
    // Dispatching an API call to delete the invoice from the database.
    dispatch(
      deleteBranchsApi(data, (res) => {
        // If the deletion is successful, displaying a success notification message.
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        // If the deletion is unsuccessful, displaying an error notification message.
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  // Function to retrieve the details of the current invoice from the database.
  const viewUser = useCallback(() => {
    // Creating an object with the id of the current invoice to be retrieved.
    let data = { id: id };
    // Dispatching an API call to retrieve the invoice details from the database.
    dispatch(
      invoiceViewApi(data, (res) => {
        // If the retrieval is successful, updating the state variable with the invoice details.
        setViewData(res?.data);
      }),
      (err) => {
        // If the retrieval is unsuccessful, displaying an error notification message and navigating back to the previous page.
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
        history.goBack();
      }
    );
  }, [id, dispatch, sendNotification, history]);

  // useEffect hook to retrieve the invoice details on page load.
  useEffect(() => {
    viewUser();
  }, [viewUser]);

  return (
    <>
      <div className="d-flex flex-wrap align-items-center justify-content-between">
        <BreadCrumb
          onClick={() =>
            history.push(ROUTE_CONSTANTS.USERS, {
              id: USERS_CONSTS.PHARMACY_USERS.ID,
            })
          }
          previousLink={PHARMACY_USER_VIEW.PHARMACY_USERS}
          currentLink={PHARMACY_USER_VIEW.DETAILS}
        />

        {/* <button className={`btn btn-danger`} onClick={() => deleteBranch(id)}>
          Delete
        </button> */}
      </div>

      <BasicInvoiceView data={BASIC_CONST} apiData={viewData} />
    </>
  );
}

export default InvoiceView;
