import ButtonGreen from "components/atom/button/ButtonGreen";
import CustomModal from "components/atom/modal";
import React, { useEffect } from "react";
import { STATUS_OPTIONS, STRINGS } from "shared/constants";

function DocUnverifyModal({
  setShow,
  show,
  onClick = () => {},
  reason,
  setReason,
}) {
  const disabled = () => {
    if (!reason?.length) {
      return true;
    } else {
      return false;
    }
  };

  useEffect(() => {
    setReason("");
  }, [show]);

  return (
    <>
      <CustomModal
        isOpen={show}
        className="as"
        avarge={true}
        handleToggle={() => setShow((prev) => !prev)}
      >
        <div className="confirmation_modal text-center">
          <h2 className="h5 mb-2">{STRINGS.REASON_TO_UNVERIFY}</h2>
          <div className="mb-4">
            <label label={STRINGS.ENTER_REASON} />
            <textarea
              placeholder={"Enter here..."}
              name="Mark as unverified"
              className="form-control"
              rows="4"
              value={reason}
              onChange={(e) => setReason(e.target.value)}
            ></textarea>
          </div>
          <div className="text-center">
            <ButtonGreen
              disabled={disabled()}
              title={STATUS_OPTIONS.UNVERIFY.title}
              onClick={() => onClick()}
            />
          </div>
        </div>
      </CustomModal>
    </>
  );
}

export default DocUnverifyModal;
