import React from "react";

import "./react-select.scss";
import ReactSelect from "components/atom/ReactSelect";

const options = [
  { value: true, label: "Activate", color: "green" },
  { value: false, label: "Deactivate", color: "red" },
];

const ActivateDeactivate = ({ value, title, statusChange }) => {

  let selectedOption = title ? title : options[1];

  if (value) {
    if (value.is_active || value.status) {
      selectedOption = options[0];
    }else{
      selectedOption = options[1];
    }
  }

  const handleChange = (selectedOption) => {
    statusChange(value?.id, selectedOption.value);
  };

  return (
    <ReactSelect
      selectedOption={selectedOption}
      handleChange={handleChange}
      options={options}
      title={title}
      // isDisabled={value?.is_default}
    />
  );
};

export default ActivateDeactivate;
