import React, { useState } from "react";
import "./styles.scss";
import Cards from "../../../components/atom/cards/index";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import { fetchDashboardData } from "redux/actions/auth";
import { API_RESPONSE, DASHBOARD_CARDS } from "shared/constants";
import { Images } from "shared/images";
import Users from "../users";

function Dashboard() {
  const dispatch = useDispatch();
  const [cardData, setCardData] = useState([]);
  const token = useSelector((state) => state.auth.token);

  useEffect(() => {
    if (token) {
      dispatch(
        fetchDashboardData("", (res, type) => {
          let temp = [
            {
              title: DASHBOARD_CARDS.Pharmacy,
              count: res?.data?.pharmacyUserCount,
              image: Images.PharmacyUsers,
            },
            {
              title: DASHBOARD_CARDS.Locum,
              count: res?.data?.locumUserCount,
              image: Images.LocumUsers,
            },
            {
              title: DASHBOARD_CARDS.Pending,
              count: res?.data?.pendingRequestCount,
              image: Images.verifyLogo,
            },
            {
              title: DASHBOARD_CARDS.Vacancies,
              count: res?.data?.vacanciesCount,
              image: Images.Vacancies,
            },
          ];
          if (type === API_RESPONSE.SUCCESS) setCardData(temp);
        })
      );
    }
  }, [token, dispatch]);

  return (
    <>
      <div className="row card_block">
        {cardData?.length
          ? cardData.map((i, idx) => <Cards key={idx} data={i} index={idx} />)
          : null}
      </div>
      <Users />
    </>
  );
}

export default Dashboard;
