import React, { useState } from "react";
import "./style.scss";

const Accordion = ({ title, children }) => {
    const [isShow,setIsShow] = useState(true);
    return (
        <div className="accordion" id={`${title.replace(/ /g, '')}-accordion`}>
            <div className="accordion-item">
                <h2 className="accordion-header" id={`${title.replace(/ /g, '')}-heading`}>
                    <button
                        className={`accordion-button header-accord  ${isShow ? "show" : ""}`}
                        type="button"
                        data-bs-toggle="collapse"
                        data-bs-target={`#collapse-${title.replace(/ /g, '')}`}
                        aria-expanded="true"
                        aria-controls={`collapse-${title.replace(/ /g, '')}`}
                        onClick={()=> setIsShow(prev => !prev)}
                    >
                        {title}
                    </button>
                </h2>
                <div
                    id={`collapse-${title.replace(/ /g, '')}`}
                    className={`accordion-collapse collapse ${isShow ? "show" : ""}`}
                    aria-labelledby={`${title.replace(/ /g, '')}-heading`}
                    data-bs-parent={`#${title.replace(/ /g, '')}-accordion`}
                >
                    <div className="accordion-body">
                        {children}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Accordion;
