import { SOCKET_INSTANCE } from "../actions/actionType";

const initialState = {
  socketInstance: {},
};

function socketReducer(state = initialState, action) {
  switch (action.type) {
      case SOCKET_INSTANCE:
      return {
        ...state,
        socketInstance: action.payload,
      }
    default:
      return state;
  }
}

export default socketReducer;
