import { all, put, takeLatest } from "redux-saga/effects";
import { stopLoader, startLoader } from "redux/actions/commonCRUD";
import { clearProfileData, setAuthToken } from "redux/actions/auth";
import {
  GET_CHAT_USERS_LIST,
  GET_MESSAGES_BY_TEXT,
  GET_USERS_LIST_BY_NAME,
  GET_USER_MESSAGES_LIST,
  UPLOAD_MEDIA,
} from "redux/actions/actionType";
import { STATUS_CODES } from "services/api/constants";
import { API } from "shared/api";
import { getRequest, postRequest, updateAuthToken } from "shared/axios";
import { objectToFormData } from "components/utility";

// get usersList by name
function* fetchUsersListByName({
  payload,
  onSuccess = () => {},
  onError = () => {},
}) {
  const { isDebounceCall = false, search = "", page = 1, limit = 10 } = payload;
  try {
    if (!isDebounceCall) {
      yield put(startLoader());
    }
    const { status, data = {} } = yield getRequest({
      API: `${API.FETCH_USERS_BY_NAME}?search=${encodeURIComponent(search)}&page=${page}&limit=${limit}`,
    });
    onSuccess(data);
  } catch (error) {
    if (error?.status === STATUS_CODES.UNAUTHORIZED) {
      updateAuthToken(null);
      yield put(clearProfileData());
      yield put(setAuthToken(""));
    }
    onError(error?.data?.message || error?.data?.detail || error?.message);
  } finally {
    yield put(stopLoader());
  }
}

// get chat Users list
function* fetchChatUsers({ payload, onSuccess = () => {}, onError = () => {} }) {
  const {
    isDebounceCall = false,
    page = 1,
    limit = 10,
    archive = 0,
    blocked = 0,
  } = payload;
  let apiUrl = `${API.FETCH_CHAT_USERS_LIST}?page=${page}&limit=${limit}`;
  if (archive) {
    apiUrl += `&archive=${archive}`;
  }
  if (blocked) {
    apiUrl += `&blocked=${blocked}`;
  }
  try {
    if (!isDebounceCall) {
      yield put(startLoader());
    }
    const { status, data = {} } = yield getRequest({
      API: apiUrl,
    });
    onSuccess(data);
  } catch (error) {
    if (error?.status === STATUS_CODES.UNAUTHORIZED) {
      updateAuthToken(null);
      yield put(clearProfileData());
      yield put(setAuthToken(""));
    }
    onError(error?.data?.message || error?.data?.detail || error?.message);
  } finally {
    yield put(stopLoader());
  }
}

// get chat history of User
function* fetchChatHistory({
  payload,
  onSuccess = () => {},
  onError = () => {},
}) {
  const {
    isDebounceCall = false,
    page = 1,
    limit = 10,
    room_name = "",
    id = null,
  } = payload;
  try {
    if (!isDebounceCall) {
      yield put(startLoader());
    }
    let apiUrl = `${API.FETCH_USER_MESSAGES_LIST}?page=${page}&limit=${limit}&room_name=${room_name}`;
    if (id) {
      apiUrl += `&id=${id}`;
    }
    const { status, data = {} } = yield getRequest({
      API: apiUrl,
    });
    onSuccess(data);
  } catch (error) {
    if (error?.status === STATUS_CODES.UNAUTHORIZED) {
      updateAuthToken(null);
      yield put(clearProfileData());
      yield put(setAuthToken(""));
    }
    onError(error?.data?.message || error?.data?.detail || error?.message);
  } finally {
    yield put(stopLoader());
  }
}

// get chat history of User
function* uploadMedia({ payload, onSuccess = () => {}, onError = () => {} }) {
  const { isDebounceCall = false, type, media, room_name } = payload;
  try {
    if (!isDebounceCall) {
      yield put(startLoader());
    }
    const formDataObj = objectToFormData({
      type,
      media,
      room_name,
    });
    let apiUrl = `${API.UPLOAD_MEDIA}`;
    const { status, data = {} } = yield postRequest({
      API: apiUrl,
      DATA: formDataObj,
    });
    onSuccess(data);
  } catch (error) {
    if (error?.status === STATUS_CODES.UNAUTHORIZED) {
      updateAuthToken(null);
      yield put(clearProfileData());
      yield put(setAuthToken(""));
    }
    onError(error?.data?.message || error?.data?.detail || error?.message);
  } finally {
    yield put(stopLoader());
  }
}

