export const REGEX = {
  EMAIL:
    /^(([^<>()\\.,;:\s@"]+(\.[^<>()\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
  PASSWORD:
    /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[ !"#$%&'()*+,/:;<=>?@[\]^_`{|}~])(?=.{8,})/,
  NAME: /^[\w'\-,.][^0-9_!??/\\+=@#$%&*(){}|~<>;:[\]]{2,}$/,
  NUMBERS: /^[0-9]+$/,
};

