import { DEVICE_TYPE, NA, STATUS_OPTIONS } from "shared/constants";
import {
  ERROR_MESSAGES,
  INPUT_TYPES,
  PASSWORD_TYPES,
  STRINGS,
} from "./atom/CustomForm/constants";
import moment from "moment";

// Pagination page click which returns page number
export const handlePageClick = (event, itemsPerPage, items) => {
  return (event.selected * itemsPerPage) % items.length;
};

//toggle css classes by passing name of class and remove class
export const toggleClasses = (className, addClass, type) => {
  if (document.getElementsByClassName(className) && type === "add") {
    document.getElementsByClassName(className)[0].classList?.add(addClass);
  } else {
    document.getElementsByClassName(className)[0].classList?.remove(addClass);
  }
};

//extract key from value of object
export const extractKeyValue = (obj, value) => {
  return Object.keys(obj)[Object.values(obj).indexOf(value)];
};

// object to convert object or object to array to formdata
// This function converts a JS object to FormData format
export function objectToFormData(data) {
  // Create an empty instance of FormData
  let formData = new FormData();

  // Loop through each key-value pair in the object
  for (let key in data) {
    // If the value is an array, stringify it and append it as a new key-value pair.
    if (Array.isArray(data[key])) {
      formData.append(key, JSON.stringify(data[key]));
    }
    // Otherwise, simply append the key-value pair to the FormData instance.
    else {
      formData.append(key, data[key]);
    }
  }
  // Return the FormData instance
  return formData;
}

//to convert date string to a local date string in specified format
export function getLocalizedDateString(dateString) {
  // const format = "yyyy-MM-ddTHH:mm:ss.SSSZ";
  const date = new Date(dateString);
  if (isNaN(date.getTime())) {
    throw new Error("Invalid date string");
  }
  const options = { year: "numeric", month: "long", day: "numeric" };
  return date.toLocaleDateString(undefined, options);
}

//to convert date string to a local time string in specified format
export function convertDateTimeToTime(dateString) {
  const date = new Date(dateString);
  const hours = date.getHours();
  const minutes = date.getMinutes();
  const ampm = hours >= 12 ? "PM" : "AM";
  const formattedHours = hours % 12 === 0 ? 12 : hours % 12;
  const formattedMinutes = minutes < 10 ? `0${minutes}` : minutes;
  const formattedTime = `${formattedHours}:${formattedMinutes} ${ampm}`;
  return formattedTime;
}

//function to get times ago from current time. eg: 30 mins ago
export function timeAgo(created_at) {
  const now = new Date();
  const created = new Date(created_at);
  const elapsed = now - created;

  // Get the elapsed time in seconds
  const seconds = Math.floor(elapsed / 1000);

  // Convert seconds to the appropriate unit of time
  if (seconds < 60) {
    return `${seconds} seconds ago`;
  } else if (seconds < 3600) {
    const minutes = Math.floor(seconds / 60);
    return `${minutes} ${minutes === 1 ? "minute" : "minutes"} ago`;
  } else if (seconds < 86400) {
    const hours = Math.floor(seconds / 3600);
    return `${hours} ${hours === 1 ? "hour" : "hours"} ago`;
  } else {
    const days = Math.floor(seconds / 86400);
    return `${days} ${days === 1 ? "day" : "days"} ago`;
  }
}

//filter data by document type
export function filterByDocumentType(data, documentType) {
  let obj = data.filter((item) => item.document_type === documentType);
  return obj[0] ? obj[0].verified : STATUS_OPTIONS.NOT_UPLOADED.key;
}

// get document url of specified type and appending the url
export function filterByDocumentData(data, documentType) {
  const URL_HEAD = process.env.REACT_APP_API_BASE_URL;
  let obj = data.filter((item) => item.document_type === documentType);
  const url = URL_HEAD + obj[0]?.document;
  return url;
}

//Disable buttons for no documents uploaded
export function disableDocViewer(data, documentType) {
  let obj = data.filter((item) => item.document_type === documentType);
  return obj[0]?.document?.length && obj[0]?.document !== "/media/False"
    ? true
    : false;
}

//verify Status switch cases which returns keys for status changes
export function verifyStatusSwitch(status) {
  switch (status) {
    case STATUS_OPTIONS.UNVERIFIED.key:
      return STATUS_OPTIONS.UNVERIFIED.title;
    case STATUS_OPTIONS.VERIFIED.key:
      return STATUS_OPTIONS.VERIFIED.title;
    case STATUS_OPTIONS.PENDING.key:
      return STATUS_OPTIONS.PENDING.title;
    case STATUS_OPTIONS.NOT_UPLOADED.key:
      return STATUS_OPTIONS.NOT_UPLOADED.cs;
    default:
      return STATUS_OPTIONS.DEFAULT.title;
  }
}

//function for splicing element from array
export function splice(state, element) {
  let newArr = [...state];
  newArr.splice(newArr.indexOf(element), 1);
  return newArr;
}

//function to convert 1 year 6 months expierence to 1.5 expierence
export function experienceToYears(years, months) {
  return (years + months / 12)?.toFixed(1);
}

// function to truncate string with given length
export function TruncateString(str, length, maxCharacter) {
  return str?.length > length ? str?.substring(0, maxCharacter) + "..." : str;
}

export function firstCharacter(str, maxCharacter) {
  return str?.length ? str?.substring(0, maxCharacter) : "U";
}

//for profile completeness, we have to get average value from 2 keys in data(profile_completeness and document verified)
//Function for Profile loader
export function profileCompleted(profilePercent, documentPercent) {
  return profilePercent || documentPercent
    ? (profilePercent + documentPercent) / 2 > 100
      ? 100
      : (profilePercent + documentPercent) / 2
    : 0;
}

//function to remove duplicate document type entries
export function getUniqueDocumentTypes(data) {
  const types = data?.reduce((typesObj, item) => {
    typesObj[item?.document_type] = typesObj[item?.document_type] || item;
    return typesObj;
  }, {});

  return Object.values(types);
}

// This function checks whether a string is JSON by parsing it and returning the result or returning the original argument if it's not a string.
export function jsonParse(res) {
  // If the argument is a string, try to parse it as JSON.
  if (typeof res == "string") {
    return JSON.parse(res);
  }
  // If the argument isn't a string, simply return it as is.
  else {
    return res;
  }
}

export const typeSelection = (data, setTypeToggle) => {
  if (PASSWORD_TYPES.includes(data)) {
    setTypeToggle((prev) => {
      if (prev[data] === INPUT_TYPES.TEXT) {
        return {
          ...prev,
          [data]: INPUT_TYPES.PASSWORD,
        };
      } else {
        return {
          ...prev,
          [data]: INPUT_TYPES.TEXT,
        };
      }
    });
  } else {
    return INPUT_TYPES.TEXT;
  }
};

export const validateConfirmPassword = (val, registerName, watch) => {
  if (registerName === STRINGS.CONFIRM_PASSWORD) {
    if (watch(INPUT_TYPES.PASSWORD) != val) {
      return `${ERROR_MESSAGES.PASSWORD_VALID}`;
    }
  }
};

//function for converting HTML text to normal text
export function htmlToText(html) {
  let tempElement = document.createElement("div");
  tempElement.innerHTML = html;
  return tempElement.innerText;
}

//function to check if it is mobile device or not
export const mobileDevice = () => {
  if (
    /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini|Windows Phone/i.test(
      navigator.userAgent
    )
  ) {
    return DEVICE_TYPE.MOBILE;
  } else {
    return DEVICE_TYPE.DESKTOP;
  }
};

export function convertTimeToAmPm(timeString) {
  // const [hour, minute] = timeString.split(":");
  // const amOrPm = hour < 12 ? "AM" : "PM";
  // let hourInAmPmFormat = hour % 12;
  // if (hourInAmPmFormat === 0) {
  //   hourInAmPmFormat = 12;
  // }

  // return `${hourInAmPmFormat}:${minute} ${amOrPm}`;
  const [time, period] = timeString.split(" ");
  let [hour, minute] = time.split(":");
  
  if (period === "PM" && hour !== "12") {
    hour = parseInt(hour, 10) + 12;
  } else if (period === "AM" && hour === "12") {
    hour = "00";
  }

  return `${hour}:${minute}`;
}

export function convertToPrice(res) {
  return Math.trunc(parseInt(res)).toLocaleString();
}

