//   return moment(startDate).add(date, "days").format("DD MMM YYYY");
// };

export const getDatefromDayOfWeek = (startDate, endDate, selectedDays) => {
  let data = [];
  let diff = moment(endDate).diff(startDate, "days");
  for (let i = 0; i <= diff; i++) {
      let day = moment(startDate).add(i, "days").day();
      let dayData = selectedDays.find((item) => item.day == (day == 0 ? 6 : day - 1));
      if (
          dayData?.start_time &&
          dayData?.end_time &&
          dayData?.lunch_start_time &&
          dayData?.lunch_end_time
      ) {
          data.push({
              day: day == 0 ? 6 : day - 1,
              start_time: dayData.start_time,
              end_time: dayData.end_time,
              lunch_start_time: dayData.lunch_start_time,
              lunch_end_time: dayData.lunch_end_time,
              branch: "",
              isSelected: true,
              date: moment(startDate).add(i, "days").format("DD MMM YYYY"),
          });
      }
  }
  return data;
};


export function truncateText(text, maxLength) {
  if (text.length <= maxLength) {
    // If the text is shorter than or equal to the max length, just return it
    return text;
  } else {
    // Otherwise, truncate the text and add the "view more" link
    const truncated = text.substr(0, maxLength) + "...";
    const moreLink =
      '<a href="#" onclick="showMore(event)"><code>View more</code></a>';

    return `${truncated} ${moreLink}`;
  }
}

export function showMore(event) {
  // Prevent the link from being followed
  event.preventDefault();

  // Get the full text and remove the "view more" link
  const fullText = event.target.parentNode.innerText.replace("View more", "");

  // Replace the truncated text with the full text and add the "view less" link
  const lessLink = <a onclick="showLess(event)">View less</a>;
  event.target.parentNode.innerHTML = `${fullText} ${lessLink}`;
}

export function showLess(event) {
  // Prevent the link from being followed
  event.preventDefault();

  // Get the truncated text and add the "view more" link
  const truncated = event.target.parentNode.innerText.split("...")[0] + "...";
  const moreLink = `<a href="#" onclick="showMore(event)">View more</a>`;
  event.target.parentNode.innerHTML = `${truncated} ${moreLink}`;
}

export function formatAddress(addressDetail) {
  const addressParts = [];
  if (addressDetail) {
    const { address, city, state, country, postal_code } = addressDetail;
    if (address) addressParts.push(address);
    if (city) addressParts.push(city);
    if (state) addressParts.push(state);
    if (country) addressParts.push(country);
    if (postal_code) addressParts.push(postal_code);
  }

  return addressParts.length > 0 ? addressParts.join(", ") : "NA";
}

export function formatBranchAddressVacancy(viewData) {
  const addressParts = [];
  if (viewData?.branch_address) {
    const { branch_name, address, city, state, country, postal_code } =
      viewData.branch_address;
    if (branch_name) addressParts.push(branch_name);
    if (address) addressParts.push(address);
    if (city) addressParts.push(city);
    if (state) addressParts.push(state);
    if (country) addressParts.push(country);
    if (postal_code) addressParts.push(postal_code);
  }

  return addressParts.length > 0 ? addressParts.join(", ") : "NA";
}

export function formatPharmacistAddressVacancy(viewData) {
  const addressParts = [];
  if (viewData?.branch_address) {
    const { branch_name, address, city, state, country, postal_code } =
      viewData.branch_address;
    if (city) addressParts.push(city);
    if (state) addressParts.push(state);
  }

  return addressParts.length > 0 ? addressParts.join(", ") : "";
}

export function getPaginatedIndex(limit, pageNumber, idx) {
  return (pageNumber - 1) * limit + idx + 1;
}

export const switchbadges = (status) => {
  switch (status) {
    case 0:
      return "pending";
    case 1:
      return "pending";
    case 2:
      return "success";
    case 3:
      return "danger";
    case 4:
      return "danger";
    default:
      return "info";
  }
};
