import React, { useState } from "react";
import {
  STATUS_DROPDOWN_LOCUM,
  STATUS_DROPDOWN_PHARMACY,
  STATUS_OPTIONS,
  USERS_CONSTS,
} from "shared/constants";
import ReactSelect from "components/atom/ReactSelect";

function StatusChangeDropdown({
  setDocType,
  setShow,
  active,
}) {
  const switchDropdown = () => {
    switch (active) {
      case USERS_CONSTS.LOCUM_USERS.ID:
        return STATUS_DROPDOWN_LOCUM;
      case USERS_CONSTS.PHARMACY_USERS.ID:
        return STATUS_DROPDOWN_PHARMACY;
      default:
    }
  };

  const options = switchDropdown().map((i) => ({
    value: i.key,
    label: i.title,
  }));

  const handleChange = (selectedOption) => {
    setDocType(selectedOption.value);
    setShow(true);
  };

  return (
    <ReactSelect
      handleChange={handleChange}
      options={options}
      title={STATUS_OPTIONS.PLACEHOLDER}
    />
  );
}

export default StatusChangeDropdown;
