import React, { useState } from "react";
import GoogleMapReact from "google-map-react";
import { Images } from "shared/images";
import "./styles.scss";

function GoogleMap({ address = "1600 Amphitheatre Parkway, Mountain View, california.", lat = 37.42216, lng = -122.08427, zoomLevel = 10 }) {
  const [searchAddress, setSearchAddress] = useState(""); // new state for the search input
  const [location, setLocation] = useState({
    address: address,
    lat: lat,
    lng: lng,
  }); // new state for the search input

  // const location = {
  //   address: address,
  //   lat: lat,
  //   lng: lng,
  // };

  const LocationPin = ({ text }) => (
    <div className="d-flex address_block">
      <em><img src={Images.mapPin} alt="" width="20" /></em>
      <span className="d-block text-small">{text}</span>
    </div>
  );

  const handleSearch = async () => {
    try {
      const url = `https://maps.googleapis.com/maps/api/geocode/json?address=${encodeURIComponent(
        searchAddress
      )}`; // replace with your own Google Maps API key
      const response = await fetch(url);
      const data = await response.json();
      //const { lat, lng } = data.results[0].geometry?.location;
      setLocation({ address: searchAddress, lat, lng }); // update location state with new coordinates
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <div className="gMap_sec">
       <GoogleMapReact
        bootstrapURLKeys={{ key: "" }}
        defaultCenter={location}
        defaultZoom={zoomLevel}
      >
        <LocationPin
          lat={location.lat}
          lng={location.lng}
          text={location.address}
        />
      </GoogleMapReact>
    </div>
  );
}

export default GoogleMap;
