import ButtonStatus from "components/atom/button/ButtonStatus";
import { Pagination } from "components/atom/pagination";
import CustomTable from "components/atom/table";
import ActivateDeactivate from "components/molecules/activateDeactivate";
import CheckboxMultiBody from "components/molecules/checkboxMulti/body";
import CheckboxHeader from "components/molecules/checkboxMulti/header";
import SortIcon from "components/molecules/sortIcon";
import React, { useEffect } from "react";
import {
  NA,
  SORT_KEY,
  STATUS_OPTIONS,
  STRINGS,
  USERS_CONSTS,
} from "shared/constants";
import { Images } from "shared/images";
import { BRANCH_SERVICES } from "shared/tableData";

function BranchesTable({
  setmultiplePayload = () => {},
  multiplepayload,
  navigateBranches = () => {},
  data,
  deleteBranch = () => {},
  itemsPerPage,
  setItemsPerPage = () => {},
  setPageCount = () => {},
  setSortKey = () => {},
  sortKey,
  setSortType = () => {},
  multiSelection,
  setMultipSelection = () => {},
  pageCount,
  handleSort = () => {},
  toggle = {
    isActive: false,
    name: "",
  },
  setToggle = () => {},
}) {
  // function to switch sort key based on the name of the column
  const sortSwitchKey = (name) => {
    switch (name) {
      case BRANCH_SERVICES.BRANCH_NAME:
        return setSortKey(SORT_KEY.branch_name);
      case BRANCH_SERVICES.NO_OF_STAFF:
        return setSortKey(SORT_KEY.noOfstaff);
      default:
        return setSortKey(SORT_KEY.default);
    }
  };

  // function to return a boolean indicating whether an image should be shown for a given column
  const sortImage = (name) => {
    switch (name) {
      case BRANCH_SERVICES.BRANCH_NAME:
        return true;
      case BRANCH_SERVICES.NO_OF_STAFF:
        return true;
      default:
        return false;
    }
  };

  // useEffect hook to set the multiple selection flag to false if there are no payloads
  useEffect(() => {
    if (multiplepayload?.length === 0) setMultipSelection(false);
  }, [multiplepayload]);

  return (
    <div className="tab_content">
      <CustomTable>
        <thead>
          <tr key={1000 + 1}>
            {/* column for checkboxes */}
            <th width={70}>
              <CheckboxHeader
                multipleSelect={multiSelection}
                setmultiplePayload={setmultiplePayload}
                setMultipleSelect={setMultipSelection}
                data={data}
                multiplepayload={multiplepayload}
              />
            </th>

            {/* columns for branch data */}
            {Object.values(BRANCH_SERVICES).map((i, idx) => (
              <th scope="col" id={idx}>
                {i}

                {/* if the column is branch name, show a sort icon */}
                {sortImage(i) ? (
                  <SortIcon
                    toggleIcon={
                      toggle.name === i && toggle.isActive ? true : false
                    }
                    onClick={() => {
                      if (toggle.name !== i) {
                        setSortType(0);
                      }
                      setToggle({
                        name: i,
                        isActive: !toggle.isActive,
                      });
                      sortSwitchKey(i);
                      setPageCount(1);
                      handleSort();
                    }}
                  />
                ) : null}
              </th>
            ))}
          </tr>
        </thead>

        {/* table body */}
        {data?.length ? (
          data.map((i, idx) => {
            return (
              <>
                <tbody>
                  <tr>
                    {/* checkbox for each row */}
                    <td>
                      <CheckboxMultiBody
                        setMultipleSelect={setMultipSelection}
                        setmultiplePayload={setmultiplePayload}
                        value={i}
                        multiplepayload={multiplepayload}
                        data={data}
                      />
                    </td>
                    <td
                      onClick={() => navigateBranches(i?.id)}
                      style={{ cursor: "pointer" }}
                    >
                      {i?.branch_name ? i?.branch_name : NA}{" "}
                    </td>
                    <td>{i?.phone_number ? "+" + i?.phone_number : NA} </td>
                    <td>
                      {JSON.stringify(i?.number_of_staff)?.length
                        ? i?.number_of_staff
                        : NA}{" "}
                    </td>
                    <td>
                      <div className="d-flex align-items-center">
                        <span
                          className={`m-1 badge-md badge-${
                            i?.status ? "success" : "danger"
                          }`}
                        >
                          {i?.status
                            ? STATUS_OPTIONS.ACTIVATE.title
                            : STATUS_OPTIONS.DEACTIVATE.title}
                        </span>
                      </div>
                      {/* <ActivateDeactivate
                       // statusChange={userStatusChange}
                        value={i}
                      /> */}
                    </td>
                    <td width={200}>
                      <div className="d-flex">
                        <ButtonStatus
                          image={Images.viewIcon}
                          status={"default"}
                          title={"View Details"}
                          onClick={() => navigateBranches(i?.id)}
                        />
                        <ButtonStatus
                          image={Images.deleteIcon}
                          status={"Unverified"}
                          title={"View Details"}
                          onClick={() => deleteBranch(i?.id)}
                        />
                      </div>
                    </td>
                  </tr>
                </tbody>
              </>
            );
          })
        ) : (
          <tbody>
            <tr>
              <td
                colSpan={Object.values(BRANCH_SERVICES).length + 1}
                className="text-center py-5"
              >
                {STRINGS.NO_DATA_AVAILIABLE}
              </td>
            </tr>
          </tbody>
        )}
      </CustomTable>

      {data?.length ? (
        <Pagination
          items={data?.length ? data?.length : []}
          setPageCount={setPageCount}
          itemsPerPage={itemsPerPage}
          setItemsPerPage={setItemsPerPage}
          pageCount={pageCount}
        />
      ) : null}
    </div>
  );
}

export default BranchesTable;
