import React from "react";

function SideItem({ data, handleClick, active }) {
  return (
    <li
      className={`${active === data.id ? "active" : ""}`}
      onClick={() => handleClick(data)}
    >
      <em className="me-3">
        <img src={data.icon} alt="" />
      </em>
      <span>{data.title}</span>
    </li>
  );
}

export default SideItem;
