import useNotification from "components/atom/snackbar";
import ActivateDeactivate from "components/molecules/activateDeactivate";
import React, { useCallback, useEffect, useRef, useState } from "react";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import "./styles.scss";

import {
  InvoiceListingApi,
  branchListingApi,
  changeDocumentStatus,
  deleteBranchsApi,
  deleteInvoiceApi,
  deleteUsersApi,
  deleteVacancy,
  pharmacyVacancy,
  viewUserDetailsApi,
} from "redux/actions/auth";
import { userStatusUpdate } from "redux/actions/common";
import {
  PHARMACY_DETAILS,
  PHARMACY_USER_VIEW,
  SNACK_MESSAGES,
  SNACK_VARIANT,
  SORT_KEY,
  STATUS_OPTIONS,
  TAB_PHARMACY_USER_DATA,
  USERS_CONSTS,
  VACANCY_TYPE,
} from "shared/constants";
import { ROUTE_CONSTANTS } from "shared/routeConstants";
import BasicPharmacyInfo from "./BasicPharmacyInfo";
import { useLocation } from "react-router-dom/cjs/react-router-dom.min";
import NavTab from "components/atom/tabNav";
import BranchesTable from "../branches/BranchesTable";
import SearchBar from "components/atom/searchBar";
import InvoiceTable from "../invoice/InvoiceTable";
import BreadCrumb from "components/atom/breadcrum";
import DocUnverifyModal from "components/molecules/docUnverifyModal";
import { startLoader, stopLoader } from "redux/actions/commonCRUD";
import {
  disableDocViewer,
  filterByDocumentData,
  filterByDocumentType,
} from "components/utility";
import DocumentViewerModal from "components/molecules/documentViewer";
import VacanciesTable from "components/molecules/vacancyTable";
import FiltersCustom from "components/atom/chatComponents/Filter";
import moment from "moment";
import DeleteModal from "components/molecules/deleteModal";

function PharmacyView({ reload = () => {} }) {
  const history = useHistory();
  const dispatch = useDispatch();
  const idParam = useLocation();
  const id = idParam?.state?.id;
  const [msg, sendNotification] = useNotification();
  const [multiplePayload, setMultiplePayload] = useState([]);
  const [userData, setUserData] = useState();
  const [activeItem, setActiveItem] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(15);
  const [pageCount, setPageCount] = useState(1);
  const [pageItems, setPageItems] = useState(1);
  const [keyword, setKeyword] = useState("");
  const [fetchedBranchData, setFetchedBranchData] = useState({});
  const [fetchedInvoiceData, setFetchedInvoiceData] = useState({});
  const [fetchedVacancyData, setFetchedVacancyData] = useState({});
  const [sortKey, setSortKey] = useState(0);
  const [sortType, setSortType] = useState(0);
  const [multiSelection, setMultipSelection] = useState(false);
  const [toggle, setToggle] = useState({
    isActive: false,
    name: "",
  });

  const [filterData, setFilterData] = useState({
    country: "",
    state: "",
    selectedDate: {
      from: null,
      to: new Date(""),
    },
    rateRange: [0, 0],
  });

  const [viewDoc, setViewDoc] = useState(false);
  const [showReasonModal, setShowReasonModal] = useState(false);
  const [docViewerParams, setDocViewerParams] = useState([]);
  const [modalOpacity, setmodalOpacity] = useState(false);
  const [reasonUnverify, setReasonUnverify] = useState("");
  const [deleteModal, setDeleteModal] = useState(false);
  const [jobID, setJobID] = useState();
  const [toggleFilter, setToggleFilter] = useState(false);
  const firstRenderDone = useRef(false);

  // This useEffect is triggered when the component is mounted.
  // If an id is available, it fetches the user details, otherwise it redirects back to the previous page
  useEffect(() => {
    if (id) {
      viewUser();
    } else {
      history.goBack();
    }
  }, []);

  // This useCallback function is used to fetch the branch list.
  // It uses the payload object to dispatch an action to get branch data from the server.
  // It updates the fetched branch data and page items state variables based on the server response.
  // It shows an error notification if an error occurs while fetching the data.
  const fetchBranchList = () => {
    let payload = {
      page: pageCount,
      search: keyword,
      limit: itemsPerPage,
      sorting: sortKey,
      sort_type: sortType,
      id: id,
    };
    dispatch(
      branchListingApi(
        payload,
        (res) => {
          setFetchedBranchData(res?.data);
          setPageItems(res.count);
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  // This useCallback function is used to fetch the invoice list.
  // It uses the payload object to dispatch an action to get invoice data from the server.
  // It updates the fetched invoice data and page items state variables based on the server response.
  // It shows an error notification if an error occurs while fetching the data.
  const fetchInvoiceList = (isResetting = false) => {
    let payload = {
      page: pageCount,
      search: keyword,
      limit: itemsPerPage,
      sorting: sortKey,
      sort_type: sortType,
      id: id,
      ...(filterData.selectedDate.from && {
        start_date:
          isResetting == true
            ? ""
            : filterData.selectedDate.from
            ? moment(filterData.selectedDate.from).format("YYYY-MM-DD")
            : "",
      }),
      ...(filterData.selectedDate.from && {
        end_date:
          isResetting == true
            ? ""
            : filterData.selectedDate.to
            ? moment(filterData.selectedDate.to).format("YYYY-MM-DD")
            : "",
      }),
    };
    dispatch(
      InvoiceListingApi(
        payload,
        (res) => {
          setFetchedInvoiceData(res?.data);
          setPageItems(res.count);
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const fetchVacancyList = (isResetting = false) => {
    let payload = {
      page: pageCount,
      search: keyword,
      limit: itemsPerPage,
      sorting: sortKey,
      sort_type: sortType,
      pharmacy_id: id,
      country: isResetting == true ? "" : filterData.country,
      state: isResetting == true ? "" : filterData.state,
      ...(filterData.selectedDate.from && {
        start_date:
          isResetting == true
            ? ""
            : filterData.selectedDate.from
            ? moment(filterData.selectedDate.from).format("YYYY-MM-DD")
            : "",
      }),
      ...(filterData.selectedDate.from && {
        end_date:
          isResetting == true
            ? ""
            : filterData.selectedDate.to
            ? moment(filterData.selectedDate.to).format("YYYY-MM-DD")
            : "",
      }),

      start_rate: isResetting == true ? "" : filterData.rateRange[0] || "",

      ...(filterData.rateRange[1] > 0 && {
        end_rate: isResetting == true ? "" : filterData.rateRange[1] || "",
      }),
    };
    dispatch(
      pharmacyVacancy(
        payload,
        (res) => {
          setFetchedVacancyData(res?.data);
          setPageItems(res.count);
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  useEffect(() => {
    if (!firstRenderDone.current) {
      firstRenderDone.current = true;
      return;
    }

    switch (activeItem) {
      case PHARMACY_DETAILS.BRANCHES.ID:
        return fetchBranchList();
      case PHARMACY_DETAILS.INVOICES.ID:
        return fetchInvoiceList();
      case PHARMACY_DETAILS.VACANCIES.ID:
        return fetchVacancyList();
      default:
        break;
    }
  }, [sortKey, sortType, activeItem, itemsPerPage, pageCount, keyword]);

  // This function is used to update the user status by calling the userStatusUpdate function.
  // It creates an array of objects that contains the user id and the status.
  // It shows a success notification if the server response is successful.
  // It shows an error notification if an error occurs while updating the user status.
  const userStatusChange = (id_, is_active) => {
    let arr = [
      {
        user_id: id,
        is_active: is_active,
      },
    ];

    let data = {
      user_status_update: arr,
    };

    dispatch(
      userStatusUpdate(
        data,
        (res) => {
          viewUser();
          sendNotification({
            msg: res?.data?.message
              ? res?.data.message
              : SNACK_MESSAGES.SAVED_SUCCESSFULLY,
            variant: SNACK_VARIANT.SUCCESS,
          });
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  // handleSortIcon toggles the sort order between ascending and descending
  const handleSortIcon = () => {
    if (sortType === SORT_KEY.ascending) {
      setSortType(SORT_KEY.descending);
    } else {
      setSortType(SORT_KEY.ascending);
    }
  };

  // deleteUsers deletes a user with the given id and updates the view and notifications
  const deleteUsers = (id) => {
    let data = {
      users: [
        {
          id: id,
        },
      ],
    };
    dispatch(
      deleteUsersApi(data, (res) => {
        viewUser();
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  const onDelete = () => {
    let arr = [];
    if (multiplePayload.length && !jobID) {
      arr = multiplePayload.map((i, idx) => {
        return i;
      });
    } else {
      arr = [jobID];
    }

    let data = {
      vacancy_list: arr,
    };
    dispatch(
      deleteVacancy(data, (res) => {
        fetchVacancyList();
        setDeleteModal(false);
        setMultiplePayload([]);
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        setDeleteModal(false);
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  // deleteBranch deletes a branch with the given id and updates the view and notifications
  const deleteBranch = (id) => {
    let arr = [];
    if (multiplePayload.length && !id) {
      arr = multiplePayload.map((i, idx) => {
        return { id: i };
      });
    } else {
      arr = [{ id: id }];
    }

    let data = {
      branches: arr,
    };
    dispatch(
      deleteBranchsApi(data, (res) => {
        fetchBranchList();
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  // deleteinvoice deletes a invoice with the given id and updates the view and notifications
  const deleteInvoice = (id) => {
    let arr = [];
    if (multiplePayload.length && !id) {
      arr = multiplePayload.map((i, idx) => {
        return { id: i };
      });
    } else {
      arr = [{ id: id }];
    }

    let data = {
      invoices: arr,
    };

    dispatch(
      deleteInvoiceApi(data, (res) => {
        fetchInvoiceList();
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  // viewUser fetches the details for the user with the given id and updates the state
  const viewUser = useCallback(() => {
    let data = {
      user_id: id,
    };
    dispatch(
      viewUserDetailsApi(data, (res) => {
        setUserData(res?.data);
      }),
      (err) => {
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
        history.goBack();
      }
    );
  }, [id, dispatch, sendNotification, history]);

  //document viewer start loader and background opacity manage for doc viewer
  useEffect(() => {
    if (viewDoc) {
      dispatch(startLoader());
      setmodalOpacity(true);
      setTimeout(() => {
        dispatch(stopLoader());
      }, 3000);
    }
  }, [viewDoc]);

  //on unverify button click function
  const onUnverifyClick = (id, type, docs) => {
    setShowReasonModal(true);
    setDocViewerParams([id, type, filterByDocumentType(docs, type)]);
  };

  //on verify button click function
  const onVerifyClick = (userId, doc_id) => {
    documentButtonClick(STATUS_OPTIONS.VERIFIED.key, userId, doc_id);
  };

  // DataSwitch displays the appropriate component based on the current activeItem
  const dataSwitch = () => {
    switch (activeItem) {
      case PHARMACY_DETAILS.BASIC_PHARMACY_INFO.ID:
        return (
          <BasicPharmacyInfo
            data={userData}
            reload={viewUser}
            onDocumentView={onDocumentView}
            onUnverifyClick={onUnverifyClick}
            onVerifyClick={onVerifyClick}
          />
        );
      case PHARMACY_DETAILS.BRANCHES.ID:
        return (
          <BranchesTable
            multiplepayload={multiplePayload}
            setmultiplePayload={setMultiplePayload}
            navigateBranches={(id) =>
              history.push(ROUTE_CONSTANTS.BRANCHES_VIEW, { id: id })
            }
            data={fetchedBranchData}
            deleteBranch={deleteBranch}
            itemsPerPage={itemsPerPage}
            setItemsPerPage={setItemsPerPage}
            pageCount={pageCount}
            setPageCount={setPageCount}
            sortKey={sortKey}
            setSortKey={setSortKey}
            setSortType={setSortType}
            handleSort={handleSortIcon}
            sortType={sortType}
            pageItems={pageItems}
            setPageItems={setPageItems}
            multiSelection={multiSelection}
            setMultipSelection={setMultipSelection}
            setToggle={setToggle}
            toggle={toggle}
          />
        );
      case PHARMACY_DETAILS.VACANCIES.ID:
        return (
          <VacanciesTable
            multiplepayload={multiplePayload}
            setmultiplePayload={setMultiplePayload}
            navigate={(id) =>
              history.push(ROUTE_CONSTANTS.VACANCIES_VIEW, {
                id: id,
                type: VACANCY_TYPE.PHARMACY,
              })
            }
            data={fetchedVacancyData}
            onDelete={onDelete}
            itemsPerPage={itemsPerPage}
            setItemsPerPage={setItemsPerPage}
            setSortType={setSortType}
            pageCount={pageCount}
            setPageCount={setPageCount}
            sortKey={sortKey}
            setSortKey={setSortKey}
            handleSort={handleSortIcon}
            sortType={sortType}
            pageItems={pageItems}
            setPageItems={setPageItems}
            multiSelection={multiSelection}
            setMultipSelection={setMultipSelection}
            setToggle={setToggle}
            toggle={toggle}
            type={VACANCY_TYPE.PHARMACY}
            setId={setJobID}
          />
        );
      case PHARMACY_DETAILS.TEMPLATE.ID:
        return <>No Data</>;
      case PHARMACY_DETAILS.INVOICES.ID:
        return (
          <InvoiceTable
            navigateBranches={(id) =>
              history.push(ROUTE_CONSTANTS.INVOICE_VIEW, { id: id })
            }
            data={fetchedInvoiceData}
            deleteBranch={deleteInvoice}
            itemsPerPage={itemsPerPage}
            setItemsPerPage={setItemsPerPage}
            pageCount={pageCount}
            setPageCount={setPageCount}
            handleSort={handleSortIcon}
            sortKey={sortKey}
            setSortKey={setSortKey}
            setSortType={setSortType}
            sortType={sortType}
            pageItems={pageItems}
            setPageItems={setPageItems}
            setToggle={setToggle}
            toggle={toggle}
          />
        );
      default:
    }
  };

  const onTabChange = useCallback(() => {
    setMultiplePayload([]);
    setPageCount(1);
  }, [activeItem]);

  useEffect(() => {
    onTabChange();
  }, [activeItem, onTabChange]);

  // searchAPI_Switch returns the appropriate API function for searching data based on the current activeItem
  const searchAPI_Switch = () => {
    switch (activeItem) {
      case PHARMACY_DETAILS.BASIC_PHARMACY_INFO.ID:
        return viewUser;
      case PHARMACY_DETAILS.BRANCHES.ID:
        return fetchBranchList;
      case PHARMACY_DETAILS.INVOICES.ID:
        return fetchInvoiceList;
      case PHARMACY_DETAILS.VACANCIES.ID:
        return fetchVacancyList;
      default:
    }
  };

  // Delete_Switch returns the appropriate API function for deleting data based on the current activeItem
  const delete_Switch = () => {
    switch (activeItem) {
      case PHARMACY_DETAILS.BASIC_PHARMACY_INFO.ID:
        return deleteUsers();
      case PHARMACY_DETAILS.BRANCHES.ID:
        return deleteBranch();
      case PHARMACY_DETAILS.VACANCIES.ID:
        return onDelete();
      default:
    }
  };

  const documentButtonClick = (isVerified, type, document_id) => {
    let arr = [
      {
        user_id: id,
        document_type: type ? type : docViewerParams[1],
        ...(isVerified === STATUS_OPTIONS.UNVERIFIED.key && {
          comment: reasonUnverify,
        }),
        verified: isVerified,
        document_id: document_id
          ? document_id
          : docViewerParams[0]
          ? docViewerParams[0]
          : "",
      },
    ];

    const payload = {
      doc_status_update: arr,
    };
    documentStatusChange(payload);
  };

  //Api function for document status change
  const documentStatusChange = (payload) => {
    dispatch(
      changeDocumentStatus(payload, (res) => {
        setViewDoc(false);
        setShowReasonModal(false);
        reload();
        viewUser();
        sendNotification({
          msg: res?.data?.message
            ? res?.data.message
            : SNACK_MESSAGES.SAVED_SUCCESSFULLY,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        setShowReasonModal(false);
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  const onDocumentView = (id, type, docs) => {
    if (disableDocViewer(docs, type)) {
      setViewDoc(true);
      setDocViewerParams([
        id,
        type,
        filterByDocumentType(docs, type),
        filterByDocumentData(docs, type),
      ]);
    } else {
      sendNotification({
        msg: SNACK_MESSAGES.DOCUMENT_NOT_UPLOADED,
        variant: SNACK_VARIANT.ERROR,
      });
    }
  };

  const handleFetchDataForFilter = () => {
    setPageCount(1);
    switch (activeItem) {
      case PHARMACY_DETAILS.VACANCIES.ID:
        return fetchVacancyList();
      case PHARMACY_DETAILS.INVOICES.ID:
        return fetchInvoiceList();
      default:
        break;
    }
  };

  const resetFilter = () => {
    setPageCount(1);
    setFilterData({
      country: "",
      state: "",
      selectedDate: {
        from: null,
        to: new Date(""),
      },
      rateRange: [0, 0],
    });
    switch (activeItem) {
      case PHARMACY_DETAILS.VACANCIES.ID:
        return fetchVacancyList(true);
      case PHARMACY_DETAILS.INVOICES.ID:
        return fetchInvoiceList(true);
      default:
        break;
    }
  };

  const handleChangeFilterData = (key, value) => {
    setFilterData((prevFilterData) => ({ ...prevFilterData, [key]: value }));
  };
  useEffect(() => {
    if (!toggleFilter) resetFilter();
  }, [toggleFilter]);

  const filterSwitch = () => {
    switch (activeItem) {
      case PHARMACY_DETAILS.VACANCIES.ID:
        return (
          <FiltersCustom
            toggle={toggleFilter}
            filterData={filterData}
            reset={resetFilter}
            handleChangeFilterData={handleChangeFilterData}
            handleFetchDataForFilter={handleFetchDataForFilter}
            type={VACANCY_TYPE.PHARMACY}
          />
        );
      case PHARMACY_DETAILS.INVOICES.ID:
        return (
          <FiltersCustom
            toggle={toggleFilter}
            filterData={filterData}
            reset={resetFilter}
            handleChangeFilterData={handleChangeFilterData}
            handleFetchDataForFilter={handleFetchDataForFilter}
            type={VACANCY_TYPE.INVOICES}
          />
        );
      default:
        return null;
    }
  };

  return (
    <>
      <div className="d-flex flex-wrap align-items-center justify-content-between">
        <BreadCrumb
          onClick={() =>
            history.push(ROUTE_CONSTANTS.USERS, {
              id: USERS_CONSTS.PHARMACY_USERS.ID,
            })
          }
          previousLink={PHARMACY_USER_VIEW.PHARMACY_USERS}
          currentLink={PHARMACY_USER_VIEW.DETAILS}
        />
        <div className="d-flex flex-wrap">
          {PHARMACY_DETAILS.BASIC_PHARMACY_INFO.ID === activeItem ? (
            <ActivateDeactivate
              title={"Select Action"}
              statusChange={userStatusChange}
              value={userData}
            />
          ) : null}
          {multiplePayload.length &&
          PHARMACY_DETAILS.BASIC_PHARMACY_INFO.ID !== activeItem ? (
            <button
              className={`btn btn-sm btn-danger ms-2`}
              onClick={() => delete_Switch()}
            >
              Delete
            </button>
          ) : null}
          {PHARMACY_DETAILS.BASIC_PHARMACY_INFO.ID === activeItem ? null : (
            <SearchBar
              searchAPI={() => searchAPI_Switch()}
              keyword={keyword}
              setKeyword={setKeyword}
              setPageCount={setPageCount}
              pageCount={pageCount}
              filter={
                PHARMACY_DETAILS.VACANCIES.ID === activeItem ||
                PHARMACY_DETAILS.INVOICES.ID === activeItem
              }
              toggle={toggleFilter}
              setToggle={setToggleFilter}
              reset={resetFilter}
            />
          )}
        </div>
      </div>
      <hr className="divider" />
      {filterSwitch()}

      <div className="d-flex flex-wrap align-items-center justify-content-between tab_full">
        <NavTab
          onClick={(id) => {
            setActiveItem(id);
            resetFilter();
            setToggleFilter(false);
            setKeyword("");
          }}
          tabsData={TAB_PHARMACY_USER_DATA}
          activeTab={activeItem}
        />
      </div>

      <DocumentViewerModal
        viewDoc={viewDoc}
        setViewDoc={setViewDoc}
        documentButtonClick={documentButtonClick}
        disableKey={docViewerParams[2]}
        setShowReasonModal={setShowReasonModal}
        docURL={docViewerParams[3]}
        doc_type={docViewerParams[1]}
        onLoadError={() => {
          setViewDoc(false);
          dispatch(stopLoader());
          sendNotification({
            msg: SNACK_MESSAGES.DOCUMENT_LOAD_FAILED,
            variant: SNACK_VARIANT.ERROR,
          });
        }}
        onLoad={() => {
          setmodalOpacity(false);
          dispatch(stopLoader());
        }}
        setmodalOpacity={setmodalOpacity}
        modalOpacity={modalOpacity}
      />

      <DocUnverifyModal
        show={showReasonModal}
        setShow={setShowReasonModal}
        onClick={() => documentButtonClick(STATUS_OPTIONS.UNVERIFIED.key)}
        reason={reasonUnverify}
        setReason={setReasonUnverify}
      />

      {dataSwitch()}
      <DeleteModal
        show={deleteModal}
        toggleModal={()=> setDeleteModal(prev => !prev)}
        onClick={onDelete}
      />
    </>
  );
}

export default PharmacyView;
