import React from "react";
import Select from "react-select";

function ReactSelect({
  selectedOption,
  handleChange,
  options,
  title,
  isDisabled = false,
}) {
  return (
    <>
      <Select
        key={selectedOption}
        value={selectedOption}
        onChange={handleChange}
        options={options}
        isClearable={false}
        placeholder={title}
        classNamePrefix="react-prefix"
        className={`react-select ${
          selectedOption?.color ? selectedOption?.color : ""
        } ${isDisabled ? "disabled" : ""}`}
        isSearchable={false}
        // menuIsOpen={true}
      />
    </>
  );
}

export default ReactSelect;
