import { splice } from "components/utility";
import React from "react";

function CheckboxMultiBody({
  multiplepayload,
  value,
  setmultiplePayload = () => {},
  setMultipleSelect = () => {},
  setId = () => {},
  data,
}) {
  return (
    <div className={`checkbox`}>
      <input
        type="checkbox"
        checked={multiplepayload.includes(value?.id)}
        //disabled={value?.is_default}
        onChange={(e) => {
          setId(null);
          if (e.target.checked) {
            setmultiplePayload((prev) => [...prev, value?.id]);
            if (
              data?.length ===
              multiplepayload?.length + 1
            ) {
              setMultipleSelect(true);
            }
          } else {
            setmultiplePayload(splice(multiplepayload, value?.id));
            setMultipleSelect(false);
          }
        }}
      />
      <label></label>
    </div>
  );
}

export default CheckboxMultiBody;
