// utils
import { downloadFile, getDocsUrl } from "../../shared/utils";

const DEFAULT_MESSAGE_LENGTH = 500;

const HighlightedText = ({ text = "", highlight = "", isActiveIndex = false }) => {
    if (!highlight.trim()) {
        return <span>{text}</span>;
    }
    const regex = new RegExp(`(${escapeRegExp(highlight)})`, "gi");
    const parts = text.split(regex);
    return (
        <span>
            {parts
                .filter((part) => part)
                .map((part, i) =>
                    regex.test(part) ? (
                        <mark className={isActiveIndex ? "active" : ""} key={i}>
                            {part}
                        </mark>
                    ) : (
                        <span key={i}>{part}</span>
                    )
                )}
        </span>
    );
};

const ShowMoreLessButton = ({message = {} , visibleContent = DEFAULT_MESSAGE_LENGTH, setVisibleContent = () => {}}) => {
    return (
        <>
            {message?.content?.length > visibleContent && (
                <span className="show_more_btn" onClick={() => setVisibleContent(message?.content?.length || visibleContent)}>
                    Show more
                </span>
            )}
            {visibleContent === message?.content?.length && (
                <span className="show_less_btn" onClick={() => setVisibleContent(DEFAULT_MESSAGE_LENGTH)}>Show less</span>
            )}
        </>
    );
};

