import React, { useEffect, useState } from "react";
import { Images } from "shared/images";
import { BUTTONS, STRINGS } from "shared/constants";
import ButtonStatus from "components/atom/button/ButtonStatus";
import { PAGES_MANAGEMENT_TABLE } from "shared/tableData";
import ActivateDeactivate from "components/molecules/activateDeactivate";
import CheckboxMultiBody from "components/molecules/checkboxMulti/body";
import CheckboxHeader from "components/molecules/checkboxMulti/header";
import { TruncateString, htmlToText } from "components/utility";

function Table({
  data,
  statusChange,
  setmultiplePayload = () => {},
  multiplepayload,
  setModalText = () => {},
  setShowModal = () => {},
  activeTab,
  setId,
  setPagesData = () => {},
}) {
  const [multipleSelect, setMultipleSelect] = useState(false);

  useEffect(() => {
    if (multiplepayload?.length === 0) setMultipleSelect(false);
  }, [multiplepayload]);

  return (
    <>
      <thead>
        <tr key={1000 + 1}>
          <th>
            <CheckboxHeader
              multipleSelect={multipleSelect}
              setmultiplePayload={setmultiplePayload}
              setMultipleSelect={setMultipleSelect}
              data={data[data?.findIndex((item) => item?.id === activeTab)]?.sections}
              multiplepayload={multiplepayload}
            />
          </th>
          {Object.values(PAGES_MANAGEMENT_TABLE).map((i, idx) => (
            <th scope="col" id={idx}>
              <span> {i} </span>
            </th>
          ))}
        </tr>
      </thead>

      {data?.length ? (
        data[data?.findIndex((item) => item?.id === activeTab)]?.sections?.map(
          (value, index) => (
            <tbody key={index}>
              <tr>
                <td width={80}>
                  <CheckboxMultiBody
                    setMultipleSelect={setMultipleSelect}
                    setmultiplePayload={setmultiplePayload}
                    value={value}
                    multiplepayload={multiplepayload}
                    data={data[data?.findIndex((item) => item?.id === activeTab)]?.sections}
                  />
                </td>
                <td>
                  {htmlToText(TruncateString(value.section_name, 20, 20))}
                </td>
                <td>
                  {htmlToText(TruncateString(value.section_content, 70, 70))}
                </td>
                <td>
                  <ActivateDeactivate
                    statusChange={statusChange}
                    value={value}
                  />
                </td>

                <td width={200}>
                  <div className="d-flex btn_group">
                    <ButtonStatus
                      image={Images.edit}
                      status={"default"}
                      title={"Edit Service"}
                      onClick={() => {
                        setPagesData(value);
                        setModalText(BUTTONS.EDIT);
                        setShowModal(true);
                        setId(value?.id);
                      }}
                    />
                  </div>
                </td>
              </tr>
            </tbody>
          )
        )
      ) : (
        <tbody>
          <tr>
            <td colSpan={Object.values(PAGES_MANAGEMENT_TABLE).length + 1} className="text-center py-5">
            {STRINGS.NO_DATA_AVAILIABLE}
            </td>
          </tr>
        </tbody>
      )}
    </>
  );
}

export default Table;
